import myUpload from "@/components/myUpload/scheduleUpload"; // secondary package based on el-pagination

let myLoading = undefined; // 弹窗服务调用
const modifyTypesVerify = (rule, value, callback) => {
  if (!value) {
    callback(new Error("请选择编辑的排班类型"));
  } else {
    if (!value.length) {
      callback(new Error("请选择编辑的排班类型期"));
      return;
    }
    callback();
  }
};
export default {
  name: "schedual",
  components: { myUpload },

