    groupFilter() {
      let filterPersonIds = [this.addTemp.leaderId, this.addTemp.driverId, this.addTemp.safetyId, this.addTemp.businessId, this.addTemp.addBusinessId, this.addTemp.additionSafetyId], flag = false, flagCar = false;
      this.addTempRepeatPersons = [];
      this.addTempRepeatCar = null;
      for (let i = 0; i < filterPersonIds.length; i++) {
        if (this.autoPersonFilter(filterPersonIds[i])) {
          flag = true;
          this.addTempRepeatPersons.push(filterPersonIds[i]);
          // break;
        }
      }
      if (flag == true) {
        this.$message.warning('与其他排班存在重复的排班人员')

      }
      if (this.autoCarFilter(this.addTemp.carId)) {
        flagCar = true;
        this.addTempRepeatCar = this.addTemp.carId;
        // break;
      }
      if (flagCar == true) {
        this.$message.warning('与其他排班存在重复的排班车辆')

      }
    },
    addCarFilter() {
      if (this.autoCarFilter(this.addTemp.carId)) {
        this.addTempRepeatCar = this.addTemp.carId;
        this.$message.warning('与其他排班存在重复的排班车辆')
      }
    },
    addPersonFilter(personId) {
      if (this.autoPersonFilter(personId)) {
        if (this.addTempRepeatPersons.indexOf(personId) == -1) {
          this.addTempRepeatPersons.push(personId);
        }
        this.$message.warning('与其他排班存在重复的排班人员')
      }
