    checkDate() {
      let d, e, today, foday;
      d = new Date();
      today = new Date(d.getFullYear(), d.getMonth(), d.getDate());

      e = new Date(this.listQuery.selectDate);


      foday = new Date(e.getFullYear(), e.getMonth(), e.getDate());



      if (foday < today) {
        // alert('在今天之前');
        this.dateDisabled = true;
      } else {
        // alert('在今天或今天之后 ');
        this.dateDisabled = false;
      }

    },
    formatTime(time) {
      return renderTime(time);
    },
    //点击增加
    handleCreate() {
      this.addTemp = { type: 2, carId: null, leaderId: null, driverId: null, safetyId: null, additionSafetyId: null, businessId: null, addBusinessId: null, };
      this.addVisible = true;
      this.$nextTick(() => {
        this.$refs["addForm"].clearValidate();
      });

    },
    //添加功能
    createData() {
      this.$refs["addForm"].validate(valid => {
        if (valid) {
          this.addOrEdit(this.addTemp, 1);
        } else {
          return false;
        }
      });
    },
    handleEdit(row) {
      this.editVisible = true;
      this.editTemp = { ...row, types: [row.type] };
      //编辑窗口重复人员重置
      this.editTempRepeatPersons = [];
      let arr = [row.leaderId, row.driverId, row.safetyId, row.businessId, row.addBusinessId, row.additionSafetyId];
      arr.forEach((item, index) => {
        if (item && item != -1 && row[row.id + '-' + item]) {
          this.editTempRepeatPersons.push(item);
        }
      })
      if (row.carId && row[row.id + 'car' + row.carId]) {
        this.editTempRepeatCar = row.carId;
      }

    },
    //编辑功能
    editData() {
      // console.warn('编辑');
      // this.editVisible=false;
      // return;
      this.$refs["editForm"].validate(valid => {
        if (valid) {
          this.addOrEdit(this.editTemp, 2);
        } else {
          return false;
        }
      });
    },
    //保存修改
    saveSubmit(row, handletype) {
      if (handletype == 'delete') {
        if (row.status == 1) {
          row.status = 2;
        } else if (row.status == 3) {
          row.status = 4;
        }
      }
      this.addOrEdit(row, 2)
    },
    findItem(locations, key, val) {
      let obj = {};
      obj = locations.find((item) => {
        return item[key] === val;
      });
      if (obj == undefined) {
        obj = {};
      }

      return obj;
    },
    //添加或者是保存数据处理和提交
    addOrEdit(data, type) {

      let subData = { ...data }, that = this;
      //获取所有接口需要的数据



      //年月日
      if (type == 1) {
        // let dates = subData.addDate.split('_');
        // subData.planYear = dates[0];
        // subData.planMonth = dates[1];
        // subData.planDay = dates[2];
        let date = new Date(this.listQuery.selectDate);
        subData.planYear = date.getFullYear();
        subData.planMonth = date.getMonth() + 1;
        subData.planDay = date.getDate();


        subData.status = 3;//添加的状态都是3
        //道路
        subData.routeName = this.findItem(this.geometryOptions, 'id', subData.routeId).name;
        //人员信息
        subData.driverName = this.findItem(this.driverOptions, 'id', subData.driverId).name;

        subData.safetyName = this.findItem(this.safetyOptions, 'id', subData.safetyId).name;
        if (subData.additionSafetyId == null) {
          subData.additionSafetyName = null;
        } else {
          subData.additionSafetyName = this.findItem(this.safetyOptions, 'id', subData.additionSafetyId).name;
        }
        if (subData.businessId == null) {
          subData.businessName = null
        } else {
          subData.businessName = this.findItem(this.businessOptions, 'id', subData.businessId).name;

        }
        if (subData.addBusinessId == null) {
          subData.addBusinessName = null
        } else {
          subData.addBusinessName = this.findItem(this.businessOptions, 'id', subData.addBusinessId).name;
        }




        //车组各种信息
        // let groupMsg = this.findItem(this.groupAlllist, 'leaderId', subData.leaderId);
        // if (!groupMsg) {
        //   this.$message.warning('该车组长未绑定车组');


        //   return;
        // }
        // subData.leaderName = groupMsg.leaderName;
        // subData.safetyId = groupMsg.safetyId;
        // subData.safetyName = groupMsg.safetyName;
        // subData.additionSafetyId = groupMsg.additionSafetyId;
        // subData.additionSafetyName = groupMsg.additionSafetyName;
        // subData.businessId = groupMsg.businessId;
        // subData.businessName = groupMsg.businessName;
        // subData.addBusinessId = groupMsg.addBusinessId;
        // subData.addBusinessName = groupMsg.addBusinessName;
        // subData.groupId = groupMsg.id;
        // subData.groupName = groupMsg.groupName;
        // subData.driverId = groupMsg.driverId;
        // subData.driverName = groupMsg.driverName;
        if (!subData.leaderName) {
          this.$message.warning('该车组长未绑定车组,请绑定后再选择该车组长');
          return;

        }

        //车辆司机信息
        let carMsg = this.findItem(this.carOptions, 'id', subData.carId);
        subData.carLicenseNo = carMsg.licenseNo;
        // subData.driverId = carMsg.driverId;
        // subData.driverName = carMsg.driverName;
      } else if (type == 2) {
        // if(subData.businessId==subData.addBusinessId||subData.safetyId==subData.additionSafetyId){
        // this.$message.warning('押运员或业务员重复,请检查后重新选择')
        // return;
        // }
      }
      // if (subData.safetyId == undefined || subData.businessId == undefined) {
      //   this.$message.warning('该车组长对应车组未绑定押运员1或业务员1');

      //   return;
      // }
      //业务员1可以没有
      if (subData.safetyId == undefined) {
        this.$message.warning('该车组长对应车组未绑定押运员1');

        return;
      }
      if (subData.driverId == undefined) {
        this.$message.warning('该车组未绑定司机');
        return;
      }

      if (type == 1) {
        myLoading = this.$loading({ target: "#mydialog1 .el-dialog" });

      } else {
        myLoading = this.$loading({ target: "#mydialog2 .el-dialog" });

      }
      function loadingClose() {

        myLoading.close()

      }



      //请求
      let addData = { ...subData }, req;
      delete addData.addDate;//不需要传addDate

      if (type == 1) {
        req = addactuallySchedule;
      } else if (type == 2) {
        req = updateActually;
        if (addData.status == 2 || addData.status == 4) {//删除功能
          let addData1 = { ...addData }
          addData = [addData1]
        } else {
          addData = (this.list1.concat(this.list2)).filter((item, index) => {
            let flag = false;
            let commonTrueOrFalse = (subData.routeId == item.routeId) && (this.editTemp.types.indexOf(item.type) >= 0) && (subData.status == item.status && item.status % 2 != 0) && (this.editTemp.planYear == item.planYear) && (this.editTemp.planMonth == item.planMonth) && (this.editTemp.planDay == item.planDay)
            if (item.idIdentify == this.editTemp.idIdentify) {
              flag = true;
            } else if (item.type !== this.editTemp.type && commonTrueOrFalse) {
              flag = true;
            }
            return flag;
          })

          // //根据选择的编辑的日期确定编辑哪几天的相同路线、排班类型和排班状态的数据
          // addData = (this.list1.concat(this.list2)).filter((item, index) => {
          //   return (subData.routeId == item.routeId) && (this.editTemp.types.indexOf(item.type) >= 0) && (subData.status == item.status) && (this.editTemp.planYear == item.planYear) && (this.editTemp.planMonth == item.planMonth) && (this.editTemp.planDay == item.planDay)
          // })
          addData.forEach(element => {
            element.carId = subData.carId;
            element.carLicenseNo = subData.carLicenseNo;
            element.leaderId = subData.leaderId;
            element.leaderName = subData.leaderName;
            element.safetyId = subData.safetyId;
            element.safetyName = subData.safetyName;
            element.additionSafetyId = subData.additionSafetyId;
            element.additionSafetyName = subData.additionSafetyName;
            element.businessId = subData.businessId;
            element.businessName = subData.businessName;
            element.addBusinessId = subData.addBusinessId;
            element.addBusinessName = subData.addBusinessName;
            element.driverId = subData.driverId;
            element.driverName = subData.driverName;
          });

        }
      }

      req(addData)
        .then(res => {
          if (res.status == 0) {
            if (type == 1) {
              this.addVisible = false;
            }
            if (type == 2) {
              this.editVisible = false;
            }
            this.getSelect()
            this.getList();
            this.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          loadingClose();

        });


    },
    handlePublish() {
      this.fullscreenLoading = this.$loading({
        lock: true,
        text: '稍等哦，发布排班中，请勿做任何操作！',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      //生成之前先清空原来的定时器
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.showPublish = false;
      // this.listLoading = true;
      publishActually({ dates: [this.listQuery.selectDate] })
        .then(res => {
          if (res.status == 0) {
            this.queryGenerate(res.data);
          } else {
            this.$message.error(res.msg);
          }
        }).catch(() => {
          this.fullscreenLoading.close()
        })
        .finally(() => {
          // this.listLoading = false;
        });
    },
    queryGenerate(resMsg) {
      let that = this;
      queryExp({
        uuid: resMsg
      })
        .then(res => {
          if (res.status == 0) {
            //生成预排班是异步的，需要轮询查询是否生成成功
            let msg;
            if (res.data.success == 0) {
              if (this.timer) {
                clearInterval(this.timer);
              }
              this.timer = setInterval(function () {
                that.queryGenerate(resMsg);
              }, 10000)
            } else if (res.data.success == 1) {
              msg = res.data.msg;
              this.getSelect()
              this.getList();
              if (this.timer) {
                clearInterval(this.timer);
              }
              this.$message.success('操作成功');
              this.fullscreenLoading.close()

            } else if (res.data.success == 2) {
              msg = res.data.msg;
              if (this.timer) {
                clearInterval(this.timer);
              }
              this.$message.error('操作失败');
              this.fullscreenLoading.close()

            }

          } else {
            this.$message.error(res.msg);
          }
        }).catch(err => {
          if (this.timer) {
            clearInterval(this.timer);
          }
          this.fullscreenLoading.close()
        })

    },

    gotask(row) {
      sessionStorage.schedule = 'schedule';
      sessionStorage.schedulingId = row.idIdentify;
      sessionStorage.schedulingDate = row.planYear + '_' + row.planMonth + '_' + row.planDay
      sessionStorage.time = this.listQuery.selectDate;
      sessionStorage.schedulePublish = row.publish;
      this.$router.push('/schedualManagement/schedual/task');
    },
    //下来框重新选择以后改变对应的表格行信息
    selectChange(row, type) {
      if (type == 'car') {
        this.editCarFilter();
        for (let i = 0; i < this.carOptions.length; i++) {
          if (this.carOptions[i].id == row.carId) {
            row.carLicenseNo = this.carOptions[i].licenseNo
          }
        }
      } else if (type == 'group') {
        this.editFilter(row.leaderId);
        for (let i = 0; i < this.teamLeaderOptions.length; i++) {
          if (this.teamLeaderOptions[i].id == row.leaderId) {
            row.leaderName = this.teamLeaderOptions[i].name
          }
        }
      } else if (type == 'driver') {
        this.editFilter(row.driverId);

        for (let i = 0; i < this.driverOptions.length; i++) {
          if (this.driverOptions[i].id == row.driverId) {
            row.driverName = this.driverOptions[i].name
          }
        }
      } else if (type == 'safety') {
        this.editFilter(row.safetyId);

        for (let i = 0; i < this.safetyOptions.length; i++) {
          if (this.safetyOptions[i].id == row.safetyId) {
            row.safetyName = this.safetyOptions[i].name
          }
        }
      } else if (type == 'business') {
        this.editFilter(row.businessId);

        for (let i = 0; i < this.businessOptions.length; i++) {
          if (this.businessOptions[i].id == row.businessId) {
            if (this.businessOptions[i].id == null) {
              row.businessName = null

            } else {
              row.businessName = this.businessOptions[i].name

            }
            // row.businessName = this.businessOptions[i].name
          }
        }
      } else if (type == 'additionSafety') {
        this.editFilter(row.additionSafetyId);

        for (let i = 0; i < this.safetyOptions.length; i++) {
          if (this.safetyOptions[i].id == row.additionSafetyId) {
            if (this.safetyOptions[i].id == null) {
              row.additionSafetyName = null;
            } else {
              row.additionSafetyName = this.safetyOptions[i].name;

            }
          }
        }
      } else if (type == 'addBusiness') {
        this.editFilter(row.addBusinessId);

        for (let i = 0; i < this.businessOptions.length; i++) {
          if (this.businessOptions[i].id == row.addBusinessId) {
            if (this.businessOptions[i].id == null) {
              row.addBusinessName = null

            } else {
              row.addBusinessName = this.businessOptions[i].name

