          }
        }
      }

    },
    handleSelectOptions(option, id, name, idN, nameN) {
      let hasItem = false;
      for (let i = 0; i < option.length; i++) {
        if (option[i][idN] == id) {
          hasItem = true;
          return;
        }
      }
      if (hasItem === false) {
        if (id !== null) {
          option.unshift({ [idN]: id, [nameN]: name, remove: true });
        }
      }
    },
    //判定select选中的是否在数组里
    judgeRemoveIn(arr, id) {
      let flag = false;
      let res = arr.some(item => {
        if (item.id === id && item.remove === true) {
          flag = true;
        }
      })
      return flag
    },

    dateChange(val) {
      this.checkDate()
      this.handleFilter();
    },
    //格式化时间
    formatDateTime(date) {

      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      // m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      // d = d < 10 ? "0" + d : d;
      var h = date.getHours();
      var minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return y + "_" + m + "_" + d;
    },

    //获取列表
    getList() {

      let that = this;
      this.listLoading = true;
      this.showPublish = false;
      queryActually({
        times: [this.listQuery.selectDate]
      })
        .then(res => {
          if (res.status == 0) {
            that.list = [], that.list1 = [], that.list2 = [], that.list3 = [], that.list4 = [];
            if (res.data) {
              for (let index in res.data) {
                for (let i = 0; i < res.data[index].length; i++) {
                  let imteI = res.data[index][i]
                  res.data[index][i].sortDate = parseInt(index);
                  if (res.data[index][i].status == 2 || res.data[index][i].status == 4) {
                    res.data[index][i].statusDisabled = true;
                  }
                  Promise.all(this.getSelectPromises).then(() => {
                    that.handleSelectOptions(that.carOptions, imteI.carId, imteI.carLicenseNo, 'id', 'licenseNo');
                    that.handleSelectOptions(that.teamLeaderOptions, imteI.leaderId, imteI.leaderName, 'id', 'name');
                    that.handleSelectOptions(that.driverOptions, imteI.driverId, imteI.driverName, 'id', 'name');
                    that.handleSelectOptions(that.safetyOptions, imteI.safetyId, imteI.safetyName, 'id', 'name');
                    that.handleSelectOptions(that.businessOptions, imteI.businessId, imteI.businessName, 'id', 'name');
                    that.handleSelectOptions(that.safetyOptions, imteI.additionSafetyId, imteI.additionSafetyName, 'id', 'name');
                    that.handleSelectOptions(that.businessOptions, imteI.addBusinessId, imteI.addBusinessName, 'id', 'name');

                  })
                }
                that.list = that.list.concat(res.data[index]);
              }
              //按路线名称排序
              that.sortByRouteName(that.list)
              //按是否被取消排序
              that.list.sort((a, b) => {

                if (a.status % 2 == 0 && b.status % 2 != 0) {
                  return 1;
                } else if (a.status % 2 != 0 && b.status % 2 == 0) {
                  return -1;
                } else {
                  return 0;
                }
              })


              //按排班类型排序
              that.list = that.sortType(that.list)
              // //按日期排序
              // that.list.sort((a, b) => {
              //   return a.sortDate - b.sortDate
              // })
              //重复的排班人员文字标红
              that.getRepeatIds();
              that.getCarRepeatIds();

              if (that.list.length) {
                that.showPublish = false;
                for (let j = 0; j < that.list.length; j++) {

                  if (that.list[j].publish == 0 && (that.list[j].status == 1 || that.list[j].status == 3)) {
                    that.showPublish = true;
                    break;
                  }
                }
                // that.list[0].publish == 1 ? (that.showPublish = false) : (that.showPublish = true)
              } else {
                that.showPublish = false
              }
              for (let k = 0; k < that.list.length; k++) {
                //晚接
                if (that.list[k].type == 1) {
                  that.list2.push(that.list[k])

                } else if (that.list[k].type == 2) {//早送
                  that.list1.push(that.list[k])
                } else if (that.list[k].type == 5) {//加钞
                  that.list3.push(that.list[k])
                } else if (that.list[k].type == 7) {//收款
                  that.list4.push(that.list[k])
                }
              }
            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          that.listLoading = false;
        });
    },
    sortType(list) {
      let arr1 = [], arr2 = [], arr3 = []
      for (let i = 0; i < list.length; i++) {
        if (list[i].type == 1) {
          arr1.push(list[i])
        } else if (list[i].type == 2) {
          arr2.push(list[i])
        } else {
          arr3.push(list[i])
        }
      }
      return [...arr2, ...arr1, ...arr3]
    },

