    //点击搜索
    handleFilter() {
      this.currpage = 1;
      this.currpage1 = 1;
      this.getSelect()
      this.getList();
    },

    /**
     * @description: 判断某个排班的人员是否与其他排班的同为早送或晚收的正常排班人员重复
     * @param {*} personId 需要判断是否重复的人员id
     * @param {*} idIdentify 需要判断人员所在的排班idIdentify，编辑功能idIdentify存在，添加功能idIdentify不存在
     * @param {*} filterIdIdentifys 编辑功能时可能同时存在多个排班需要判断，需要排除这多个排班，其他情况该参数默认为空，多个排班的idIdentify
     * @param {*} type 编辑的排班的排班类型
     * @return {*}
     */
    autoPersonFilter(personId, idIdentify, filterIdIdentifys = [], type) {
      if (!personId) {
        return false
      }
      let repeatSchedules = this.list.filter((item, index) => {
        return (item.idIdentify != idIdentify) &&
          filterIdIdentifys.indexOf(item.idIdentify) == -1 &&
          ((item.type == type && idIdentify) || ((this.addTemp.type == 3 || this.addTemp.type == item.type) && !idIdentify)) &&  //(item.type==this.editTemp.type&&idIdentify)代表是编辑功能的判断，((this.addTemp.type==3||this.addTemp.type==item.type)&&!idIdentify)代表是添加功能判断
          (item.status % 2 != 0) &&
          (personId == item.addBusinessId || personId == item.additionSafetyId || personId == item.businessId || personId == item.driverId || personId == item.leaderId || personId == item.safetyId)
      });
      let flag = false;
      if (repeatSchedules.length) {
        flag = true;
      }
      return flag;
    },
    /**
     * @description: 查找出需要编辑的排班，然后一个个排班看是否存在与其他排班人员重复的情况
     * @param {*} personId 如果是编辑人员的情况下，personId要传值，因为是比较具体的这一个员工跟其他排班有人员是否重复，如果是编辑所选排班类型则是要重新比较所有车组人员跟其他排班的重复情况，不需要传personId
     * @param {*} editType 添加这个用于在编辑单个人员时需要传personId 时personId==null（选择请选择）的情况下及时返回
     * @return {*}
     */
    editFilter(personId, editType) {
      if (!personId && !editType) {
        return;
      }
      //如果是选择排班类型的话清空editTempRepeatPersons，以避免后续重复人员判断正确
      if (!personId) {
        this.editTempRepeatPersons = [];
      }
      let filterData = [], filterIdIdentifys = [], hasRepeat = false;
      //查找出要编辑的排班，由于编辑可以同时选择早送和晚收，因此编辑的可能是多个排班
      filterData = (this.list).filter((item, index) => {
        let flag = false;
        let commonTrueOrFalse = (this.editTemp.routeId == item.routeId) &&
          (this.editTemp.types.indexOf(item.type) >= 0) &&
          (this.editTemp.status == item.status && item.status % 2 != 0) &&
          (this.editTemp.planYear == item.planYear) &&
          (this.editTemp.planMonth == item.planMonth) &&
          (this.editTemp.planDay == item.planDay)
        if (item.idIdentify == this.editTemp.idIdentify) {
          flag = true;
        } else if (item.type != this.editTemp.type && commonTrueOrFalse) {
          flag = true;
        }
        if (flag == true) {
          filterIdIdentifys.push(item.idIdentify)
        }
        return flag;
      })

      for (let i = 0; i < filterData.length; i++) {
        let item = filterData[i];
        let filterPersonIds = personId ? [personId] : [this.editTemp.leaderId, this.editTemp.driverId, this.editTemp.safetyId, this.editTemp.businessId, this.editTemp.addBusinessId, this.editTemp.additionSafetyId];
        for (let j = 0; j < filterPersonIds.length; j++) {
          if (this.autoPersonFilter(filterPersonIds[j], item.idIdentify, filterIdIdentifys, item.type)) {
            hasRepeat = true;
            if (this.editTempRepeatPersons.indexOf(filterPersonIds[j]) == -1) {
              this.editTempRepeatPersons.push(filterPersonIds[j])
            }
          }
        }

      }
      if (hasRepeat == true) {
        this.$message.warning('与其他排班存在重复的排班人员');
      }

    },
    /**
 * @description: 判断某个排班的车辆是否与其他排班的同为早送或晚收的正常排班车辆重复
 * @param {*} carId 需要判断是否重复的车辆id
 * @param {*} idIdentify 需要判断车辆所在的排班idIdentify，编辑功能idIdentify存在，添加功能idIdentify不存在
 * @param {*} filterIdIdentifys 编辑功能时可能同时存在多个排班需要判断，需要排除这多个排班，其他情况该参数默认为空，多个排班的idIdentify
 * @param {*} type 编辑的排班的排班类型
 * @return {*}
 */
    autoCarFilter(carId, idIdentify, filterIdIdentifys = [], type) {
      if (!carId) {
        return false
      }
      let repeatSchedules = this.list.filter((item, index) => {
        return (item.idIdentify != idIdentify) &&
          filterIdIdentifys.indexOf(item.idIdentify) == -1 &&
          ((item.type == type && idIdentify) || ((this.addTemp.type == 3 || this.addTemp.type == item.type) && !idIdentify)) &&  //(item.type==this.editTemp.type&&idIdentify)代表是编辑功能的判断，((this.addTemp.type==3||this.addTemp.type==item.type)&&!idIdentify)代表是添加功能判断
          (item.status % 2 != 0) &&
          carId == item.carId
      });
      let flag = false;
      if (repeatSchedules.length) {
        flag = true;
      }
      return flag;
    },
    /**
     * @description: 查找出需要编辑的排班，然后一个个排班看是否存在与其他排班车辆重复的情况
     * @param {*} carId 如果是编辑车辆的情况下，carId要传值，因为是比较具体的车辆跟其他排班车辆是否重复，如果是编辑所选排班类型则是要重新比较所有车辆跟其他排班的重复情况，不需要传carId
     * @param {*} editType 添加这个用于在编辑单个车辆时需要传carId 时carId==null（选择请选择）的情况下及时返回
     * @return {*}
     */
    editCarFilter() {
      let carId = this.editTemp.carId;
      this.editTempRepeatCar = null;
      if (!carId) {
        return;
      }
      let filterData = [], filterIdIdentifys = [], hasRepeat = false;
      //查找出要编辑的排班，由于编辑可以同时选择早送和晚收，因此编辑的可能是多个排班
      filterData = (this.list).filter((item, index) => {
        let flag = false;
        let commonTrueOrFalse = (this.editTemp.routeId == item.routeId) &&
          (this.editTemp.types.indexOf(item.type) >= 0) &&
          (this.editTemp.status == item.status && item.status % 2 != 0) &&
          (this.editTemp.planYear == item.planYear) &&
          (this.editTemp.planMonth == item.planMonth) &&
          (this.editTemp.planDay == item.planDay)
        if (item.idIdentify == this.editTemp.idIdentify) {
          flag = true;
        } else if (item.type != this.editTemp.type && commonTrueOrFalse) {
          flag = true;
        }
        if (flag == true) {
          filterIdIdentifys.push(item.idIdentify)
        }
        return flag;
      })

      for (let i = 0; i < filterData.length; i++) {
        let item = filterData[i];
        if (this.autoCarFilter(carId, item.idIdentify, filterIdIdentifys, item.type)) {
          hasRepeat = true;
          if (this.editTempRepeatCar !== carId) {
            this.editTempRepeatCar = carId;
          }
        }
      }
      if (hasRepeat == true) {
        this.$message.warning('与其他排班存在重复的排班车辆');
      }

    },
    //获取排班里面所有有重复的人员的id 
    getRepeatIds() {
      let that = this;
      //判断每个排班
      this.list.forEach((item, index) => {
        if (item.status % 2 == 0) {
          return;
        }
        //判断排班里面的每个人员
        let teamPersonIds = Array.from(new Set([item.leaderId, item.driverId, item.safetyId, item.businessId, item.addBusinessId, item.additionSafetyId]));
        teamPersonIds.forEach((item1, index1) => {
          if (item1) {
            judgeRepeat(item1);
          }
        })

        function judgeRepeat(personId) {
          for (let i = 0; i < that.list.length; i++) {
            if (
              item.idIdentify != that.list[i].idIdentify &&
              item.type == that.list[i].type &&
              that.list[i].status % 2 !== 0 &&
              (personId == that.list[i].addBusinessId || personId == that.list[i].additionSafetyId || personId == that.list[i].businessId || personId == that.list[i].driverId || personId == that.list[i].leaderId || personId == that.list[i].safetyId)
            ) {
              item[item.id + '-' + personId] = true;
            }

          }
        }
      })


    },
    //获取排班里面所有有重复的车辆的id 
    getCarRepeatIds() {
      let that = this;
      //判断每个排班
      this.list.forEach((item, index) => {
        if (item.status % 2 == 0) {
          return;
        }
        //判断排班里面的车辆
        let carId = item.carId;

        judgeRepeat(carId);


        function judgeRepeat(carId) {
          for (let i = 0; i < that.list.length; i++) {
            if (
              item.idIdentify != that.list[i].idIdentify &&
              item.type == that.list[i].type &&
              that.list[i].status % 2 !== 0 &&
              (carId == that.list[i].carId)
            ) {
              item[item.id + 'car' + carId] = true;
            }

          }
        }
      })


    },


    //重置
    resetTemp() {
      this.temp = {

      };
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      // 查找数据
      exportExcelReq({ excelType: 19, title: '实际排班表', times: [new Date(this.listQuery.selectDate).getTime()] }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "实际排班" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      });
    },
    sortByRouteName(arr) {
      arr.forEach((item, index) => {
        item.routeName = item.routeName.replace('\t', '')
        item.routeName = item.routeName.trim();
      })

      arr.sort((a, b) => { return this.sortDevName(a.routeName, b.routeName) })
    },
    sortDevName(str1, str2) {
      let res = 0
      for (let i = 0; i < Math.max(str1.length, str2.length); i++) {
        // if (!str1[i] || !str2[i]) {
        //   res = str1.length - str2.length
        //   break
        // }
        const char1 = str1[i] || ''
        const char1Type = this.getChartType(char1)
        const char2 = str2[i] || ''
        const char2Type = this.getChartType(char2)

        // 类型相同的逐个比较字符
        if (char1Type[0] === char2Type[0]) {
          if (char1 === char2) {
            continue
          } else {
            if (char1Type[0] === "zh") {
              res = char1.localeCompare(char2)
            } else if (char1Type[0] === "en") {
              res = char1.charCodeAt(0) - char2.charCodeAt(0)
            } else if (char1Type[0] === "number") {
              res = char1 - char2
            } else {
              res = char1 - char2
            }
            break
          }
        } else {
          // 类型不同的，直接用返回的数字相减
          res = char1Type[1] - char2Type[1]
          break
        }
      }
      return res
    },
    getChartType(char) {
      // 数字可按照排序的要求进行自定义，我这边产品的要求是
      // 数字（0->9）->大写字母（A->Z）->小写字母（a->z）->中文拼音（a->z）

      if (/^[\u4E00-\u9FA5]+$/.test(char)) {
        return ["zh", 300]
      }
      if (/^[a-zA-Z]$/.test(char)) {
        return ["en", 200]
      }
      if (/^[0-9]$/.test(char)) {
        return ["number", 100]
      }
      return ["others", 999]
    },
    getMaxStrLength(arr) {
      return arr.sort((a, b) => { return b.routeName.length - a.routeName.length })[0].routeName.length;
    },

    sortName(arr) {
      arr.forEach((item, index) => {
        item.routeName = item.routeName.replace('\t', '')
        item.routeName = item.routeName.trim();
      })
      let maxStrLength = this.getMaxStrLength(arr)
      for (let i = 0; i < maxStrLength; i++) {
        arr.sort((a, b) => {
          let char1 = a.routeName.substr(0, i + 1);
          let char2 = b.routeName.substr(0, i + 1);
          if (char1 !== char2) {
            return 1;
          }
        })
      }
    }



  }
};
</script>
