import request from '../../../request'
export function queryRouteList(data) {
    return request({
      url: '/eoms/route/queryList',
      method: 'post',
      data,
    })
}
export function queryGroupRouteReport(data) {
    return request({
      url: '/eoms/report/queryGroupRouteReport',
      method: 'post',
      data,
    })
}
export function queryGroupTaskReport(data) {
    return request({
      url: '/eoms/report/queryGroupTaskReport',
      method: 'post',
      data,
    })
}
