<template>
  <div class="app-container" id="progress">
    <div class="tableBox">
      <el-tabs v-model="activeName" @tab-click="handleClick" class="t-tabs">
        <div class="t-tab-bottom"></div>
        <div class="filter-container">
          <el-date-picker class="filter-item" v-model="listQuery.selectDate" align="right" type="date" @change="getList" value-format="timestamp" placeholder="选择日期" :picker-options="pickerOptions">
          </el-date-picker>

        </div>
        <el-tab-pane label="早送" name="first">
          <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
            <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
            <el-table-column label="组名" min-width="80px" align="center">
              <template slot-scope="{row}">
                <span>{{ row.groupName }}</span>
              </template>
            </el-table-column>
            <el-table-column label="车牌号" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.carLicenseNo }}</span>
              </template>
            </el-table-column>
            <el-table-column label="道路" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.routeName }}</span>
              </template>
            </el-table-column>
            <el-table-column label="状态" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.status==1||scope.row.status==3?'正常':'取消' }}</span>
              </template>
            </el-table-column>
            <el-table-column label="交接情况" min-width="400px" align="center">
              <template slot-scope="scope">
                <div class="am oneday">
                  <div class="routeName link-type" @click="gotask(scope.row)">详细信息</div>
                  <div class="progress">
                    <el-popover placement="right" title="网点" width="200" trigger="click" @show="popShow(scope.row)">
                      <div class="progressNetwork">
                        <div v-for="(item,index) in taskList" :key="item.id" class="taskList" :class="{colorItem:item.finish==4||item.finish==5||item.finish==2,colorCancle:(item.status==2||item.status==4)}">{{item.destinationName}}</div>
                      </div>
                      <el-progress slot="reference" status="warning" :inline="true" :text-inside="true" :stroke-width="24" :format="setItemText(scope.row)" :percentage="scope.row.finishTask/(scope.row.taskCount- scope.row.cancelledTask)*100" v-if="scope.row.taskCount"></el-progress>
                    </el-popover>

                  </div>

                </div>
              </template>
            </el-table-column>
            <el-table-column label="出库" min-width="120px" align="center">
              <template slot-scope="scope">
                <div class="inAndOut">{{"出库("+scope.row.intOutPackageNum+")" }} </div>
              </template>
            </el-table-column>
            <el-table-column label="交接" min-width="120px" align="center">
              <template slot-scope="scope">
                <div class="inAndOut" :class="{red:scope.row.pointHandoverPackageNum!==scope.row.intOutPackageNum}">{{"交接("+scope.row.pointHandoverPackageNum+")" }} </div>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="晚收" name="second">
          <el-table :key="tableKey" v-loading="listLoading" :data="list1" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
            <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
            <el-table-column label="组名" min-width="80px" align="center">
              <template slot-scope="{row}">
                <span>{{ row.groupName }}</span>
              </template>
            </el-table-column>
            <el-table-column label="车牌号" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.carLicenseNo }}</span>
              </template>
            </el-table-column>
            <el-table-column label="道路" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.routeName }}</span>
              </template>
            </el-table-column>
            <el-table-column label="状态" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.status==1||scope.row.status==3?'正常':'取消' }}</span>
              </template>
            </el-table-column>
            <el-table-column label="交接情况" min-width="400px" align="center">
              <template slot-scope="scope">

                <div class="am oneday">
                  <div class="routeName link-type" @click="gotask(scope.row)">详细信息</div>
                  <div class="progress">
                    <el-popover placement="right" title="网点" width="200" trigger="click" @show="popShow(scope.row)">
                      <div class="progressNetwork">
                        <div v-for="(item,index) in taskList" :key="item.id" class="taskList" :class="{colorItem:item.finish==4||item.finish==5||item.finish==2,colorCancle:(item.status==2||item.status==4)}">{{item.destinationName}}</div>

                      </div>
                      <el-progress :format="setItemText(scope.row)" slot="reference" status="warning" :inline="true" :text-inside="true" :stroke-width="24" :percentage="scope.row.finishTask/(scope.row.taskCount- scope.row.cancelledTask)*100" v-if="scope.row.taskCount"></el-progress>

                    </el-popover>

                  </div>

                </div>
              </template>
            </el-table-column>
            <el-table-column label="交接" min-width="120px" align="center">
              <template slot-scope="scope">
                <div class="inAndOut">{{"交接("+scope.row.pointHandoverPackageNum+")" }} </div>
              </template>
            </el-table-column>
            <el-table-column label="入库" min-width="120px" align="center">
              <template slot-scope="scope">
                <div class="inAndOut" :class="{red:scope.row.pointHandoverPackageNum!==scope.row.intOutPackageNum}">{{"入库("+scope.row.intOutPackageNum+")" }} </div>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </div>
    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
//大队列表，添加车组，编辑，删除，搜索

// import {
//   progressList
// } from "../../../api/managementArchives/vehicleArchives";
import {
  progressList
} from "../../../mock/mock";
import { formatDate } from "../../../../src/common"; //时间格式化
import { allDestination } from '../../../api/managementArchives/network'
import { progresssStatistics } from '../../../api/progress'
import { actuallyByScheduling } from "../../../api/schedule/schedule";

let myLoading = undefined; // 弹窗服务调用

export default {
  name: "progress1",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: [

      ],
      list1: [],
      list2: [],
      list3: [],

      total: 0, //大队数量
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        selectDate: new Date().setHours(0, 0, 0, 0)
      },

      //添加或编辑使用的数据
      temp: {
        id: null,
        teamName: null,
        geometryId: null,
        geometryName: null,
        leaderId: null,
        leaderName: null,
        createTime: null,
        status: null
      },
      dialogFormVisible: false, //添加或编辑窗口显隐
      dialogStatus: "", //控制是编辑窗口还是添加窗口
      textMap: {
        update: "编辑",
        create: "添加"
      },
      taskList: [],
      //添加或编辑表单校验
      rules: {
        teamName: [
          { required: true, message: "请填写大队名称", trigger: "blur" }
        ],
        status: [
          { required: true, message: "请选择大队状态", trigger: "change" }
        ]
      },
      activeName: 'first',
      pickerOptions: {
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      },
      intervalId: null,


    };
  },
  beforeDestroy() {
    this.clear();

  },
  created() {
    this.getList(); //初始化获取列表
    // this.dataRefreh()
  },
  methods: {

    // 定时刷新数据函数
    dataRefreh() {
      // 计时器正在进行中，退出函数
      if (this.intervalId != null) {
        return;
      }
      // 计时器为空，操作
      this.intervalId = setInterval(() => {
        this.getList()
      }, 20000);
    },
    // 停止定时器
    clear() {
      clearInterval(this.intervalId); //清除计时器
      this.intervalId = null; //设置为null
    },
    setItemText(row) {
      return () => {

        return row.finishTask + '/' + (row.taskCount - row.cancelledTask)


      }
    },
    // // 自定义进度条文字
    // setItemText(row) {
    //   return () => {
    //      return row.finishTask / row.taskCount
    //     // if (row.taskCount) {
    //     //   return row.finishTask / row.taskCount

    //     // } else {
    //     //   return '0%';
    //     // }

    //   }
    // },
    gotask(row) {
      sessionStorage.schedule = 'schedule';
      sessionStorage.schedulingId = row.idIdentify;
      sessionStorage.schedulingDate = row.planYear + '_' + row.planMonth + '_' + row.planDay
      sessionStorage.time = new Date().getTime();
      sessionStorage.schedulePublish = row.publish;
      this.$router.push('/schedualManagement/schedual/task');

    },
    handleClick(tab, event) {
    },
    getList() {
      let that = this;
      this.listLoading = true;
      this.list = [];
      this.list1 = [];
      this.list2 = [];
      this.list3 = [];
      progresssStatistics({
        date: this.listQuery.selectDate,
        type: 2,
      })
        .then(res => {
          if (res.status == 0) {
            this.list = this.dealData(res.data);
            this.sortByRouteName(this.list);


          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          if (this.activeName == 'first') {
            that.listLoading = false;

          }
        });
      progresssStatistics({
        date: this.listQuery.selectDate,
        type: 1,
      })
        .then(res => {
          if (res.status == 0) {
            this.list1 = this.dealData(res.data);
            this.sortByRouteName(this.list1);

          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          if (this.activeName == 'second') {
            that.listLoading = false;

          }
        });
    },
    dealData(arr) {
      for (let i = 0; i < arr.length; i++) {

        arr[i] = Object.assign(arr[i], arr[i].schedulingEntityDto)
      }

      return arr;
    },
    popShow(row) {

      let _t = this;
      this.taskList = []
      actuallyByScheduling({ uuid: row.idIdentify })
        .then(res => {
          if (res.status == 0) {
            _t.taskList = res.data;
          } else {
            _t.taskList = [];
            _t.$message.error(res.msg);
          };
        })
        .finally(() => {
        });
    },

    //点击搜索
    handleFilter() {
      if (
        this.listQuery.teamName == "" ||
        this.listQuery.teamName == undefined
      ) {
        this.listQuery.teamName = null;
        this.getList();
        return;
      }
      this.getList();
      // teamSearch({
      //   teamName: this.listQuery.teamName
      // }).then(res => {
      //   if (res.status == 0) {
      //     let that = this;
      //     this.listQuery.page = 1;
      //     this.listLoading = false;
      //     this.list = res.data;
      //     this.total = 0;
      //     this.dealData();
      //   } else {
      //     this.$message.error(res.msg);
      //   }
      // });
    },


    //重置
    resetTemp() {
      this.temp = {
        id: null,
        teamName: null,
        geometryId: null,
        geometryName: null,
        leaderId: null,
        leaderName: null,
        createTime: null,
        status: null
      };
    },





    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    sortByRouteName(arr) {
      arr.forEach((item, index) => {
        item.routeName = item.routeName.replace('\t', '')
        item.routeName = item.routeName.trim();
      })

      arr.sort((a, b) => { return this.sortDevName(a.routeName, b.routeName) })
    },
    sortDevName(str1, str2) {
      let res = 0
      for (let i = 0; i < Math.max(str1.length, str2.length); i++) {
        // if (!str1[i] || !str2[i]) {
        //   res = str1.length - str2.length
        //   break
        // }
        const char1 = str1[i] || ''
        const char1Type = this.getChartType(char1)
        const char2 = str2[i] || ''
        const char2Type = this.getChartType(char2)
        // 类型相同的逐个比较字符
        if (char1Type[0] === char2Type[0]) {
          if (char1 === char2) {
            continue
          } else {
            if (char1Type[0] === "zh") {
              res = char1.localeCompare(char2)
            } else if (char1Type[0] === "en") {
              res = char1.charCodeAt(0) - char2.charCodeAt(0)
            } else if (char1Type[0] === "number") {
              res = char1 - char2
            } else {
              res = char1 - char2
            }
            break
          }
        } else {
          // 类型不同的，直接用返回的数字相减
          res = char1Type[1] - char2Type[1]
          break
        }
      }
      return res
    },
    getChartType(char) {
      // 数字可按照排序的要求进行自定义，我这边产品的要求是
      // 数字（0->9）->大写字母（A->Z）->小写字母（a->z）->中文拼音（a->z）

      if (/^[\u4E00-\u9FA5]+$/.test(char)) {
        return ["zh", 300]
      }
      if (/^[a-zA-Z]$/.test(char)) {
        return ["en", 200]
      }
      if (/^[0-9]$/.test(char)) {
        return ["number", 100]
      }
      return ["others", 999]
    },

  }
};
</script>
<style lang="scss">
#progress {
  padding: 0;
  .tableBox {
    height: calc(100vh - 180px);
  }
  .el-tabs__header {
    background: #eaecf2;
  }
  .el-tabs__item {
    background: white;
  }
  .filter-container {
    height: auto;
    border: 0px;
  }
  .oneday {
    display: inline-flex;
    align-items: center;
    // padding: 10px 20px 10px 10px;
    .routeName,
    .onedayText {
      height: 50px;
      line-height: 50px;
      padding: 0 10px;
    }
    .routeName {
      width: 150px;
      text-align: right;
      overflow: hidden;
      word-break: keep-all;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }

  .progress {
    width: 416px;
    height: 50px;
    .el-progress-bar__outer {
      background: #c9c9c9;
      height: 50px !important;
    }
  }

  .onedayText {
    width: 60px;
    border: 1px solid #ccc;
  }
  .inAndOut {
    display: inline-block;
    border: 1px solid #32be3f;
    border-radius: 10px;
    color: #32be3f;
    width: 115px;
    height: 36px;
    line-height: 36px;
  }
  .red {
    color: red;
    border: 1px solid red;
  }
  .allout {
    background-color: #ccc;
  }
  .el-progress-bar__outer,
  .el-progress-bar__inner {
    border-radius: 0;
  }
  .el-progress-bar__inner {
    // margin-left: 50%;
    .el-progress-bar__innerText {
      margin: 0;

      color: #000000;
      font-size: 18px;
    }
  }
  .el-tabs {
    border: 0;
    &.el-tabs--card > .el-tabs__header .el-tabs__nav {
      border: 0;
    }
    .el-tabs__item {
      min-width: 100px;
      border: 0;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      text-align: center;
      &.is-active {
        background: #31478f;
        color: white;
      }
    }
  }
  //tabs固定在顶部
  .el-tabs--card {
    height: calc(100vh - 300px);
    /* overflow-y: auto; */
  }
  .el-tab-pane {
    height: calc(100vh - 300px);
    overflow-y: auto;
  }
}
.progressNetwork {
  div {
    margin: 10px 0;
  }
  div:not(.colorItem) {
    color: #cccccc;
  }
}

.colorItem {
  color: #ffc323;
}
.colorCancle {
  text-decoration: line-through;
}
</style>









