 * @FilePath: \bda09-Web\src\components\myUpload\scheduleUpload.vue
-->
<template>
  <div>
    <el-dialog title="导入" :visible.sync="openUploadDialog" v-loading="loading" width="40%">
      <div class="myBox">
          <div class="el-upload__tip red topTip" slot="tip">将Excel排班表内容导入并覆盖 <span class="importDate">{{date}}</span> 的排班</div>
        <el-upload drag ref="upload" :limit="limitNum" :auto-upload="false" accept=".xls" :on-remove="handleRemove" :action="UploadUrl()" :before-upload="beforeUploadFile" :on-change="fileChange" :on-exceed="exceedFile" :on-success="handleSuccess" :on-error="handleError" :file-list="fileList">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">只能上传xls文件,且文件大小不得超过10M</div>
          <div class="el-upload__tip red" slot="tip">请在“帮助”界面的“文件下载”清单中下载标准模板，并使用标准模板填写信息后再上传</div>
        </el-upload>
      </div>

      <br />
      <div slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="uploadFile">上传</el-button>
        <el-button size="small" @click="openUploadDialog=false">取消</el-button>

      </div>

    </el-dialog>
  </div>
</template>
<script>
import {importFileReq} from "../../api/managementArchives/common";
import { formatDate } from '../../common'

export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    limitNum: {
      type: Number,
      default: 1
    },
    uploadData: {
      type: Object,
      required: true,
    },
    dataUpdated: {
      type: Boolean,
      default: false
    },
     date: {
      type: String,
      default: false
    }

  },
  data() {
    return {
      fileList: [],
      loading: false

    }
  },
  computed: {
    openUploadDialog: {
      get() {
        return this.visible
      },
      set(val) {
        this.$emit('update:visible', val) // openUploadDialog改变的时候通知父组件
        this.$refs.upload.clearFiles();
        this.fileList = []
      }
    }
  },

  methods: {
    formatDate,
    handleRemove(file, fileList) {
      if (file && file.status === "success") {
        //移除方法
        this.fileList = []
      }
    },
    // 文件超出个数限制时的钩子
    exceedFile(files, fileList) {
      this.$message.warning(`只能选择 ${this.limitNum} 个文件，当前共选择了 ${files.length + fileList.length} 个`);
    },
    // 文件状态改变时的钩子
    fileChange(file, fileList) {
      this.fileList = [file.raw]
    },
    // 上传文件之前的钩子, 参数为上传的文件,若返回 false 或者返回 Promise 且被 reject，则停止上传
    beforeUploadFile(file) {

      let extension = file.name.substring(file.name.lastIndexOf('.') + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== 'xls' && extension !== 'XLS') {
        this.$message.warning('只能上传后缀是.xls的文件');
        return false
      }
      if (size > 10) {
        this.$message.warning('文件大小不得超过10M');
        return false
      }
    },
    // 文件上传成功时的钩子
    handleSuccess(res, file, fileList) {
      this.$message.success('文件上传成功');
    },
    // 文件上传失败时的钩子
    handleError(err, file, fileList) {
      this.$message.error('文件上传失败');
    },
    UploadUrl: function () {
      // 因为action参数是必填项，我们使用二次确认进行文件上传时，直接填上传文件的url会因为没有参数导致api报404，所以这里将action设置为一个返回为空的方法就行，避免抛错
      return ""
    },
    uploadFile() {
      this.$emit('dataUpdated', false);
      if (this.fileList.length === 0) {
        this.$message.warning('请选择文件');
      } else {
            this.loading = true;
            importFileReq({ file: this.fileList[0], creator: JSON.parse(sessionStorage.userInfo).UserName, type: this.uploadData.type, date: this.date })
              .then(res1 => {
                this.$message.success(res1.data);
                this.$emit('dataUpdated', true);
                this.openUploadDialog = false;
              })
              .catch(() => {

              }).finally(() => {
                this.$refs.upload.clearFiles();
                this.loading = false;

                this.fileList = []
              });
      }
    }

  }
}
</script>
<style lang="scss">
.myBox {
  //排班日期居中
  .el-form-item__label {
    height: 60px;
    line-height: 60px;
  }
}
</style>
<style scoped lang="scss">
$theme-color: #324890;
.myBox {
//   display: flex;
  //提示颜色修改
  .el-upload__tip {
    font-size: 14px;
    &.red {
      color: red;
      font-size: 16px;
    }
    &.topTip{
    margin-bottom:20px;
    }
  }
  .importDate{
      color:$theme-color;
  }
}
</style>