<template>
  <div id="bankRealTimeData">
    <el-card class="box-card box-card1">
      <div slot="header" class="clearfix">
        <el-divider direction="vertical"></el-divider>
        <span>当日任务</span>
      </div>
      <div class="boxCardItem boxCardItem1">
        <div class="topBar">
          <ul>
            <li></li>
            <li>未完成</li>
          </ul>
          <ul>
            <li></li>
            <li>已完成</li>
          </ul>
        </div>
        <div class="taskBox">
          <div class="taskItem" :key="index" v-for="(item,index) in tasksList">
            <div class="title">{{item.name}}</div>
            <div class="pieContentBox">
              <div class="taskBoxChild">
                <div class="taskNum">{{item.finishTask}}</div>
                <div class="taskText">已完成</div>
              </div>
              <div class="taskBoxChild">
                <div class="taskNum">{{item.taskCount-item.finishTask}}</div>
                <div class="taskText">未完成</div>
              </div>
              <div :id="'pie'+index"></div>
            </div>
          </div>
          <div class="taskItem">
            <div class="title">其他任务</div>
            <div class="pieContentBox">
              <div class="taskBoxChild">
                <div class="taskNum">{{addNum}}</div>
                <div class="taskText">加钞</div>
              </div>
              <div class="taskBoxChild">
                <div class="taskNum">{{payNum}}</div>
                <div class="taskText">人行提送款</div>
              </div>
              <div class="taskBoxChild">
                <div class="taskNum">{{receiveNum}}</div>
                <div class="taskText">上门收款</div>
              </div>

            </div>
          </div>

        </div>
      </div>
    </el-card>
    <el-card class="box-card box-card2">
      <div slot="header" class="clearfix">
        <el-divider direction="vertical"></el-divider>
        <span>数据分布图</span>
      </div>
      <div class="boxCardItem boxCardItem2">
        <div class="taskBar" id="chartBar"></div>
        <!-- <el-divider direction="vertical"></el-divider> -->
        <!-- <div class="taskPie" id="mapContent"></div> -->
      </div>
    </el-card>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import { selectTodayTask, selectTodayBankPointPackage } from "../../api/realTimeData";//出入库统计
export default {
  name: "bankRealTimeData",
  data() {
    return {
      tasksList: [
        {
          name: '早送任务',
          finishTask: 0,
          taskCount: 0,
          cancelledTask: 0
        },
        {
          name: '晚收任务',
          finishTask: 0,
          taskCount: 0,
          cancelledTask: 0
        },
        // {
        //   name: '其他任务',
        //   finishTask : 0,
        //   taskCount : 0,
        //   cancelledTask:0
        // },
      ],
      myChart0: null,
      myChart1: null,
      myChart2: null,
      chartBar: null,
      mapCanvas: null,
      addNum: 0,
      payNum: 0,
      receiveNum: 0


    };
  },
  created() {
    window.addEventListener('resize', this.throttle(this.echartsResize, 1000), false);

  },
  mounted() {
    Promise.all(this.getTask()).then((res) => {
      this.tasksList = [res[0], res[1]]
      this.tasksList[0].name = '早送任务';
      this.tasksList[1].name = '晚收任务';
      this.addNum = res[2].taskCount - res[2].cancelledTask;
      this.payNum = res[3].taskCount - res[3].cancelledTask;
      this.receiveNum = res[4].taskCount - res[4].cancelledTask;
      // this.addNum:0,
      // payNum:0,
      // receiveNum:0

      // this.tasksList[2].name = '其他任务';
      for (let i = 0; i < 2; i++) {
        this.initPie(i)
      }
    })
    selectTodayBankPointPackage({
      bankerId: JSON.parse(this.$store.state.user.userInfo).PersonId,
    }).then((res) => {
      if (res.data) {
        let xData = [], sendData = [], receiveData = [],sendAndReceiveData=[];
        res.data.sort((a, b) => {
          return (a.sendNumber + a.receiveNumber) - (b.sendNumber + b.receiveNumber)
        })
        res.data.forEach(element => {
          xData.push(element.pointName);
          sendData.push(element.sendNumber);
          receiveData.push(element.receiveNumber);
           sendAndReceiveData.push(element.sendNumber+element.receiveNumber)
        });
       
        // this.initBar2(xData, sendData, receiveData);
        this.initBar(xData, sendAndReceiveData, sendData,receiveData);

      } else {
        this.initBar();

      }
    })

    // this.drawChina();

  },

  beforeDestroy() {
    this.myChart0 && this.myChart0.dispose()
    this.myChart1 && this.myChart1.dispose()
    this.myChart2 && this.myChart2.dispose()
    this.chartBar && this.chartBar.dispose()
    this.mapCanvas && this.mapCanvas.dispose()
    this.myChart0 = null;
    this.myChart1 = null;
    this.myChart2 = null;
    this.chartBar = null;
    this.mapCanvas = null;


  },
  methods: {
    getTask() {
      let arr = [], type;
      for (let i = 1; i < 6; i++) {
        switch (i) {
          case 1:
            type = 2;
            break;
          case 2:
            type = 1;
            break;
          case 3:
            type = 5;
            break;
          case 4:
            type = 9;
            break;
          case 5:
            type = 7;
            break;

        }

        let p = new Promise((resolve, reject) => {
          selectTodayTask({
            bankerId: JSON.parse(this.$store.state.user.userInfo).PersonId,
            type: type
          }).then((res) => {
            if (res.data == null) {
              res.data = []
            }
            resolve(res.data);
          }).catch(() => {
            reject();
          })

        })
        arr.push(p);
      }
      return arr;


    },
    throttle(func, delay) {
      var prev = Date.now();
      return function () {
        var context = this;
        var args = arguments;
        var now = Date.now();
        if (now - prev >= delay) {
          func.apply(context, args);
          prev = Date.now();
        }
      }
    },
    echartsResize() {
      this.myChart0 && this.myChart0.resize()
      this.myChart1 && this.myChart1.resize()
      this.myChart2 && this.myChart2.resize()
      this.chartBar && this.chartBar.resize()
      this.mapCanvas && this.mapCanvas.resize()

    },
    initChartBar() {
      var chartDom = document.getElementById('chartBar');
      var myChart = echarts.init(chartDom);
      var option;

      option = {
        tooltip: {
          trigger: 'axis',
        },
        grid: {   //绘图区调整
          x: 80,  //左留白
          y: 10,   //上留白
          x2: 10,  //右留白
          y2: 10   //下留白
        },
        xAxis: {
          type: 'value',
          show: false,
        },
        yAxis: {
          type: 'category',
          data: ['累计任务', '已调度', '已完成', '工作中'],
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },

        },
        series: [
          {
            type: 'bar',
            barMinHeight: 25,
            //  barWidth: 48,  //柱宽度
            data: [72, 0, 41, 42],
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                  offset: 0,
                  color: "#38BDF6" // 0% 处的颜色
                }, {
                  offset: 1,
                  color: "#3C7EFE" // 100% 处的颜色
                }], false)
              },

            },
            barCategoryGap: '35%',
            label: {
              show: true,
              position: 'insideRight',
              distance: 13,
              color: '#fff'
            },

          }
        ]
      };

      option && myChart.setOption(option);

    },
    initChartPie() {
      var chartDom = document.getElementById('chartPie');
      var myChart = echarts.init(chartDom);
      var option;

      option = {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },

        legend: {
          bottom: 0,
          left: 'center'
        },

        series: [
          {
            name: '每日任务',
            type: 'pie',
            radius: ['40%', '80%'],
            avoidLabelOverlap: false,
            itemStyle: {
              borderColor: '#fff',
              borderWidth: 14,
              color: function (params) {
                var colorList = [
                  {
                    c1: '#5EBEFF',
                    c2: '#C39CFF'
                  },
                  {
                    c1: ' #00C5A5',
                    c2: '#8DDAD5'
                  },
                  {
                    c1: '#E58FE1',
                    c2: '#FA8B8C'
                  }, {
                    c1: '#F7E1EB',
                    c2: '#F7E1EB'
                  }]
                return new echarts.graphic.LinearGradient(1, 0, 0, 0, [{ //颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上

                  offset: 0,
                  color: colorList[params.dataIndex].c1
                }, {
                  offset: 1,
                  color: colorList[params.dataIndex].c2
                }])
                /*  return colorList[params.dataIndex]*/
              }


            },
            label: {
              show: true,
              position: 'outside'
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '16',
                fontWeight: 'bold'
              }
            },
            labelLine: {
              smooth: 0.2,
              length: 10,
              length2: 10
            },
            data: [
              { value: 72, name: '累计任务' },
              { value: 30, name: '已调度' },
              { value: 41, name: '已完成' },
              { value: 42, name: '工作中' }
            ],

          }
        ]
      };

      option && myChart.setOption(option);

    },
    initPie(index) {

      var chartDom = document.getElementById('pie' + index);
      let that = this;
      this['myChart' + index] = echarts.init(chartDom);
      var value1, value2;
      var option;
      value1 = this.tasksList[index].finishTask
      value2 = this.tasksList[index].taskCount - this.tasksList[index].finishTask

      option = { //加载数据图表            
        title: {
        },

        // legend: {
        //     textStyle: { //图例文字的样式
        //         color: '#000',
        //         fontSize: 12
        //     },
        //     type: 'scroll',
        //     orient: 'vertical',
        //     right: 10,
        //     top: 20,
        //     bottom: 20,
        //     data: ['已完成','任务总数']
        // },
        series: [{
          name: '当日任务',
          type: 'pie',
          radius: '75%',
          //   roseType: 'radius',
          center: ['50%', '50%'],
          label: {
            show: false,
          },
          data: [
            { value: value1, name: '已完成' },
            { value: value2, name: '未完成' }
          ],
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            },
            normal: {
              color: function (params) {
                var colorList = [
                  {
                    c1: ' #38BDF6',
                    c2: '#3C7EFE'
                  },
                  {
                    c1: ' #76EBE1',
                    c2: '#76EBE1'
                  }
                ]
                return new echarts.graphic.LinearGradient(1, 0, 0, 0, [{ //颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上

                  offset: 0,
                  color: colorList[params.dataIndex].c1
                }, {
                  offset: 1,
                  color: colorList[params.dataIndex].c2
                }])
                /*  return colorList[params.dataIndex]*/
              }
            }
          }

        }]
      }




      option && this['myChart' + index].setOption(option);
      let currentIndex = 0;

      // setTimeout(function () {

      currentIndex--;

      let dataLen = option.series[0].data.length;

      // 取消之前高亮的图形

      this['myChart' + index].dispatchAction({

        type: 'downplay',

        seriesIndex: 0,

        dataIndex: currentIndex

      });

      currentIndex = (currentIndex + 1) % dataLen;

      // 高亮当前图形

      this['myChart' + index].dispatchAction({

        type: 'highlight',

        seriesIndex: 0,

        dataIndex: currentIndex

      });

      // }, 1000);

      $("#pie" + index).mouseenter(function () {

        that['myChart' + index].dispatchAction({

          type: 'downplay',

          seriesIndex: 0,

          dataIndex: currentIndex

        });

      });

      $("#pie" + index).mouseleave(function () {

        that['myChart' + index].dispatchAction({

          type: 'highlight',

          seriesIndex: 0,

          dataIndex: currentIndex

        });

      });

    },
   
