      this.mapCanvas = echarts.init(document.getElementById("mapContent"));
      // document.getElementById("mapCanvas").style.width = 700 + "px";
      // document.getElementById("mapCanvas").style.height = 440 + "px";
      this.mapCanvas.resize(); // 直接加这句即可
      let option = {
        // title: {  //标题样式
        //   // text: 'ECharts 中国地图',
        //   x: "center",
        //   textStyle: {
        //     fontSize: 18,
