        // },
        // tooltip: {  //这里设置提示框
        //   trigger: 'item',  //数据项图形触发
        //   backgroundColor: "red",  //提示框浮层的背景颜色。
        //   //字符串模板(地图): {a}（系列名称），{b}（区域名称），{c}（合并数值）,{d}（无）
        //   formatter: '地区：{b}<br/>模拟数据：{c}'
        // },
        // visualMap: {//视觉映射组件
        //   show: false,
        //   x: 'right',
        //   y: 'bottom',
        //   // min: 1000,
        //   // max: 500000,
        //   // text: ['0~1000', '1000~2000', '2000~3000', '3000~4000', '4000~5000', '5000~500000'],
        //   realtime: false,  //拖拽时，是否实时更新
