          ]
        },
        series: [
          {
            name: '模拟数据',
            type: 'map',
            mapType: 'china',
            roam: false,//是否开启鼠标缩放和平移漫游
            itemStyle: {
              normal: {
                label: { show: false },

                // areaColor: 'rgb(46, 53, 94)',
                // color: 'rgb(46, 53, 94)',
                // borderColor: 'rgb(135, 166, 255)'
                areaColor: 'rgb(216, 229, 255)',
                color: 'rgb(216, 229, 255)',
                borderColor: 'rgb(135, 166, 255)'
              },
              emphasis: {
                // label: { show: true, color: '#2E355E' },
                // areaColor: 'rgb(135, 166, 255)',
                // color: 'rgb(135, 166, 255)',

                label: { show: true, color: '#2E355E' },
