      };
      // 使用刚指定的配置项和数据显示图表。
      this.mapCanvas.setOption(option);
    },
    drawChina1() {
      let map = new BMap.Map("mapContent");
      let point = new BMap.Point(112.857615, 28.228625);
      map.centerAndZoom(point, 7);
      map.enableScrollWheelZoom(true);
      //设置放大级别固定为7
      //   map.setMaxZoom(7);
      //   map.setMinZoom(7);
      //禁止拖拽
      //   map.disableDragging();
      //禁用双击放大
      //   map.disableDoubleClickZoom();
      //   //禁用双指操作缩放
      //   map.disablePinchToZoom();
      //   //禁用键盘操作
      //   map.disableKeyboard();
      //创建一个覆盖物把当前显示的地图全部挡住，作为白色背景
      //获取四个角的坐标
      //   let currentBounds = map.getBounds();
      //   let sw = currentBounds.getSouthWest();
      //   let ne = currentBounds.getNorthEast();
      //   var wn = new BMap.Point(sw.lng, ne.lat);
      //   var es = new BMap.Point(ne.lng, sw.lat);
      //   //创建矩形覆盖物
      //   let bgPly = new BMap.Polygon([sw, wn, ne, es], { fillColor: '#ffffff', fillOpacity: 1, strokeColor: '#ffffff' });
      // map.addOverlay(bgPly);
      //   let boundary = new BMap.Boundary();
      var geoc = new BMap.Geocoder();
      geoc.getLocation(point, function (rs) {
        var addComp = rs.addressComponents;
        // $('#result_l').text(addComp.province + ", " + addComp.city + ", " + addComp.district + ", " + addComp.street + ", " + addComp.streetNumber);
      })

      //根据地区名称（省市县）在地图上绘制多边形区域
      let boundary = new BMap.Boundary();
      boundary.get('湖南省', rs => {
        //多边形的坐标点数组
        let plyPoints = [];
        rs.boundaries[0].split(';').map(item => {
          item = item.split(',');
          plyPoints.push(new BMap.Point(parseFloat(item[0]), parseFloat(item[1])));
        })
        let ply = new BMap.Polygon(plyPoints, { fillColor: '#A8D780', fillOpacity: 1, strokeColor: '#000000', strokeOpacity: 0, strokeWeight: 1 });
        map.addOverlay(ply);
      });
      //   boundary.get('湖南', function (rs) {
      //     console.log('外轮廓：', rs.boundaries[0]);
      //     console.log('内镂空：', rs.boundaries[1]);
      //     var hole = new BMap.Polygon(rs.boundaries, {
      //       fillColor: 'blue',
      //       fillOpacity: 0.2
      //     });
      //     map.addOverlay(hole);
      //   });
      //   boundary.get('陕西', function (rs) {
      //     // console.log('外轮廓：', rs.boundaries[0]);
      //     // console.log('内镂空：', rs.boundaries[1]);
      //     var hole = new BMap.Polygon(rs.boundaries, {
      //       fillColor: 'blue',
      //       fillOpacity: 0.2
      //     });
      //     map.addOverlay(hole);
      //   });
    },
    initBar(xData = [], sendAndReceiveData = [], sendData = [],receiveData=[]) {
      var chartDom = document.getElementById('chartBar');
      this.chartBar = echarts.init(chartDom);

        // xData = ["项目1", "项目2", "项目3", "项目4", "项目5", "项目6"];
        // // 送箱数据数组
        // sendAndReceiveData = [320, 302, 301, 334, 390, 0];
        // // 接箱数据数组
        // sendData = [320, 132, 101, 0, 90, 0];
      var color = [
        {
          type: "linear",
          x: 0,
          x2: 1,
          y: 0,
          y2: 0,
          colorStops: [
            {
              offset: 0,
              color: "#327CD3",
            },
            {
              offset: 0.5,
              color: "#327CD3",
            },
            {
              offset: 0.5,
              color: "#55A3FF",
            },
            {
              offset: 1,
              color: "#55A3FF",
            },
          ],
        },
        {
          type: "linear",
          x: 0,
          x2: 1,
          y: 0,
          y2: 0,
          colorStops: [
            {
              offset: 0,
              color: "#E0623A",
            },
            {
              offset: 0.5,
              color: "#E0623A",
            },
            {
              offset: 0.5,
              color: "#FF8761",
            },
            {
              offset: 1,
              color: "#FF8761",
            },
          ],
        },
      ];
      var barWidth = 20;
      var constData = [];
      var showData = [];
      var otherData = [];
      for (var i = 0; i < sendAndReceiveData.length; i++) {
        sendAndReceiveData[i] = Number(sendAndReceiveData[i]);
        sendData[i] = Number(sendData[i]);
        otherData[i] = sendAndReceiveData[i] - sendData[i];
        if (sendAndReceiveData[i] <= 0) {
          constData.push(0);
          showData.push({
            value: 1,
            itemStyle: {
              normal: {
                borderColor: "rgba(0,0,0,0)",
                borderWidth: 2,
                color: "rgba(0,0,0,0)",
              },
            },
          });
        } else {
          if (sendData[i] == sendAndReceiveData[i]) {
            constData.push({
              value: 1,
              itemStyle: {
                normal: {
                  color: color[1],
                },
              },
            });
          } else {
            constData.push(1);
          }
          if (sendData[i] > 0) {
            showData.push({
              value: sendAndReceiveData[i],
              itemStyle: {
                normal: {
                  borderColor: "#FFC0AC",
                  borderWidth: 2,
                  color: "#FFC0AC",
                },
              },
            });
          } else {
            showData.push({
              value: sendAndReceiveData[i],
              itemStyle: {
                normal: {
                  borderColor: "#71b0f8",
                  borderWidth: 2,
                  color: "#71b0f8",
                },
              },
            });
          }
        }
      }
      var option = {
        tooltip: {
          trigger: "axis",
          formatter: function (params) {
            return params[0].axisValue + "：" +
              "<br/>送箱：" + sendData[params[0].dataIndex] +
              "<br/>接箱：" + receiveData[params[0].dataIndex];
          },
        },
        legend: {
          data: [ "送箱","接箱"],
          right: 40,
          selectedMode :false
        },
        grid: {
          left: "3%", //图表距边框的距离
          right: "3%",
          top: "15%",
          bottom: "10%",
          containLabel: true,
        },
        xAxis: {
          data: xData,
          axisLabel: {
            show: true,
            textStyle: {
              color: '#000',  //更改坐标轴文字颜色
            }
          },

          axisLine: {
            lineStyle: {
              color: '#E0E0E0'
            }

          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          axisLine: {
            lineStyle: {
              color: '#E0E0E0'
            },
          },
          axisTick: {
            show: false,
          },

          axisLabel: {
            show: false
          },
          splitLine: {
            show: false
          }

        },
        series: [
          {
            z: 1,
            name: "接箱",
            type: "bar",
            barWidth: barWidth,
            stack: "总量",
            color: color[0],
            data: otherData,
            borderColor: '#fff',
            barGap: 200,
            barCategoryGap: 200,
            barGap: '30%'
          },
          {
            z: 2,
            name: "送箱",
            type: "bar",
            barWidth: barWidth,
            stack: "总量",
            color: color[1],
            data: sendData,
            borderColor: '#fff',
            barGap: 200,
            barCategoryGap: 200,
            barGap: '30%'
          },
          {
            z: 3,
            name: "项目",
            type: "pictorialBar",
            data: constData,
            symbol: "diamond",
            symbolOffset: ["0%", "50%"],
            symbolSize: [barWidth, 10],
            itemStyle: {
              normal: {
                color: color[0],
              },
            },
            tooltip: {
              show: false,
            },
          },
          {
            z: 4,
            name: "项目",
            type: "pictorialBar",
            data: otherData,
            symbol: "diamond",
            symbolPosition: "end",
            symbolOffset: ["0%", "-50%"],
            symbolSize: [barWidth, 10],
            itemStyle: {
              normal: {
                color: color[1],

              },
            },
            tooltip: {
              show: false,
            },
          },
          {
            z: 5,
            name: "项目",
            type: "pictorialBar",
            symbolPosition: "end",
            data: showData,
            symbol: "diamond",
            symbolOffset: ["0%", "-50%"],
            symbolSize: [barWidth - 4, (10 * (barWidth - 4)) / barWidth],
            tooltip: {
              show: false,
            },
          },
        ],
        dataZoom: [
          {   // 这个dataZoom组件，默认控制x轴。
            type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
            start: 0,      // 左边在 10% 的位置。
            end: 100,         // 右边在 60% 的位置。
            bottom: 0,
          },
          {   // 这个dataZoom组件，也控制x轴。
            type: 'inside', // 这个 dataZoom 组件是 inside 型 dataZoom 组件
            start: 0,      // 左边在 10% 的位置。
            end: 60,       // 右边在 60% 的位置。
            bottom: 0,

          }
        ],
        //  // 可左右滑动
        //     dataZoom: [{
        //       show: false,
        //       realtime: true,
        //       start: 0,
        //       end: 40
        //     }, {
        //       type: 'slider',
        //       realtime: false,
        //       start: 0,
        //       end: 40,
        //       zoomOnMouseWheel: false, // 禁用滚轮
        //       bottom: 20,
        //       height: 20
        //     }
        //     ],
      };
      option && this.chartBar.setOption(option);

    },
  
  },
};
</script>

