<template>
  <div class="app-container" id="realTimeData">
    <div :is='active'></div>
  </div>
</template>

<script>
import mainRealTimeData from "./mainRealTimeData.vue"
import bankRealTimeData from "./bankRealTimeData.vue"


export default {
  name: "realTimeData",
  components: {
    mainRealTimeData,
    bankRealTimeData
  },
  data() {
    return {
    active:''
    };
  },
  created(){
    if(JSON.parse(this.$store.state.user.userInfo).Power==5){
    this.active=bankRealTimeData;
    }else{
    this.active=mainRealTimeData;
    }
  }
};
</script>

<style lang="scss">
#realTimeData {
  &.app-container {
    background-color: white;
    width: calc(100vw);
    height: calc(100vh - 120px - 10px);
    margin: 10px 0px 0px;
    margin-left: 0;
    position: relative;
    z-index: 0;
  }
 
}
</style>









