<template>
  <div class="app-container" id="package">
    <div class="filter-container">
        <el-input v-model="listQuery.name" placeholder="请输入关键字搜索" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-select v-model="listQuery.belongToBankId" class="filter-item" placeholder="请选择银行" @change="handleFilter">
        <el-option v-for="item in bankOptions" :key="item.id" :label="item.name" :value="item.id" />
      </el-select>
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="handleFilter">搜索</el-button>
      <el-button class="filter-item" type="primary" @click="handleCreate" icon="el-icon-circle-plus-outline">添加</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="uploadVisible=true" icon="iconfont icondaoru">导入</el-button>


    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="库包名称" width="180px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.name }}</span>
          </template>
        </el-table-column>
        <el-table-column label="标签" width="250px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.rfid }}</span>
          </template>
        </el-table-column>
        <el-table-column label="库包类型" width="180px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.type==1?'归属银行':'未定义' }}</span>
          </template>
        </el-table-column>

        <el-table-column label="库包状态" min-width="180px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.status==1?'正常':(row.status==2?'废弃':'损坏')}}</span>
          </template>
        </el-table-column>
        <el-table-column label="所属银行" min-width="180px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.belongToBankName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="目的地" min-width="180px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.destinationName }}</span>
          </template>
        </el-table-column>

        <el-table-column label="操作" align="center" min-width="300" fixed="right" class-name="small-padding fixed-width" >
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleUpdate(row)" :disabled="row.status==4" icon="el-icon-edit-outline">编辑</el-button>
            <el-button type="warning" size="mini" @click="handleUnbind(row)" :disabled="!row.rfid" icon="iconfont iconjiesuo5">解绑标签</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog id="myDialog" @close="scrollTop" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center :class="{'editClass':true}">
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <el-form-item label="库包名称" prop="name">
          <el-input v-model="temp.name" />
        </el-form-item>
        <el-form-item label="标签" prop="rfidId">
          <el-select filterable v-model="temp.rfidId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in rfidOptions" :key="item.id" :label="item.rfid" :value="item.id" />
          </el-select>
        </el-form-item>

      
        <el-form-item label="库包状态" prop="status">
          <el-select filterable v-model="temp.status" class="filter-item" placeholder="请选择">
            <el-option v-for="item in statusOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="所属银行" prop="belongToBankId">
          <el-select filterable v-model="temp.belongToBankId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in bankOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.status==2" />
          </el-select>
        </el-form-item>
         <el-form-item label="目的地" prop="destinationId">
          <el-select filterable v-model="temp.destinationId" class="filter-item">
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id"  />
          </el-select>
        </el-form-item>
        
      </el-form>
      <!-- 暂时注释，目前暂时不用写周转箱
      <div class="red">*周转箱请不要选择目的地</div> -->
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>
  
    <el-dialog id="myDialog" :title="bindMap[bindDialogStatus]" :visible.sync="BindDialogFormVisible" center>
      <el-form ref="bindForm" :rules="bindRules" :model="bindTemp" label-position="left" class="formStyle">

        <el-form-item :label="`${bindMap[bindDialogStatus]}类型`" prop="type">
          <el-select filterable v-model="bindTemp.type" class="filter-item" placeholder="请选择">
            <el-option v-for="item in bindTypeOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.disabled" />
          </el-select>
        </el-form-item>
        <el-form-item label="司机" prop="driverId" v-if="bindTemp.type==1&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.driverId " class="filter-item" placeholder="请选择">
            <el-option v-for="item in driverOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="所属大队" prop="teamId" v-if="bindTemp.type==2&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.teamId " class="filter-item" placeholder="请选择">
            <el-option v-for="item in largeTeamOptions" :key="item.id" :label="item.teamName" :value="item.id" />
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bindDialogStatus=='bind'?bindSubmit():unbindSubmit()">确定</el-button>
      </div>
    </el-dialog>
<my-upload :visible.sync="uploadVisible"  :uploadData="{type:4}" @dataUpdated="reqSuccess"></my-upload>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import myUpload from "@/components/myUpload"; // secondary package based on el-pagination

//车辆列表，添加车辆，编辑，删除，搜索，批量添加车辆
import {

  bindDriver,
  unbindDriver,
  bindTeam,
  unbindTeam
} from "../../api/managementArchives/vehicleArchives";
import {
  selectBank
} from "../../api/managementArchives/bank";

import { allDestination } from "../../api/managementArchives/network";

import { formatDate } from "../../../src/common"; //格式化时间
//大队下拉框数据，车组下拉框数据
import {

  personsSelect,
  exportExcelReq
} from "../../api/managementArchives/common";


import {  saveOrUpdateP, unbindRfidP,  queryPpage } from "../../api/inOut/package";
import { queryR } from "../../api/inOut/rfid";

const carUsageOptions = [
  {
    key: -1,
    display_name: "请选择"
  },
  {
    key: 1,
    display_name: "使用中"
  },
  {
    key: 2,
    display_name: "维修中"
  },
  {
    key: 3,
    display_name: "保养中"
  }
];

//自定义设备号头验证规则
const reasonableLen = (rule, value, callback) => {
  const reasonableLength = /[0-9]{4}/;
  if (!reasonableLength.test(value)) {
    callback(new Error("长度为4"));
  } else {
    callback();
  }
};
//未选择
const unSelect = (rule, value, callback) => {
  if (value == -1) {
    callback(new Error("请选择"));
  } else {
    callback();
  }
};
let myLoading = undefined; // 弹窗服务调用
export default {
  name: "package",
  components: {
    Pagination,myUpload
  },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //车辆列表
      total: 0, //车辆总数
      listLoading: true,
      indexMethod: 1, // 序号
      listQuery: {
        page: 1,
        limit: 10,
        licenseNo: undefined
      },
      carUsageOptions, //车辆使用情况下拉数据
      runOptions: [
        {
          key: -1,
          display_name: "请选择"
        },
        {
          key: 0,
          display_name: "入库"
        },
        {
          key: 1,
          display_name: "上路"
        }
      ], //运行情况下拉数据
      groupsOptions: [], //车组下拉数据

      largeTeamOptions: [], //大队下拉数据
      channelNoOptions: [], //通道个数下拉数据
      //添加或编辑数据
      temp: {

      },
      dialogFormVisible: false, //添加或编辑窗口显隐
      dialogStatus: "", //控制是编辑窗口还是添加窗口
      textMap: {
        update: "编辑",
        create: "添加"
      },
      //添加或编辑表单校验
      rules: {

        name: [
          {
            required: true,
            message: "请填写库包名称",
            trigger: "blur"
          }
        ],

        rfidId: [
          {
            message: "请选择标签",
            trigger: "change",
            required: true
          }
        ],
        belongToBankId: [
          {
            message: "请选择所属银行",
            trigger: "change",
            required: true
          }
        ]
      },
    

  
      dialogVisible: false, //批量添加窗口显隐
      addAlltemp: {
        head: undefined,
        begin: undefined,
        end: undefined,
        channelNo: null
      },
      bindTypeOptions: [
        { id: null, name: "请选择" },
        { id: 1, name: "司机" },
        { id: 2, name: "大队" }

      ], //绑定类型下拉数据
      bindTemp: {

        type: 1
      },

      BindDialogFormVisible: false,
      bindDialogStatus: "",//控制是绑定还是解绑窗口
      bindMap: {
        bind: "绑定",
        unbind: "解绑"
      },
      bindRules: {
        type: [
          { required: true, message: "请选择绑定类型", trigger: "change", validator: unSelect }
        ],
        driverId: [
          { required: true, message: "请选择绑定司机", trigger: "change", validator: unSelect }
        ],
        teamId: [
          { required: true, message: "请选择绑定大队", trigger: "change", validator: unSelect }
        ]
      },
      rfidOptions: [{ id: null, rfid: "请选择" }],
      typeOptions: [{ id: 1, name: '归属银行' }, { id: 2, name: '未定义' }],
      statusOptions: [{ id: 1, name: '正常' }, { id: 2, name: '废弃' }, { id: 3, name: '损坏' }],
      bankOptions: [{ id: -1, name: '请选择' }],

      uploadVisible:false,
      networkOptions:[{id:null,name:'请选择'}],
      exportLoading: false,


    };
  },
  created() {
    this.getList(); //初始化获取车辆列表
    this.getSelect(); //初始化弹窗的下拉框
    this.getChannelNoOptions(); //初始化通道下拉数据
  },
  mounted() { },
  methods: {  
  
    reqSuccess: function(val){  //val用于接收子组件传过来的值
      if(val==true){
        this.getList();
      }
    },
    handleUnbind(row) {
      myLoading = this.$loading({ target: "#myDialog .el-dialog" });

      unbindRfidP({ id: row.id })
        .then(res => {
          if (res.status == 0) {
            this.getList();
            this.getSelect();

            this.$message.success(res.msg);
            this.BindDialogFormVisible = false;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          myLoading.close();
        });
    },
    findItem(locations, key, val) {
      let obj = {};
      obj = locations.find((item) => {
        return item[key] === val;
      });
      if (obj == undefined) {
        obj = {};
      }
      return obj;
    },
    filterObj(obj) {
      for (let index in obj) {
        if (obj[index] === -1 || obj[index] === null || obj[index] === undefined) {
          delete obj[index]
        }
      }
      return obj
    },
    handleBind(row) {
      this.bindTypeOptions = [
        { id: -1, name: "请选择" },
        { id: 1, name: "司机" },
        { id: 2, name: "大队" }

      ];
      this.bindDialogStatus = "bind";
      this.bindTemp = {
        type: 1,
        carId: row.id,
        driverId: row.driverId,
        teamId: row.teamId
      }
      this.BindDialogFormVisible = true;
    },

    bindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData, request;
          if (this.bindTemp.type == 1) {
            subData = {
              carId: this.bindTemp.carId,
              driverId: this.bindTemp.driverId,
            }
            request = bindDriver;

          } else if (this.bindTemp.type == 2) {
            subData = {
              carId: this.bindTemp.carId,
              teamId: this.bindTemp.teamId,
            }
            request = bindTeam;
          }
          request(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.getSelect();

                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });

        }
      })
    },
    unbindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });

          let subData, request;
          subData = {
            carId: this.bindTemp.carId
          }
          if (this.bindTemp.type == 1) {

            request = unbindDriver;

          } else if (this.bindTemp.type == 2) {

            request = unbindTeam;


          }
          request(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        }
      })
    },
    handleFilter(){
      this.listQuery.page=1;
      this.getList();
    },
    getList() {
      let _t = this;
      _t.listLoading = true;
      let subData={
         page: this.listQuery.page,
        limit: this.listQuery.limit,
        status:[1,3]
      }
      if(this.listQuery.belongToBankId){
      subData.belongToBankId=this.listQuery.belongToBankId
      }
      if(this.listQuery.name){
      subData.name=this.listQuery.name
      }
      queryPpage(subData)
        .then(res => {
          if (res.status == 0) {
            this.listLoading = false;
            this.list = res.data;

            this.total = res.count;
            //处理数据
            this.dealData();
            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
  
    dealData() {
      this.list.forEach((item, index) => {
        this.list[index].broughtTimeShow = formatDate(item.broughtTime, 1);
        this.list[index].registerTimeShow = formatDate(item.registerTime, 1);
      });
    },
    //初始化通道数据
    getChannelNoOptions() {
      this.channelNoOptions = [];
      for (let i = 0; i < 17; i++) this.channelNoOptions.push({ channelNo: i });
    },
   
  
  

    //重置添加或编辑数据
    resetTemp() {
      this.temp = {
        name: "",
        rfidId: null,
        // type: 1,
        status: 1,
        belongToBankId: null
      }
    },
   
    //点击添加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
      this.getRfid({});
    },
  
    //添加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
        
          let subData = {};

          subData = {
            name: this.temp.name,
            rfidId: this.temp.rfidId,
            type: this.temp.type,
            status: this.temp.status,
            destinationId:this.temp.destinationId
          }

          subData.rfid = this.findItem(this.rfidOptions, 'id', this.temp.rfidId).rfid;
          subData.belongToBankId = this.temp.belongToBankId

          if (this.temp.belongToBankId !== null) {
            subData.belongToBankName = this.findItem(this.bankOptions, 'id', this.temp.belongToBankId).name
          } else {
            subData.belongToBankName=null;
            subData.belongToBankId=null;
          }
          if (this.temp.destinationId !== null) {
            subData.destinationName = this.findItem(this.networkOptions, 'id', this.temp.destinationId).name;

          } else {
            subData.destinationId=null;
            subData.destinationName=null

          }


          saveOrUpdateP(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
                //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
                // this.$store.commit("realTimeMonitor/ISEDITFILES", true);
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
  

    //点击编辑
    handleUpdate(row) {
      this.getRfid(row);
      this.temp = {
        id: row.id,
        name: row.name,
        rfidId: row.rfidId,
        rfid: row.rfid,
        type: row.type,
        status: row.status,
        belongToBankId: row.belongToBankId,
        belongToBankName: row.belongToBankName,
        destinationId:row.destinationId,
        destinationName:row.destinationName
      }
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },

    //编辑
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
            myLoading = this.$loading({ target: "#myDialog .el-dialog" });
         

          let subData = { ...this.temp };
          subData.rfid = this.findItem(this.rfidOptions, 'id', this.temp.rfidId).rfid;



          if (this.temp.belongToBankId !== null) {
            subData.belongToBankName = this.findItem(this.bankOptions, 'id', this.temp.belongToBankId).name;

          } else {
            subData.belongToBankName=null;
             subData.belongToBankId=null;
          }
           if (this.temp.destinationId !== null) {
            subData.destinationName = this.findItem(this.networkOptions, 'id', this.temp.destinationId).name;

          } else {
            subData.destinationId=null;
            subData.destinationName=null;

          }


          saveOrUpdateP(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("编辑成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
 
  
  
    //初始化下拉框
    getSelect() {
      var that = this;
      personsSelect({ work: 1, status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.driverOptions = res.data;
          that.driverOptions.unshift({ id: -1, name: "请选择" });
        } else {
          if (res.data == null) {
            this.driverOptions = [];
            this.driverOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
      
      
      selectBank().then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            that.bankOptions = [{ id: null, name: "请选择" }];
            return;
          }
          that.bankOptions = res.data;
          that.bankOptions.unshift({ id: null, name: "请选择" });
        } else {
          if (res.data == null) {
            this.bankOptions = [];
          }
          this.$message.error(res.msg);
        }
      });
       
         allDestination({ type: -1 }).then(res => {
        if (res.status == 0) {
          if (res.data.records === null) {
            that.networkOptions = [];
            return;
          }
          that.networkOptions = res.data;
          that.networkOptions.unshift({ id: null, name: "请选择" });
        } else {
          if (res.data == null) {
            this.networkOptions = [];
          }
          this.$message.error(res.msg);
        }
      });

    },
     getRfid(row) {
       let that = this;
      this.rfidOptions = [];
      queryR({ rfidType: [90],status:[0] }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.rfidOptions = res.data;
            //把自身的卡添加进去，否则匹配不到
          if(row.rfidId){
          that.rfidOptions.unshift({ id: row.rfidId, rfid: row.rfid });
          }
          that.rfidOptions.unshift({ id: null, rfid: "请选择" });
        } else {
          if (res.data == null) {
            this.rfidOptions = [];
            this.rfidOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });

      
      
    },
   
    scrollTop() {
      // 弹窗滚动到顶部
      let myDialog = document.querySelector("#myDialog .el-dialog__body");
      myDialog.scrollTop = 0;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      this.exportLoading = true;
      // 查找数据
      exportExcelReq({ excelType: 26, title: '库包信息表' }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "库包信息表" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      }).finally(() => {
        this.exportLoading = false;
      });
    }
  }
};
</script>

