<template>
  <div class="app-container" id="rfid">
    <div class="filter-container">
       <el-input v-model="rfid" placeholder="标签号筛选" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-select v-model="rfidType" class="filter-item" placeholder="按标签类型筛选" multiple filterable collapse-tags>
        <el-option v-for="item in rfidtypeOptions" :key="item.id" :label="item.name" :value="item.id" />
      </el-select>
       <el-select v-model="rfidStatus" class="filter-item" placeholder="按标签状态筛选" multiple filterable collapse-tags>
        <el-option v-for="item in rfidStatusOptions" :key="item.id" :label="item.name" :value="item.id" />
      </el-select>
      <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
      <el-button class="filter-item" type="primary" @click="handleCreate" icon="el-icon-circle-plus-outline">添加</el-button>
      <el-button class="filter-item" type="primary" @click="handleReg" v-if="power==1">注册元数据</el-button>

      <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu"  :loading="exportLoading">导出</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="uploadVisible=true" icon="iconfont icondaoru">导入</el-button>

    </div>

    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="标签号" min-width="180px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.rfid }}</span>
          </template>
        </el-table-column>
        <el-table-column label="标签类型" min-width="180px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatType(row.rfidType) }}</span>
          </template>
        </el-table-column>

        <el-table-column label="标签状态" min-width="180px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.status==0?'初始化':(row.status==1?'正常':'废弃')}}</span>
          </template>
        </el-table-column>
        <el-table-column label="关联名称" min-width="180px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.linkName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center" min-width="300" fixed="right" class-name="small-padding fixed-width">
          <template slot-scope="{row}">

            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline">编辑</el-button>

          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" :page-sizes="[5,10,20]" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog id="myDialog" @close="scrollTop" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center class="addOrEditBox" :class="{'editClass':true}">
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <el-form-item label="标签" prop="rfid" v-if="dialogStatus=='create'">
          <el-input v-model="temp.rfid" />
        </el-form-item>
        <el-form-item label="标签状态" prop="status" v-if="dialogStatus=='update'">
          <el-select v-model="temp.status" class="filter-item" placeholder="请选择">
            <el-option v-for="item in statusOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>

    <el-dialog id="myDialog" @close="scrollTop" title="注册元数据" :visible.sync="regDialogFormVisible" center :class="{'editClass':true}">
      <el-form ref="regForm" :rules="regRules" :model="regTemp" label-position="left" class="formStyle">
        <el-form-item label="标签开头" prop="startRule">
          <el-input v-model="regTemp.startRule" />
        </el-form-item>
        <el-form-item label="标签类型" prop="rfidType">
          <el-select v-model="regTemp.rfidType" class="filter-item" placeholder="请选择">
            <el-option v-for="item in rfidtypeOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="标签状态" prop="status">
          <el-select v-model="regTemp.status" class="filter-item" placeholder="请选择">
            <el-option v-for="item in statusOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="regMetaData">确定</el-button>
      </div>
    </el-dialog>

    <my-upload :visible.sync="uploadVisible" :uploadData="{type:8}" @dataUpdated="reqSuccess"></my-upload>

  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import myUpload from "@/components/myUpload"; // secondary package based on el-pagination

//车辆列表，添加车辆，编辑，删除，搜索，批量添加车辆
import {

  bindDriver,
  bindTeam,
} from "../../api/managementArchives/vehicleArchives";
import {
  selectBank
} from "../../api/managementArchives/bank";



import {
  teamsSelect,
  personsSelect,
  exportExcelReq
} from "../../api/managementArchives/common";


import { updateR, metaData, rfidReg, queryRpage } from "../../api/inOut/rfid";


//自定义设备号头验证规则
const reasonableLen = (rule, value, callback) => {
  const reasonableLength = /[0-9]{4}/;
  if (!reasonableLength.test(value)) {
    callback(new Error("长度为4"));
  } else {
    callback();
  }
};
//未选择
const unSelect = (rule, value, callback) => {
  if (value == -1) {
    callback(new Error("请选择"));
  } else {
    callback();
  }
};
let myLoading = undefined; // 弹窗服务调用
export default {
  name: "rfid",
  components: {
    Pagination, myUpload
  },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //车辆列表
      total: 0, //车辆总数
      listLoading: true,
      indexMethod: 1, // 序号
      listQuery: {
        page: 1,
        limit: 10,
      },


      largeTeamOptions: [], //大队下拉数据
      channelNoOptions: [], //通道个数下拉数据
      //添加或编辑数据
      temp: {
        status: 1
      },
      dialogFormVisible: false, //添加或编辑窗口显隐
      dialogStatus: "", //控制是编辑窗口还是添加窗口
      textMap: {
        update: "编辑",
        create: "添加"
      },
      //添加或编辑表单校验
      rules: {

        rfid: [
          {
            required: true,
            message: "请填写标签号",
            trigger: "blur"
          }
        ],

        status: [
          {
            message: "请选择标签状态",
            trigger: "change",
            required: true
          }
        ]
      },



      bindTypeOptions: [
        { id: -1, name: "请选择" },
        { id: 1, name: "司机" },
        { id: 2, name: "大队" }

      ], //绑定类型下拉数据
      bindTemp: {

        type: 1
      },

      BindDialogFormVisible: false,
      bindDialogStatus: "",//控制是绑定还是解绑窗口
      bindMap: {
        bind: "绑定",
        unbind: "解绑"
      },
      bindRules: {
        type: [
          { required: true, message: "请选择绑定类型", trigger: "change", validator: unSelect }
        ],
        driverId: [
          { required: true, message: "请选择绑定司机", trigger: "change", validator: unSelect }
        ],
        teamId: [
          { required: true, message: "请选择绑定大队", trigger: "change", validator: unSelect }
        ]
      },
      rfidOptions: [{ id: 1, name: 1 }],
      typeOptions: [{ id: -1, name: '请选择' }, { id: 1, name: '归属银行' }, { id: 2, name: '未定义' }],
      statusOptions: [{ id: 0, name: '初始化' }, { id: 1, name: '正常' }, { id: 2, name: '废弃' }],
      bankOptions: [{ id: -1, name: '请选择' }],
      rfidtypeOptions: [
        { id: -1, name: '未定义' }, { id: 1, name: '司机' }, { id: 2, name: '业务车组长' }, { id: 3, name: '押运员' },
        { id: 4, name: '业务员' }, { id: 5, name: '大队长' }, { id: 8, name: '库管' }, { id: 7, name: '车管' },
        { id: 6, name: '枪管' }, { id: 9, name: '车辆' }, { id: 10, name: '工作人员' }, { id: 80, name: '枪支' }, { id: 90, name: '库包' }, { id: 99, name: '银行人员' }
      ],
      regTemp: {},
      regRules: {
        startRule: [
          { required: true, message: "请填写标签开头", trigger: "blur", validator: unSelect }
        ],
        rfidType: [
          { required: true, message: "请选择标签类型", trigger: "change" }
        ]
      },
      regDialogFormVisible: false,
      wsRfid: null,
      rfid: null,
      uploadVisible: false,
      rfidType: [],
      rfid:'',
      rfidStatusOptions: [{ id: 0, name: '初始化' }, { id: 1, name: '正常' }],
      rfidStatus:[0,1],
      exportLoading: false,


    };
  },
  created() {
    this.getList(); //初始化获取车辆列表
    this.getSelect(); //初始化弹窗的下拉框
    this.getChannelNoOptions(); //初始化通道下拉数据
    this.webSocketConnect();
  },
  mounted() { },
  beforeDestroy() {
    if (this.wsRfid) {
      this.wsRfid.close();
    }
  },
  methods: {
    handleFilter(e) {// 查询
      this.listQuery.page = 1;
      this.getList();
    },
    reqSuccess: function (val) {  //val用于接收子组件传过来的值
      if (val == true) {
        this.getList();
      }
    },
    webSocketConnect() {
      let protojs = require("../../proto.js"), that = this;
      this.wsRfid = new WebSocket("ws://127.0.0.1:22345");

      this.wsRfid.binaryType = "arraybuffer";
      switch (this.wsRfid.readyState) {
        case WebSocket.CONNECTING:
          console.log("正在连接");
          break;
        case WebSocket.OPEN:
          console.log("连接成功");
          break;
        case WebSocket.CLOSING:
          console.log("正在关闭");
          break;

        case WebSocket.CLOSED:
          console.log("已经关闭，或者打开连接失败");
          break;
        default:
          // this never happens
          break;
      }

      this.wsRfid.onerror = function (params) {
        console.log('error')
      };
      this.wsRfid.onclose = function () {
        console.log('close')

      };
      this.wsRfid.onopen = function () {

      };
      this.wsRfid.onmessage = function (event) {
        if (event.data instanceof ArrayBuffer) {
          let arraybuffer = event.data;
          let dataView = new DataView(arraybuffer.slice(0, 8));
          let int32_length = dataView.getInt32(0);
          // let int32_cmd = dataView.getInt32(4);
          let locationInfo = null;
          let source = new Uint8Array(arraybuffer.slice(8, int32_length + 4));
          locationInfo = protojs.lookupType(
            "com.beidou.proto.local.RealTimeRfidInfoResponse"
          )

          let message = locationInfo.decode(source);

          if (that.dialogFormVisible == true && that.dialogStatus == 'create') {
            that.dealWebSocketData(message.rfid);
          }
        }


      };
    },
    dealWebSocketData(rfid) {

      if (this.rfid == rfid) {
        return; //如果是重复的rfid不作任何处理
      };
      this.$set(this.temp, 'rfid', rfid)


      this.rfid = rfid;



    },
    handleReg() {
      this.regDialogFormVisible = true;
      this.regTemp = {};
      this.$nextTick(() => {
        this.$refs["regForm"].clearValidate();
      });
    },
    regMetaData() {
      this.$refs["regForm"].validate(valid => {
        if (valid) {
          this.regTemp.typeName = this.findItem(this.rfidtypeOptions, 'id', this.regTemp.rfidType).name
          metaData(this.regTemp)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.getSelect();

                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });

        }
      })
    },

    formatType(type) {
      let text = ''
      switch (type) {
        case -1:
          text = '未定义';
          break;
        case 1:
          text = '司机';
          break;
        case 2:
          text = '业务车组长';
          break;
        case 3:
          text = '押运员';
          break;
        case 4:
          text = '业务员';
          break;
        case 5:
          text = '大队长';
          break;
        case 8:
          text = '库管';
          break;
        case 7:
          text = '车管';
          break;
        case 6:
          text = '枪管';
          break;
        case 9:
          text = '车辆';
          break;
        case 10:
          text = '工作人员';
          break;
        case 80:
          text = '枪支';
          break;
        case 90:
          text = '库包';
          break;
        case 99:
          text = '银行人员';
          break;
        default:
          text = '无匹配'
      }
      return text;
    },
    findItem(locations, key, val) {
      let obj = {};
      obj = locations.find((item) => {
        return item[key] === val;
      });
      if (obj == undefined) {
        obj = {};
      }
      return obj;
    },
    handleBind(row) {
      this.bindTypeOptions = [
        { id: -1, name: "请选择" },
        { id: 1, name: "司机" },
        { id: 2, name: "大队" }

      ];
      this.bindDialogStatus = "bind";
      this.bindTemp = {
        type: 1,
        carId: row.id,
        driverId: row.driverId,
        teamId: row.teamId
      }
      this.BindDialogFormVisible = true;
    },

    bindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData, request;
          if (this.bindTemp.type == 1) {
            subData = {
              carId: this.bindTemp.carId,
              driverId: this.bindTemp.driverId,
            }
            request = bindDriver;

          } else if (this.bindTemp.type == 2) {
            subData = {
              carId: this.bindTemp.carId,
              teamId: this.bindTemp.teamId,
            }
            request = bindTeam;
          }
          request(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.getSelect();

                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });

        }
      })
    },

    //车辆列表
    getList() {
      let _t = this, subData;
      _t.listLoading = true;
      subData = {
        page: this.listQuery.page,
        limit: this.listQuery.limit,
      }
      if (this.rfidType.length) {
        subData.rfidType = this.rfidType;
      }
       if (this.rfidStatus.length) {
        subData.status = this.rfidStatus;
      }else{
        subData.status =[0,1];
      }
      if (this.rfid) {
        subData.rfid = this.rfid.toString().replace(/-/g, "");
      }

      queryRpage(subData)
        .then(res => {
          if (res.status == 0) {
            this.listLoading = false;
            this.list = res.data;

            this.total = res.count;
            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },

    //初始化通道数据
    getChannelNoOptions() {
      this.channelNoOptions = [];
      for (let i = 0; i < 17; i++) this.channelNoOptions.push({ channelNo: i });
    },




    //重置添加或编辑数据
    resetTemp() {
      this.temp = {
        type: 1
      }
    },

    //点击添加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },

    //添加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });



          let rfid = this.temp.rfid.toString().replace(/-/g, "");
          rfidReg({ rfid: rfid })
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.data);
                // this.$message.success(res.msg);

                this.dialogFormVisible = false;
                //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
                // this.$store.commit("realTimeMonitor/ISEDITFILES", true);
              } else {
                this.$message.error(res.data);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },


    //点击编辑
    handleUpdate(row) {
      this.temp = {
        id: row.id,
        type: row.type
      }
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },

    //编辑
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });


          let subData = { ...this.temp };
          updateR(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("编辑成功");
                this.dialogFormVisible = false;

              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },


    //初始化下拉框
    getSelect() {
      var that = this;
      teamsSelect({ status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            this.largeTeamOptions = [];
            this.largeTeamOptions.unshift({ id: -1, teamName: "请选择" });
            return;
          }
          that.largeTeamOptions = res.data;
          that.largeTeamOptions.unshift({ id: -1, teamName: "请选择" });
        } else {
          if (res.data == null) {
            this.largeTeamOptions = [];
            this.largeTeamOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
      personsSelect({ work: 1, status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.driverOptions = res.data;
          that.driverOptions.unshift({ id: -1, name: "请选择" });
        } else {
          if (res.data == null) {
            this.driverOptions = [];
            this.driverOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });

      selectBank().then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            that.bankOptions = [{ id: -1, name: "请选择" }];
            return;
          }
          that.bankOptions = res.data;
          that.bankOptions.unshift({ id: -1, name: "请选择" });
        } else {
          if (res.data == null) {
            this.bankOptions = [];
          }
          this.$message.error(res.msg);
        }
      });

    },

    scrollTop() {
      // 弹窗滚动到顶部
      let myDialog = document.querySelector("#myDialog .el-dialog__body");
      myDialog.scrollTop = 0;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      this.exportLoading = true;
      // 查找数据
      exportExcelReq({ excelType: 25, title: '标签信息表' }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "标签信息表" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      }).finally(() => {
        this.exportLoading = false;
      });
    }
  }
};
</script>

<style lang="scss">
#rfid {
  .el-button--primary.is-disabled {
    color: #fff;
    background-color: #a0cfff;
    border-color: #a0cfff;
  }
  .el-dialog {
    .el-input__inner::placeholder {
      font-size: 12px;
    }
  }
  .el-form-item__label {
    width: 86px;
  }

  .addAllForm {
    .el-form-item__label {
      width: 132px;
    }
    .el-form-item__content {
      margin-left: 132px;
    }
    .el-form-item__content {
      width: 293px;
     
    }
  }
  .editClass {
    .el-dialog {
      //   margin-top: 4vh !important;
    }
    // .el-dialog__body {
    //   padding-left: 0;
    //   padding-bottom: 0;
    // }
    .el-form-item__label {
      width: 132px;
      // padding: 0 10px 0 0;
    }
    .el-form-item__content {
      margin-left: 132px;
    }
    &.addOrEditBox{
      .el-form-item__content {
      margin-left: 132px;
       .el-input{
        width:400px;
      }
    }
