<template>
  <div id="commonProblem">
    <el-card class="box-card cPro">
      <div slot="header" class="clearfix">
        <span>常见问题</span>
      </div>
      <div class="text item l-question">
        <!-- 问题列表 -->
        <el-table class="my-table" :data="tableData" v-loading="listLoading" style="width: 100%">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="答：" v-if="props.row.finish">
                  <span>{{ props.row.answer }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column label="序号" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
          <el-table-column label="问题" prop="question"></el-table-column>
          <el-table-column label="是否已回答" width="180" align="center" v-if="power==1">
            <template slot-scope="props">
              <span :class="props.row.finish==0?'nore':'fied'">{{ props.row.finish==0?'未回答':'已回答' }}</span>
              <el-button v-if="power==1" size="mini" round type="primary" class="answerBtn" @click="anQ(props.row)">回答</el-button>
            </template>
          </el-table-column>
        </el-table>
        <pagination v-show="pages.total>0" :total="pages.total" :page.sync="pages.page" :limit.sync="pages.size" @pagination="getQ" layout="prev, pager, next, sizes, total, jumper" />
        <!-- 提问按钮 -->
        <el-button type="primary" class="l-ask" @click="openDia" v-if="power==1">创建问题</el-button>
      </div>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>售后服务</span>
      </div>
      <div class="text item">深圳北斗通信科技有限公司</div>
      <div class="text item">地址：深圳市南山区科苑南路南京大学产学研基地 B 栋 7 楼</div>
      <div class="text item">售后服务：曾昭凡</div>
      <div class="text item">联系方式：john@beidoucom.com</div>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>技术支持</span>
      </div>
      <div class="text item">深圳北斗通信科技有限公司</div>
      <div class="text item">地址：深圳市南山区科苑南路南京大学产学研基地 B 栋 7 楼</div>
      <div class="text item">售后服务：吕建峰</div>
      <div class="text item">联系方式：jeff.lv@beidoucom.com</div>
    </el-card>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>文件下载</span>
      </div>

      <a class="text item" href="download/BDA09操作手册.pdf" download="BDA09操作手册">操作手册</a>

      <a class="text item" href="download/beidou install setup.exe" download="beidou install setupX64.exe">专用读卡软件</a>
      <a class="text item" href="download/人员信息模板.xls" download="人员信息模板">人员信息模板</a>
      <a class="text item" href="download/车辆信息模板.xls" download="车辆信息模板">车辆信息模板</a>
      <a class="text item" href="download/枪支信息模板.xls" download="枪支信息模板">枪支信息模板</a>
      <a class="text item" href="download/库包信息模板.xls" download="库包信息模板">库包信息模板</a>
      <a class="text item" href="download/银行人员信息模板.xls" download="银行人员信息模板">银行人员信息模板</a>
      <a class="text item" href="download/银行信息模板.xls" download="银行信息模板">银行信息模板</a>
      <a class="text item" href="download/网点信息模板.xls" download="网点信息模板">网点信息模板</a>
      <a class="text item" href="download/标签信息模板.xls" download="标签信息模板">标签信息模板</a>
      <a class="text item" href="download/每日排班模板.xls" download="每日排班模板">每日排班模板</a>

    </el-card>
    <!-- 创建问题弹窗 -->
    <el-dialog id="myDialog" title="创建问题" :visible.sync="qusDia" center>
      <el-form ref="dataForm" :rules="rules" :model="Cqus" label-position="left">
        <el-form-item label="问题" prop="cont" class="l-textarea">
          <el-input type="textarea" :rows="14" placeholder="请输入问题或意见" v-model="Cqus.cont" maxlength="200" show-word-limit> </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createQ">确定</el-button>
      </div>
    </el-dialog>
    <!-- 回答问题弹窗 -->
    <el-dialog id="myDialog1" title="回答问题" :visible.sync="anDia" center>
      <el-form ref="anForm" :rules="anRules" :model="Anqus" label-position="left">
        <el-form-item label="问题" prop="ques" class="l-textarea">{{Anqus.ques}}</el-form-item>
        <el-form-item label="回答" prop="cont" class="l-textarea">
          <el-input type="textarea" :rows="14" placeholder="请输入回答内容" v-model="Anqus.cont" maxlength="400" show-word-limit> </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="answerQ(Anqus.id)">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Pagination from "../../components/Pagination";
import { queList, queAdd, answer } from "../../api/managementArchives/gunFiles";
export default {
  name: "commonProblem",

  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      qusDia: false,// 是否显示创建问题弹窗
      Cqus: {// 创建问题
        cont: ''
      },
      rules: {// 创建问题验证规则
        cont: [{ required: true, message: "请填写内容" }]
      },
      listLoading: false,// 请求列表时的loading
      tableData: [],
      pages: {
        page: 1,
        size: 10,
        total: 0
      },
      indexMethod: 1, // 序号
      anDia: false,// 是否显示回答问题弹窗
      Anqus: {// 回答问题
        id: undefined,
        ques: 'xx',
        cont: ''
      },
      anRules: {// 回答问题验证规则
        cont: [{ required: true, message: "请填写内容" }]
      },
    }
  },
  mounted() {
    // 初始化获取列表
    this.getQ();
  },
  methods: {
    getQ() {// 获取问题列表
      let _t = this, obj = { size: _t.pages.size, page: _t.pages.page };
      _t.listLoading = true;
      _t.tableData = [];
      if (_t.power != 1) {
        obj.finish = 1;
      };
      queList(obj).then(res => {
        // console.log(res)
        if (res.success) {
          _t.tableData = res.data.records;// 表格数据
          _t.pages.total = res.data.total;// 数据长度
          _t.indexMethod = (_t.pages.page - 1) * _t.pages.size + 1;
        };
      }).finally(() => {
        _t.listLoading = false;
      });
    },
    searchQ() {// 搜索问题
    },
    createQ() {// 创建问题
      let _t = this;
      _t.$refs['dataForm'].validate((valid) => {
        if (valid) {
          queAdd({ question: _t.Cqus.cont }).then(res => {
            if (res.success) {
              _t.$message.success('提问成功!');
              _t.qusDia = false;// 关闭弹窗
              _t.pages.page = 1;// 回到第一页
              _t.getQ();// 重新获取列表
            } else {
              _t.$message.error('提交失败，请重新提交！');
            };
          }).catch(err => {
            _t.$message.error('提交失败，请重新提交！')
          });
        }
      });
    },
    openDia() {// 打开创建问题弹窗
      let _t = this;
      _t.qusDia = true;// 打开弹窗
      _t.$refs.dataForm && _t.$refs.dataForm.resetFields();// 清空弹窗内容
    },
    anQ(row) {// 打开回答弹窗
      let _t = this;
      _t.anDia = true;// 打开弹窗
      _t.$refs.anForm && _t.$refs.anForm.resetFields();// 清空弹窗内容
      // 赋值操作，将问题答案放入弹窗中
      _t.Anqus.id = row.id;
      _t.Anqus.ques = row.question;
      _t.Anqus.cont = row.answer;
    },
    answerQ(idpost) {// 回答问题
      let _t = this;
      _t.$refs['anForm'].validate((valid) => {
        if (valid) {
          answer({ id: idpost, answer: _t.Anqus.cont }).then(res => {
            if (res.success) {
              _t.$message.success('回答成功！');
              _t.anDia = false;// 关闭弹窗
              _t.getQ();// 重新获取列表
            } else {
              _t.$message.error('提交失败，请重新提交！');
            };
          }).catch(err => {
            _t.$message.error('提交失败，请重新提交！')
          });
        }
      });
    }
  }
}
</script>
<style lang="scss" scoped>
.text {
  font-size: 14px;
}
.item {
  margin-bottom: 16px;
  margin-top: 16px;
}
a.item {
  color: #4653c9;
  padding: 0 10px 10px 0px;
  min-width: 192px;
  display: inline-block;
  margin: 0;
  &:hover {
    text-decoration: underline;
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  flex: 1;
  margin: 20px;
}
</style>
<style lang="scss">
$headerBarH: 120px;
#commonProblem {
  height: calc(100vh - #{$headerBarH}) !important;
  // padding: 20px;
  display: flex;
  flex-wrap: wrap;
  .cPro {
    // 常见问题
    flex: 1 1 100%;
    .el-card__body {
      // 常见问题body
      // min-height: 380px;
      overflow: hidden;
    }
    // 问题
    .l-question {
      height: 100%;
      display: flex;
      flex-direction: column;
      .el-form-item {
        // 搜索问题
        margin-bottom: 0;
      }
      .l-content {
        // 问题列表
        flex: 1 1 100%;
        overflow-y: auto;
        background-color: #f9f7f7;
        list-style: none;
        padding: 10px;
        margin: 10px 0;
        li {
          // 每个列表的样式
          margin-bottom: 20px;
          h5 {
            margin: 5px 0;
            font-size: 14px;
          }
          .cont {
            font-size: 13px;
          }
        }
      }
      .l-ask {
        // 创建问题
        margin-right: auto;
      }
    }
  }
  // 问题弹窗
  .el-dialog {
    .l-textarea {
      .el-form-item__content {
        height: auto;
        textarea {
          font: 400 13.3333px Arial;
          resize: none;
        }
      }
    }
  }
  // 问题列表
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 80%;
  }
  .expanded + tr td,
  .expanded + tr {
    background-color: #f3f3f3;
  }
  // 表格
  .my-table {
    height: 208px;
    span.nore {
      // 未回答
      color: #f00;
    }
    span.fied {
      // 已回答
      color: #05c76d;
    }
    .answerBtn {
      // 回答按钮
      margin-left: 8px;
    }
  }
}
</style>