            <template slot-scope="scope">
              <el-select filterable v-model="scope.row.carId" class="filter-item" :class="{removeClass:judgeRemoveIn(carOptions,scope.row.carId)}" :disabled="!showPublish||scope.row.statusDisabled" placeholder="请选择" @change="selectChange(scope.row,'car')">
                <el-option v-for="item in carOptions" :key="item.id" :label="item.licenseNo" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="车组长" min-width="110px" align="center">
            <template slot-scope="scope">
