            <template slot-scope="scope">
              <span :class="{red:scope.row[scope.row.id+'-'+scope.row.driverId]==true}">{{ scope.row.driverName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="押运员1" min-width="60px" align="center" prop="safetyName">
            <template slot-scope="scope">
              <span :class="{red:scope.row[scope.row.id+'-'+scope.row.safetyId]==true}">{{ scope.row.safetyName }}</span>
