          <el-table-column label="押运员2" min-width="60px" align="center" prop="additionSafetyName">
            <template slot-scope="scope">
              <span :class="{red:scope.row[scope.row.id+'-'+scope.row.additionSafetyId]==true}">{{ scope.row.additionSafetyName }}</span>
            </template>
