          </el-select>
        </el-form-item>
        <el-form-item label="编辑排班类型" prop="types">
          <el-select filterable v-model="editTemp.types" @change="typesSelectChange" filterable multiple class="filter-item" placeholder="Please select">
            <el-option v-for="item in typeOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
