          <el-select filterable v-model.number="editTemp.leaderId" class="filter-item" placeholder="Please select" @change="selectChange(editTemp,'group')" :class="{repeatColor:editTempRepeatPersons.indexOf(editTemp.leaderId)!==-1,removeClass:judgeRemoveIn(teamLeaderOptions,editTemp.leaderId)}">
            <el-option v-for="item in teamLeaderOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
          </el-select>
        </el-form-item>
        <el-form-item label="司机">
          <div class="editItem" :class="{red:editTempRepeatPersons.indexOf(editTemp.driverId)!==-1}">{{editTemp.driverName}}</div>
        </el-form-item>
        <el-form-item label="押运员1">
