        </el-form-item>
        <el-form-item label="押运员2">
          <div class="editItem" :class="{red:editTempRepeatPersons.indexOf(editTemp.additionSafetyId)!==-1}">{{editTemp.additionSafetyName}}</div>
        </el-form-item>
        <el-form-item label="业务员1">
          <div class="editItem" :class="{red:editTempRepeatPersons.indexOf(editTemp.businessId)!==-1}">{{editTemp.businessName}}</div>
        </el-form-item>
        <el-form-item label="业务员2">
          <div class="editItem" :class="{red:editTempRepeatPersons.indexOf(editTemp.addBusinessId)!==-1}">{{editTemp.addBusinessName}}</div>
        </el-form-item>

