  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { generate, queryPlan, queryExp, publishplan, addPlanSchedule, updateplan, manualPlanScheduling } from "../../../api/schedule/schedule";
import { carGroupAlllist } from "../../../api/managementArchives/unitManagement";
import { formatDate } from "../../../../src/common"; //时间格式化
import { personsSelect, carsSelect, mapSelect, exportExcelReq } from "../../../api/managementArchives/common"; //大队下拉框数据，人员下拉框数据，车辆下拉框数据
import { addZero, renderTime } from "../../../../src/common"; //转换位数字

let myLoading = undefined; // 弹窗服务调用

//未选择校验
const unSelect = (rule, value, callback) => {
  if (value == -1) {
    callback(new Error("请选择"));
  } else {
    callback();
  }
};
//未选择编辑日期校验
const modifyDatesVerify = (rule, value, callback) => {
  if (!value) {
    callback(new Error("请选择编辑日期"));
  } else {
    if (!value.length) {
      callback(new Error("请选择编辑日期"));
      return;
    }
    callback();
  }
};

const modifyTypesVerify = (rule, value, callback) => {
  if (!value) {
    callback(new Error("请选择编辑的排班类型"));
  } else {
    if (!value.length) {
      callback(new Error("请选择编辑的排班类型期"));
      return;
    }
    callback();
  }
};
export default {
  name: "schedualChild",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      tableForm: { list: [] },
      total: 0,
      indexMethod: 1, // 序号
      listLoading: true,
      listQuery: {
        selectDate: new Date()
