      rules: {
        teamName: [
          { required: true, message: "请填写大队名称", trigger: "blur" }
        ],
        status: [
          { required: true, message: "请选择大队状态", trigger: "change" }
        ]
      },
      //添加或编辑表单校验
      listRules: {
        routeId: [
          { required: true, message: "请选择排班信息", trigger: "change", validator: unSelect }
        ],
        type: [
          { required: true, message: "请选择排班信息", trigger: "change", validator: unSelect }
        ],
        carId: [
          { required: true, message: "请选择排班信息", trigger: "change", validator: unSelect }
        ],
        leaderId: [
