          { required: true, message: "请选择排班信息", trigger: "blur" }
        ],
        businessName: [
          { required: true, message: "请选择排班信息", trigger: "blur" }
        ],
        safetyName: [
          { required: true, message: "请选择排班信息", trigger: "blur" }
        ],



