            required: true,
            message: "请填写修改原因",
            trigger: "blur"
          }
        ]
      },

      addRules: {
        addDate: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        routeId: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        carId: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        leaderId: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        type: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        status: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ]
      },
      editVisible: false,
      editTemp: {
        modifyDates: [],
        types: [],
      },
      editRules: {


        carId: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        leaderId: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        modifyDates: [
          { required: true, message: "请选择编辑日期", trigger: "change", validator: modifyDatesVerify }
        ],
        types: [
          { required: true, message: "请选择编辑的排班类型", trigger: "change", validator: modifyTypesVerify }
        ]
        // safetyId: [
        //   {
        //     required: true,
        //     message: "请选择",
        //     trigger: "change"
        //   }
        // ],
        // additionSafetyId: [
        //   {
        //     required: true,
        //     message: "请选择",
        //     trigger: "change"
        //   }
        // ],
        // businessId: [
        //   {
        //     required: true,
        //     message: "请选择",
        //     trigger: "change"
        //   }
        // ],
        // addBusinessId: [
        //   {
        //     required: true,
        //     message: "请选择",
        //     trigger: "change"
        //   }
        // ],
        // driverId: [
        //   {
        //     required: true,
        //     message: "请选择",
        //     trigger: "change"
        //   }
        // ],
      },
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }],
        disabledDate: (time) => {
          return time.getTime() > new Date().getTime()
        },
      },
      pickerOptions1: {
        // shortcuts: [{
        //   text: '最近一周',
        //   onClick(picker) {
        //     const end = new Date();
        //     const start = new Date();
        //     start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
