        //     const end = new Date();
        //     const start = new Date();
        //     start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
        //     picker.$emit('pick', [start, end]);
        //   }
        // }, {
        //   text: '最近三个月',
        //   onClick(picker) {
        //     const end = new Date();
        //     const start = new Date();
        //     start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
        //     picker.$emit('pick', [start, end]);
        //   }
        // }],
        disabledDate: (time) => {
          return time.getTime() < this.startTimeTimestamps || time.getTime() > this.startTimeTimestamps + 6 * 3600 * 24 * 1000
        },
        // disabledDate: (time) => {
        //   return time.getTime() < (new Date(this.getFirstDayOfWeek(new Date(new Date().getTime())).replace(/_/g, '/')).getTime())
        // },
      },
      modifyTemp: { modifyReason: '' },
      modifyVisible: false,
      modifyData: {},
      timer: null,
      addVisible: false,
      addTemp: { addDate: this.formatDateTime(new Date()) },
      showGenerate: false,//是否显示生成
      showPublish: false,
      dateArr: [],//日期筛选
      filteredValue: [],//默认的筛选条件
      typeArr: [{ text: '早送', value: 2 }, { text: '晚收', value: 1 }],
      filteredTypeValue: [],
      fullscreenLoading: null,
      addVisible1: false,
      addTemp1: {},
      addRules1: {},
      getSelectPromises: [],
      dateDisabled: false,//在今天以前的日期不允许添加等操作
      hasNormalSchedule: false,
      addRules1: {
        dates: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
