    },
    //判断日期是否是以前的周
    isSameWeek(old, now) {
      let oneDayTime = 1000 * 60 * 60 * 24;
      let old_count = parseInt(old.getTime() / oneDayTime);
      let now_other = parseInt(now.getTime() / oneDayTime);
      if (parseInt((old_count + 4) / 7) < parseInt((now_other + 4) / 7)) {

        return true;
      }

    },
    checkDate() {
      let now = new Date();
      let old = this.listQuery.selectDate;
      if (this.isSameWeek(old, now)) {
        this.dateDisabled = true;
      } else {
        this.dateDisabled = false;

      }
    },
    changegToGetList(val) {
      this.checkDate()
      this.getList();
    },
