      // });
    },
    //生成预排班
    generateSchdule() {
      generate({
        startTime: new Date(this.getFirstDayOfWeek(this.listQuery.selectDate).replace(/_/g, '/')).getTime(),
        days: 7,
        range: 7
      })
        .then(res => {
          if (res.status == 0) {

            this.queryGenerate(res.data);
          } else {
            this.$message.error(res.msg);
          }
        }).catch(err => {
          this.fullscreenLoading.close()
        })
    },
    publishSchdule() {
      publishplan({
        dates: this.formatDates(this.listQuery.selectDate)
      }).then(res => {
        if (res.status == 0) {

          this.queryGenerate(res.data);
        } else {
          this.$message.error(res.msg);
        }
      }).catch(err => {
        this.fullscreenLoading.close()
      })
    },
    queryGenerate(resMsg) {
      let that = this;
      queryExp({
        uuid: resMsg
      })
        .then(res => {
          if (res.status == 0) {
            //生成预排班是异步的，需要轮询查询是否生成成功
            let msg;
            if (res.data.success == 0) {
              if (this.timer) {
                clearInterval(this.timer);
              }
              this.timer = setInterval(function () {
                that.queryGenerate(resMsg);
              }, 10000)
            } else if (res.data.success == 1) {
              msg = res.data.msg;
              this.getList(true);
              if (this.timer) {
                clearInterval(this.timer);
              }
              this.$message.success('操作成功');
              this.fullscreenLoading.close()

            } else if (res.data.success == 2) {
              msg = res.data.msg;
              if (this.timer) {
                clearInterval(this.timer);
              }
              this.$message.error('操作失败');
              this.fullscreenLoading.close()

            }

          } else {
            this.$message.error(res.msg);
          }
        }).catch(err => {
          if (this.timer) {
            clearInterval(this.timer);
          }
          this.fullscreenLoading.close()
        })

