    },
    findItem(locations, key, val) {
      let obj = {};
      obj = locations.find((item) => {
        return item[key] === val;
      });
      if (obj == undefined) {
        obj = {};
      }
      return obj;
    },
    addOrEdit(data, type) {
      let subData = { ...data }, that = this;

      //获取所有接口需要的数据

      //道路
      subData.routeName = this.findItem(this.geometryOptions, 'id', subData.routeId).name;

      //年月日
      if (type == 1) {
        let dates = subData.addDate.split('_');
        subData.planYear = dates[0];
        subData.planMonth = dates[1];
        subData.planDay = dates[2];
        subData.status = 3;//添加的状态都是1
      }
      //车组各种信息
      // let groupMsg = this.findItem(this.groupAlllist, 'leaderId', subData.leaderId);
      // subData.leaderName = groupMsg.leaderName;
      // subData.safetyId = groupMsg.safetyId;
      // subData.safetyName = groupMsg.safetyName;
      // subData.additionSafetyId = groupMsg.additionSafetyId;
      // subData.additionSafetyName = groupMsg.additionSafetyName;
      // subData.businessId = groupMsg.businessId;
      // subData.businessName = groupMsg.businessName;
      // subData.addBusinessId = groupMsg.addBusinessId;
      // subData.addBusinessName = groupMsg.addBusinessName;
      // subData.groupId = groupMsg.id;
      // subData.groupName = groupMsg.groupName;
      // subData.driverId = groupMsg.driverId;
      // subData.driverName = groupMsg.driverName;
      if (!subData.leaderName) {
        this.$message.warning('该车组长未绑定车组,请绑定后再选择该车组长');
        return;

      }

      //车辆司机信息
      let carMsg = this.findItem(this.carOptions, 'id', subData.carId);
      subData.carLicenseNo = carMsg.licenseNo;
      // subData.driverId = carMsg.driverId;
      // subData.driverName = carMsg.driverName;

      let addData = { ...subData };
      delete addData.addDate;
      let req;
      if (type == 1) {
        req = addPlanSchedule;
      } else if (type == 2) {
        req = updateplan;
        if (addData.status == 2 || addData.status == 4) {//删除功能
          delete addData.sortDate
          let addData1 = { ...addData }
          addData = [addData1]
        } else {
          addData = this.tableForm.list.filter((item, index) => {
            let flag = false;
            let commonTrueOrFalse = (subData.routeId == item.routeId) && (this.editTemp.types.indexOf(item.type) >= 0) && (subData.status == item.status && item.status % 2 != 0) && (this.editTemp.modifyDates.indexOf(item.sortDate) >= 0)
            if (item.idIdentify == this.editTemp.idIdentify) {
              flag = true;
            } else if ((item.type !== this.editTemp.type || item.sortDate !== this.editTemp.sortDate) && commonTrueOrFalse) {
              flag = true;
            }

            return flag;
          })
          // //根据选择的编辑的日期确定编辑哪几天的相同路线、排班类型和排班状态的数据
          // addData = this.tableForm.list.filter((item, index) => {
          //   return (subData.routeId == item.routeId) && (this.editTemp.types.indexOf(item.type) >= 0) && (subData.status == item.status) && (this.editTemp.modifyDates.indexOf(item.sortDate) >= 0)
          // })
          addData.forEach(element => {
            element.carId = subData.carId;
            element.carLicenseNo = subData.carLicenseNo;
            element.leaderId = subData.leaderId;
            element.leaderName = subData.leaderName;
            element.safetyId = subData.safetyId;
            element.safetyName = subData.safetyName;
            element.additionSafetyId = subData.additionSafetyId;
            element.additionSafetyName = subData.additionSafetyName;
            element.businessId = subData.businessId;
            element.businessName = subData.businessName;
            element.addBusinessId = subData.addBusinessId;
            element.addBusinessName = subData.addBusinessName;
            element.groupId = subData.groupId;
            element.groupName = subData.groupName;
            element.driverId = subData.driverId;
            element.driverName = subData.driverName;
            delete element.addDate;

          });

        }

      }

      myLoading = this.$loading({ target: "#mydialog .el-dialog" });


      function loadingClose() {

        myLoading.close()



      }

      req(addData)
        .then(res => {
          if (res.status == 0) {
            if (type == 1) {
              this.addVisible = false;
            }
            if (type == 2) {
              this.editVisible = false;
            }
            this.getList(true);
            this.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          loadingClose()
        });

    },

    /**
     * @description: 判断某个排班的人员是否与其他排班的同为早送或晚收的正常排班人员重复
     * @param {*} personId 需要判断是否重复的人员id
     * @param {*} idIdentify 需要判断人员所在的排班idIdentify，编辑功能idIdentify存在，添加功能idIdentify不存在
     * @param {*} filterIdIdentifys 编辑功能时可能同时存在多个排班需要判断，需要排除这多个排班，其他情况该参数默认为空，多个排班的idIdentify
     * @param {*} item1 正在编辑的那个排班
     * @return {*}
     */
    autoPersonFilter(personId, idIdentify, filterIdIdentifys = [], item1) {
      //找出除了需要编辑的排班的跟编辑排班同日期的其他排班中是否存在重复的排班人员
      //注意：不需要跟正在编辑的其他排班比较，只需要跟不编辑的排班比较 ，通过filterIdIdentifys.indexOf(item.idIdentify) == -1过滤
      // 人员重复必须满足排班类型一致，排班日期一致，排班没被删除，不属于编辑排班，人员重复
      let repeatSchedules = this.tableForm.list.filter((item, index) => {
        //  (item.idIdentify != idIdentify) &&
        return filterIdIdentifys.indexOf(item.idIdentify) == -1 &&
          ((item.type == item1.type && idIdentify)) &&
          (item.planYear == item1.planYear && item.planMonth == item1.planMonth && item.planDay == item1.planDay) &&
          (item.status % 2 != 0) &&
          (personId == item.addBusinessId || personId == item.additionSafetyId || personId == item.businessId || personId == item.driverId || personId == item.leaderId || personId == item.safetyId)
      });
      let flag = false;
      if (repeatSchedules.length) {
        flag = true;
      }
      return flag;
    },
    /**
   * @description: 查找出需要编辑的排班，然后一个个排班看是否存在与其他排班人员重复的情况
   * @return {*}
   */
    editFilter() {
      let filterData = [], filterIdIdentifys = [], hasRepeat = false;
      this.editTempRepeatPersons = [];
      //查找出要编辑的排班，由于编辑可以同时选择早送和晚收，因此编辑的可能是多个排班
      filterData = (this.tableForm.list).filter((item, index) => {
        let flag = false;
        let commonTrueOrFalse = (this.editTemp.routeId == item.routeId) &&
          (this.editTemp.types.indexOf(item.type) >= 0) &&
          (this.editTemp.status == item.status && item.status % 2 != 0) &&
          (this.editTemp.modifyDates.indexOf(item.sortDate) >= 0)
        if (item.idIdentify == this.editTemp.idIdentify) {
          flag = true;
        } else if ((item.type !== this.editTemp.type || item.sortDate !== this.editTemp.sortDate) && commonTrueOrFalse) {
          flag = true;
        }
        if (flag == true) {
          filterIdIdentifys.push(item.idIdentify)
        }
        return flag;
      })
      //对正在编辑的排班一个个查看是否存在重复人员
      for (let i = 0; i < filterData.length; i++) {
        let item = filterData[i];
        let filterPersonIds = [this.editTemp.leaderId, this.editTemp.driverId, this.editTemp.safetyId, this.editTemp.businessId, this.editTemp.addBusinessId, this.editTemp.additionSafetyId];
        //一个排班中所有人员筛选是否有与其他排班重复人员
        for (let j = 0; j < filterPersonIds.length; j++) {
          if (this.autoPersonFilter(filterPersonIds[j], item.idIdentify, filterIdIdentifys, item)) {
            hasRepeat = true;
            if (this.editTempRepeatPersons.indexOf(filterPersonIds[j]) == -1) {
              this.editTempRepeatPersons.push(filterPersonIds[j])
            }
          }
        }

      }
      if (hasRepeat == true) {
        this.$message.warning('与其他排班存在重复的排班人员');
      }


    },
    /**
 * @description: 判断某个排班的人员是否与其他排班的同为早送或晚收的正常排班人员重复
 * @param {*} personId 需要判断是否重复的人员id
 * @param {*} idIdentify 需要判断人员所在的排班idIdentify，编辑功能idIdentify存在，添加功能idIdentify不存在
 * @param {*} filterIdIdentifys 编辑功能时可能同时存在多个排班需要判断，需要排除这多个排班，其他情况该参数默认为空，多个排班的idIdentify
 * @param {*} item1 正在编辑的那个排班
 * @return {*}
 */
    autoCarFilter(carId, idIdentify, filterIdIdentifys = [], item1) {
      //找出除了需要编辑的排班的跟编辑排班同日期的其他排班中是否存在重复的排班人员
      //注意：不需要跟正在编辑的其他排班比较，只需要跟不编辑的排班比较 ，通过filterIdIdentifys.indexOf(item.idIdentify) == -1过滤
      // 人员重复必须满足排班类型一致，排班日期一致，排班没被删除，不属于编辑排班，人员重复
      let repeatSchedules = this.tableForm.list.filter((item, index) => {
        //  (item.idIdentify != idIdentify) &&
        return filterIdIdentifys.indexOf(item.idIdentify) == -1 &&
          ((item.type == item1.type && idIdentify)) &&
          (item.planYear == item1.planYear && item.planMonth == item1.planMonth && item.planDay == item1.planDay) &&
          (item.status % 2 != 0) &&
          carId == item.carId
      });
      let flag = false;
      if (repeatSchedules.length) {
        flag = true;
      }
      return flag;
    },
    /**
   * @description: 查找出需要编辑的排班，然后一个个排班看是否存在与其他排班人员重复的情况
   * @return {*}
   */
    editCarFilter() {
      let carId = this.editTemp.carId;
      this.editTempRepeatCar = null;
      if (!carId) {
        return;
      }
      let filterData = [], filterIdIdentifys = [], hasRepeat = false;
      //查找出要编辑的排班，由于编辑可以同时选择早送和晚收，因此编辑的可能是多个排班
      filterData = (this.tableForm.list).filter((item, index) => {
        let flag = false;
        let commonTrueOrFalse = (this.editTemp.routeId == item.routeId) &&
          (this.editTemp.types.indexOf(item.type) >= 0) &&
          (this.editTemp.status == item.status && item.status % 2 != 0) &&
          (this.editTemp.modifyDates.indexOf(item.sortDate) >= 0)
        if (item.idIdentify == this.editTemp.idIdentify) {
          flag = true;
        } else if ((item.type !== this.editTemp.type || item.sortDate !== this.editTemp.sortDate) && commonTrueOrFalse) {
          flag = true;
        }
        if (flag == true) {
          filterIdIdentifys.push(item.idIdentify)
        }
        return flag;
      })
      //对正在编辑的排班一个个查看是否存在重复人员
      for (let i = 0; i < filterData.length; i++) {
        let item = filterData[i];

        if (this.autoCarFilter(carId, item.idIdentify, filterIdIdentifys, item)) {
          hasRepeat = true;
          if (this.editTempRepeatCar !== carId) {
            this.editTempRepeatCar = carId;
          }
        }


      }
      if (hasRepeat == true) {
        this.$message.warning('与其他排班存在重复的排班车辆');
      }


    },
    //获取排班里面所有有重复的人员的id 
    getRepeatIds() {
      let that = this, list = this.tableForm.list;
      //判断每个排班

      list.forEach((item, index) => {
        if (item.status % 2 == 0) {
          return;
        }
        //判断排班里面的每个人员
        let teamPersonIds = Array.from(new Set([item.leaderId, item.driverId, item.safetyId, item.businessId, item.addBusinessId, item.additionSafetyId]));
        teamPersonIds.forEach((item1, index1) => {
          if (item1) {
            judgeRepeat(item1);
          }
        })

        function judgeRepeat(personId) {
          for (let i = 0; i < list.length; i++) {
            if (
              item.idIdentify != list[i].idIdentify &&
              item.type == list[i].type &&
              list[i].status % 2 !== 0 &&
              (item.planYear == list[i].planYear && item.planMonth == list[i].planMonth && item.planDay == list[i].planDay) &&
              (personId == list[i].addBusinessId || personId == list[i].additionSafetyId || personId == list[i].businessId || personId == list[i].driverId || personId == list[i].leaderId || personId == list[i].safetyId)
            ) {
              item[item.id + '-' + personId] = true;
            }

          }
        }
      })


    },
    //获取排班里面所有有重复的车辆的id 
    getCarRepeatIds() {
      let that = this, list = this.tableForm.list;
      //判断每个排班
      list.forEach((item, index) => {
        if (item.status % 2 == 0) {
          return;
        }
        //判断排班里面的车辆
        let carId = item.carId;

        judgeRepeat(carId);


        function judgeRepeat(carId) {
          for (let i = 0; i < list.length; i++) {
            if (
              item.idIdentify != list[i].idIdentify &&
              item.type == list[i].type &&
              list[i].status % 2 !== 0 &&
              (item.planYear == list[i].planYear && item.planMonth == list[i].planMonth && item.planDay == list[i].planDay) &&

              (carId == list[i].carId)
            ) {
              item[item.id + 'car' + carId] = true;
            }

          }
        }
      })


    },
    selectChange(row, type) {
      if (type == 'car') {
        this.editCarFilter()
        for (let i = 0; i < this.carOptions.length; i++) {
          if (this.carOptions[i].id == row.carId) {
            row.carLicenseNo = this.carOptions[i].licenseNo
          }
        }
      } else if (type == 'group') {
        for (let i = 0; i < this.teamLeaderOptions.length; i++) {
          if (this.teamLeaderOptions[i].id == row.leaderId) {
            row.leaderName = this.teamLeaderOptions[i].name
          }
        }
        this.leaderChange(row);
      }
    },
    getFirstDayOfWeek(date) {
      let day = date.getDay() || 7;
      date.setDate(date.getDate() - day + 1)

      return date.getFullYear() + '_' + addZero(date.getMonth() + 1) + '_' + addZero(date.getDate())
    },
    //获取预排班列表
    getList(dataUpdate) {
      let that = this;
      this.listLoading = true;
      this.showGenerate = false;
      this.showPublish = false;
      this.hasNormalSchedule = false;
      let p1 = new Promise((resolve, reject) => {
        let times = that.formatDates(that.listQuery.selectDate)
        that.dateArr = times.map((item, index) => {
          let obj = { text: formatDate(item), value: parseInt(item) }
          return obj
        })
        let defalutTime = [], defalutType = []
        queryPlan({
          times: times
        })
          .then(res => {
            if (res.status == 0) {

              that.tableForm.list = [];
              if (res.data) {

                for (let index in res.data) {
                  for (let i = 0; i < res.data[index].length; i++) {
                    let imteI = res.data[index][i]
                    res.data[index][i].sortDate = parseInt(index);
                    if (res.data[index][i].status == 2 || res.data[index][i].status == 4 || res.data[index][i].status == 6) {
                      res.data[index][i].statusDisabled = true;
                    } else {
                      that.hasNormalSchedule = true;

                    }
                    Promise.all(that.getSelectPromises).then(() => {
                      that.handleSelectOptions(that.carOptions, imteI.carId, imteI.carLicenseNo, 'id', 'licenseNo');
                      that.handleSelectOptions(that.teamLeaderOptions, imteI.leaderId, imteI.leaderName, 'id', 'name');

                    })
                  }
                  that.tableForm.list = that.tableForm.list.concat(res.data[index]);
                }
               
                that.sortByRouteName(that.tableForm.list);
                
                //按是否被取消排序
              that.tableForm.list.sort((a, b) => {
                
               if(a.status%2==0&&b.status%2!=0){
               return 1;
               }else if(a.status%2!=0&&b.status%2==0){
               return -1;
               }else{
                 return 0;
               }
              })

                //按早送晚收
                that.tableForm.list = that.sortType(that.tableForm.list);

                //按日期排序
                that.tableForm.list.sort((a, b) => {
                  return a.sortDate - b.sortDate
                })
                //重复的排班人员文字标红
                that.getRepeatIds();
                //重复的排班车辆文字标红
                that.getCarRepeatIds()
                let allUnPublish = true;
                if (that.tableForm.list.length) {
                  that.showGenerate = false;
                  for (let j = 0; j < that.tableForm.list.length; j++) {
                    if (that.tableForm.list[j].publish == 1) {
                      allUnPublish = false;
                      break;
                    }
                  }

                  // that.tableForm.list[0].publish == 1 ? (that.showPublish = false) : (that.showPublish = true)
                } else {
                  that.showGenerate = true;
                  allUnPublish = false;
                }
                // debugger;

                if (allUnPublish) {
                  that.showPublish = true;
                }
                let refs = that.$refs.tableRef;
                if (dataUpdate && that.filteredValue && that.filteredValue.length) {
                  defalutTime = that.filteredValue;

                } else {
                  if (new Date().getTime() >= that.dateArr[0].value && new Date().getTime() <= that.dateArr[6].value) {
                    let dayIndex = new Date().getDay() - 1;//周日
                    // if (new Date().getDay() > 1) {
                    //   dayIndex = (new Date().getDay()) - 2;
                    // }
                    defalutTime = [that.dateArr[dayIndex].value]
                  } else {
                    defalutTime = [that.dateArr[0].value]
                  }
                }
                if (dataUpdate && that.filteredTypeValue && that.filteredTypeValue.length) {
                  defalutType = that.filteredTypeValue;

                } else {
                  defalutType = [1, 2]
                }


                if (refs) {

                  //解决第一次默认筛选以后生效，再次改变默认筛选不生效的问题
                  refs.columns[refs.columns.length - 2].filteredValue = defalutTime;//that.filteredValue = [that.dateArr[0].value];这样设置默认筛选只有在created的时候(第一次)生效，后面再设置则不再生效，必须通过这种方式
                  let column = refs.columns[refs.columns.length - 2];
                  //重新筛选
                  that.$refs.tableRef.store.commit('filterChange', {
                    column,
                    values: column.filteredValue,
                    silent: true
                  })

                  //解决第一次默认筛选以后生效，再次改变默认筛选不生效的问题
                  refs.columns[refs.columns.length - 3].filteredValue = defalutType;
                  let column1 = refs.columns[refs.columns.length - 3];
                  //重新筛选
                  that.$refs.tableRef.store.commit('filterChange', {
                    column1,
                    values: column1.filteredValue,
                    silent: true
                  })
                } else {


                  that.filteredValue = defalutTime;
                  that.filteredTypeValue = defalutType;

                }

              }

            } else {
              that.$message.error(res.msg);
            }
          })
          .finally(() => {
            that.listLoading = false;

          });




      })

    },
    sortType(list) {
      let arr1 = [], arr2 = [], arr3 = []
      for (let i = 0; i < list.length; i++) {
        if (list[i].type == 1) {
          arr1.push(list[i])
        } else if (list[i].type == 2) {
          arr2.push(list[i])
        } else {
          arr3.push(list[i])
        }
      }
      return [...arr2, ...arr1, ...arr3]
    },
    handleSelectOptions(option, id, name, idN, nameN) {
      let hasItem = false;
      for (let i = 0; i < option.length; i++) {
        if (option[i][idN] == id) {
          hasItem = true;
          return;
        }
      }
      if (hasItem === false) {
        if (id !== null) {
          option.unshift({ [idN]: id, [nameN]: name, remove: true });
        }
      }
    },
    //判定select选中的是否在数组里
    judgeRemoveIn(arr, id) {
      let flag = false;
      let res = arr.some(item => {
        if (item.id === id && item.remove === true) {
          flag = true;
        }
      })
      return flag
    },

    saveSubmit(row, handletype) {
      if (handletype == 'delete') {
        if (row.status == 1) {
          row.status = 2;
        } else if (row.status == 3) {
          row.status = 4;
        }
      }
      this.addOrEdit(row, 2)
    },

    //初始化下拉框
    getSelect() {
      let that = this;
      let p1 = new Promise((resolve, reject) => {
        carsSelect({ status: 1 }).then(res => {
          if (res.status == 0) {
            if (res.data == null) {
              this.carOptions = [];

              return;
            }
            that.carOptions = res.data;
          } else {
            if (res.data == null) {
              this.carOptions = [];
            }
            this.$message.error(res.msg);
          }
          resolve();
        });
      })
      let p2 = new Promise((resolve, reject) => {
        mapSelect().then(res => {
          if (res.status == 0) {
            if (res.data == null) {
              that.geometryOptions = [];
              return;
            }
            that.geometryOptions = res.data;
          } else {
            if (res.data == null) {
              this.geometryOptions = [];
            }
            this.$message.error(res.msg);
          }
          resolve();

        });
      })
      let p3 = new Promise((resolve, reject) => {
        personsSelect({ work: 2, status: 1 }).then(res => {
          if (res.status == 0) {
            if (res.data == null) {
              that.teamLeaderOptions = [];
              return;
            }
            that.teamLeaderOptions = res.data;
          } else {
            if (res.data == null) {
              this.teamLeaderOptions = [];
            }
            this.$message.error(res.msg);
          }
          resolve();

        });
      })
      let p4 = new Promise((resolve, reject) => {
        carGroupAlllist({ status: 2 }).then(res => {
          if (res.status == 0) {
            if (!res.data) {
              res.data = [];
            }
            let data = res.data;
            for (let i = data.length - 1; i >= 0; i--) {
              if (data[i].status == 0) {
                data.splice(i, 1);
              }
            }
            this.groupAlllist = data;
          } else {

            this.$message.error(res.msg);
          }
          resolve();

        });
      })
      this.getSelectPromises = [p1, p3];


    },

    //重置
    resetTemp() {
      this.temp = {

      };
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    formatDates(time) {
      let dates = [];
      let date1 = new Date(this.getFirstDayOfWeek(time).replace(/_/g, '/')).getTime();
      for (let i = 0; i < 7; i++) {
        dates.push(date1);
        date1 += 24 * 3600 * 1000
      }
      return dates;
    },

    exportExcel() {
      let _t = this;
      let dates = [];
      let date1 = new Date(this.getFirstDayOfWeek(this.listQuery.selectDate).replace(/_/g, '/')).getTime();
      for (let i = 0; i < 7; i++) {
        dates.push(new Date(date1).getTime())
        date1 += 24 * 3600 * 1000
      }
      // 导出数据
      exportExcelReq({ excelType: 18, title: '预排班表', times: dates }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "预排班表" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      });
    },
    sortByRouteName(arr) {
      arr.forEach((item, index) => {
        item.routeName = item.routeName.replace('\t', '')
        item.routeName = item.routeName.trim();
      })

      arr.sort((a, b) => { return this.sortDevName(a.routeName, b.routeName) })
    },
    sortDevName(str1, str2) {
      let res = 0
      for (let i = 0; i < Math.max(str1.length, str2.length); i++) {
        // if (!str1[i] || !str2[i]) {
        //   res = str1.length - str2.length
        //   break
        // }
        const char1 = str1[i]|| ''
        const char1Type = this.getChartType(char1)
        const char2 = str2[i]||''
        const char2Type = this.getChartType(char2)
        // 类型相同的逐个比较字符
        if (char1Type[0] === char2Type[0]) {
          if (char1 === char2) {
            continue
          } else {
            if (char1Type[0] === "zh") {
              res = char1.localeCompare(char2)
            } else if (char1Type[0] === "en") {
              res = char1.charCodeAt(0) - char2.charCodeAt(0)
            } else if (char1Type[0] === "number") {
              res = char1 - char2
            } else {
              res = char1 - char2
            }
            break
          }
        } else {
          // 类型不同的，直接用返回的数字相减
          res = char1Type[1] - char2Type[1]
          break
        }
      }
      return res
    },
    getChartType(char) {
      // 数字可按照排序的要求进行自定义，我这边产品的要求是
      // 数字（0->9）->大写字母（A->Z）->小写字母（a->z）->中文拼音（a->z）

      if (/^[\u4E00-\u9FA5]+$/.test(char)) {
        return ["zh", 300]
      }
      if (/^[a-zA-Z]$/.test(char)) {
        return ["en", 200]
      }
      if (/^[0-9]$/.test(char)) {
        return ["number", 100]
      }
      return ["others", 999]
    },
    sortName(arr) {
      // let arr=[{routeName:'测试3路线0'},{routeName:'测试3路线'},{routeName:'北斗测试路线'},{routeName:'测试3路线一'},{routeName:'测试3路线二'},{routeName:'测试3路线三'},{routeName:'上饶银行金库专用路线1'},{routeName:'测试2录像'}]
      arr.forEach((item, index) => {
        item.routeName = item.routeName.replace('\t', '')
        item.routeName = item.routeName.trim();
      })
      let maxStrLength = this.getMaxStrLength(arr)
      for (let i = 0; i < maxStrLength; i++) {
        arr.sort((a, b) => {
          let char1 = a.routeName.substr(0, i + 1);
          let char2 = b.routeName.substr(0, i + 1);
          if (char1 !== char2) {
            return -1;
          }
        })
      }
    }
  }
};
</script>
