<template>
  <div id="gunInOutProcedure" class="app-container" :class="{adminClass:(power!==1&&power!==2)}">
    <div class="contenBox">

      <el-card class="box-card headerBox">
        <div slot="header" class="clearfix">

          <div class="showText" :class="{activeColor:finish==1}">{{showText}}<span class="highlight" v-show="showRunType">{{'（待'+showRunType+')'}}</span></div>
        </div>
      </el-card>
      <div class="box1">
        <div class="box2">
          <div class="taskNum">
            今日任务数：{{taskList.length}}
            <div class="restartBtn">
              <el-button type="primary" @click="restart1">刷新</el-button>

            </div>
          </div>
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <div>扫描流程</div>
            </div>
            <div class="personMes toggleContent1 tableBox" v-show="inOutType==0||inOutType==1||inOutType==2">
              <el-table :key="tableKey" v-loading="listLoading" :data="procedureList" fit highlight-current-row :row-class-name="tableRowClassName">
                <el-table-column label="类别" width="80px" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.item}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="名称" min-width="80px" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.itemName}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="枪支/设备号" min-width="100px" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.code?scope.row.code:scope.row.gunImei}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="枪支类型" min-width="80px" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.type==1?'普通枪':scope.row.type==2?'机动枪':''}}</span>
                  </template>
                </el-table-column>
                <!-- <el-table-column label="杀伤弹" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{scope.row.bullet1}}</span>
              </template>
            </el-table-column> -->
                <el-table-column label="动能弹" min-width="80px" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.bullet2}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="刷卡时间" min-width="150px" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.time}}</span>
                  </template>
                </el-table-column>

                <el-table-column label="完成状态" min-width="80px" align="center">
                  <template slot-scope="scope">
                    <i class="el-icon-success" :class="{activeColor:(scope.row.status==1?true:false)}"></i>
                  </template>
                </el-table-column>
              </el-table>

            </div>
          </el-card>

          <!-- <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>排班信息</span>
        </div>
        <div class="personMes toggleContent1 tableBox" v-show="inOutType==0||inOutType==1||inOutType==2">
          <el-table :key="tableKey" v-loading="listLoading" :data="taskList" fit highlight-current-row :row-class-name="tableRowClassName">
            <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
            <el-table-column label="押运员1" min-width="100px" align="center" prop="driverName">
              <template slot-scope="scope">
                <span>{{ scope.row.safetyName }}</span>
              </template>
            </el-table-column>
            <el-table-column label="押运员2" min-width="100px" align="center" prop="driverName">
              <template slot-scope="scope">
                <span>{{ scope.row.additionSafetyName }}</span>
              </template>
            </el-table-column>
            <el-table-column label="路线" min-width="100px" align="center" prop="listProp">
              <template slot-scope="{row}">
                <span>{{ row.routeName }}</span>
              </template>
            </el-table-column>
            <el-table-column label="排班类型" min-width="80px" align="center" prop="listProp">
              <template slot-scope="scope">
                <span>{{ formateType(scope.row.type) }}</span>
              </template>
            </el-table-column>

            <el-table-column label="状态" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{ (scope.row.status==1||scope.row.status==3||scope.row.status==5)?'正常':'取消' }}</span>

              </template>
            </el-table-column>
            <el-table-column label="排班日期" min-width="150px" align="center" prop="sortDate">
              <template slot-scope="scope">
                <span>{{ scope.row.planYear+'-'+scope.row.planMonth+'-'+scope.row.planDay }}</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-card> -->
        </div>
        <div class="personDetail">

          <!-- <div class="photo">
              <img :src="personDetail.taskSrc" alt="暂无图片">

            </div>
            <div class="detailText">
              <div>姓名：{{personDetail.name}}</div>
              <div>性别：{{personDetail.sex==1?'男':personDetail.sex==null?'':'女'}}</div>
              <div>年龄：{{personDetail.age}}</div>
            </div> -->
          <div class="detailText">
            {{personDetail.name}}

          </div>
          <div class="photo">
            <img :src="personDetail.taskSrc" alt="暂无图片">

          </div>

        </div>

      </div>
    </div>

    <el-dialog title="提示" :visible.sync="centerDialogVisible" width="30%" top="36vh" :modal="true" center>
      <div style="text-align:center;font-size:20px;padding-bottom:20px">{{dialogText}}</div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="提示" class="dialog-footer-span" center :visible.sync="dialogVisible" width="50%" top="36vh" :show-close="true" :close-on-click-modal="false">
      <span style="text-align:center;font-size:40px;padding-bottom:20px">读卡器已经关闭，请点击按钮重新打开！</span>
      <span slot="footer">
        <!-- <el-button @click="dialogVisible = false">取 消</el-button> -->
        <el-button type="primary" class="open" @click="open">开始刷卡</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { queryR } from "../../api/inOut/rfid";
import { queryByWorker, queryTaskByPersonId } from "../../api/schedule/schedule";
import { personsDetail } from "../../api/managementArchives/personnelFiles";


import { gunPerform, facePersonRelation } from "../../api/inOut";

import { formatDate } from "../../../src/common"; //格式化时间
import { gunDetail } from "../../api/managementArchives/gunFiles";

import {
  imgDownload, photoInfosQuery
} from "../../api/schedule/schedule";
import axios from 'axios'
let myLoading = undefined; // 弹窗服务调用


export default {
  name: "gunInOutProcedure",
  data() {

    return {
      inOutType: 0,
      tableKey: 1,
      listLoading: false,
      procedureList: [
        { item: '管理员' },
        { item: '管理员' },
        { item: '押运员' },
        { item: '枪支' }

      ],
      taskList: [],
      indexMethod: 1,
      rfid: null,//最新的刷卡的rfid
      cancelRequest1: null,
      cancelRequest2: null,
      cancelRequest3: null,
      finish: 0,
      wsCar: null,
      wsArr: [],
      showText: '等待管理员刷卡中......',
      centerDialogVisible: false,
      dialogText: '',
      dialogTimer: null,
      gunScheduleDone: false,
      personDetail: {},
      power: null,
      showRunType: '',
      scanRfid: {},
      dialogVisible: false,
      facePersonRelationData: {},
      confirmShow: false,//用于控制枪支不匹配弹窗选择是否继续时不处理任何刷卡过程



    };
  },
  created() {

    this.power = JSON.parse(this.$store.state.user.userInfo).Power;
    this.getFacePersonRelation();


  },
  computed: {

  },
  mounted() {
    this.webSocketConnect();
  },
  beforeDestroy() {
    if (this.wsCar) {
      this.wsCar.close();
    }
    for (let i = 0; i < this.wsArr.length; i++) {
      if (this.wsArr[i]) {
        this.wsArr[i].close();
        this.wsArr[i] = null;
      }
    }
  },
  methods: {
    formateType(type) {
      let text = ''
      switch (type) {
        case 1:
          text = '晚收'
          break;
        case 2:
          text = '早送'
          break;

        case 5:
          text = '加钞'
          break;

        case 7:
          text = '收款'
          break;


      }
      return text;

    },
    speak(text) {


      let msg = new SpeechSynthesisUtterance(text)

      msg.volume = 100  // 声音的音量

      msg.rate = 1 // 语速，数值，默认值是1，范围是0.1到10

      msg.pitch = 1.5 // 表示说话的音高，数值，范围从0（最小）到2（最大）。默认值为1
      speechSynthesis.speak(msg);
    },
    webSocketConnect() {
      let protojs = require("../../proto.js"), that = this;
      // this.wsCar = new WebSocket("ws://127.0.0.1:22345");
      // this.wsCar.binaryType = "arraybuffer";
      // switch (this.wsCar.readyState) {
      //   case WebSocket.CONNECTING:
      //     console.log("正在连接");
      //     break;
      //   case WebSocket.OPEN:
      //     console.log("连接成功");
      //     break;
      //   case WebSocket.CLOSING:
      //     console.log("正在关闭");
      //     break;

      //   case WebSocket.CLOSED:
      //     console.log("已经关闭，或者打开连接失败");
      //     break;
      //   default:
      //     // this never happens
      //     break;
      // }

      // this.wsCar.onerror = function (params) {
      //   console.log('error')
      // };
      // this.wsCar.onclose = function () {
      //   console.log('close')

      // };
      // this.wsCar.onopen = function () {

      // };
      // this.wsCar.onmessage = function (event) {

      //   if (event.data instanceof ArrayBuffer) {
      //     let arraybuffer = event.data;
      //     let dataView = new DataView(arraybuffer.slice(0, 8));
      //     let int32_length = dataView.getInt32(0);
      //     // let int32_cmd = dataView.getInt32(4);
      //     let locationInfo = null;
      //     let source = new Uint8Array(arraybuffer.slice(8, int32_length + 4));
      //     locationInfo = protojs.lookupType(
      //       "com.beidou.proto.local.RealTimeRfidInfoResponse"
      //     )

      //     let message = locationInfo.decode(source);

      //     that.dealWebSocketData(message.rfid);
      //   }
      // };
      //   this. findIP(function(ip){
      //   //  console.warn(ip)
      //  that.wsInit("ws://"+ip+":22345",protojs)

      // })
      this.wsInit("ws://127.0.0.1:22345", protojs)
      this.wsInit("wss://beidoulab.work:11119", protojs)

    },
    findIP(callback) {
      var myPeerConnection = window.RTCPeerConnection || window.mozRTCPeerConnection || window.webkitRTCPeerConnection; //compatibility for firefox and chrome
      var pc = new myPeerConnection({ iceServers: [] }),
        noop = function () { },
        localIPs = {},
        ipRegex = /([0-9]{1,3}(\.[0-9]{1,3}){3}|[a-f0-9]{1,4}(:[a-f0-9]{1,4}){7})/g,
        key;

      function ipIterate(ip) {
        if (!localIPs[ip]) callback(ip);
        localIPs[ip] = true;
      }
      pc.createDataChannel("");
      pc.createOffer().then(function (sdp) {
        sdp.sdp.split('\n').forEach(function (line) {
          if (line.indexOf('candidate') < 0) return;
          line.match(ipRegex).forEach(ipIterate);
        });
        pc.setLocalDescription(sdp, noop, noop);
      });
      pc.onicecandidate = function (ice) {
        if (!ice || !ice.candidate || !ice.candidate.candidate || !ice.candidate.candidate.match(ipRegex)) return;
        ice.candidate.candidate.match(ipRegex).forEach(ipIterate);
      };
    },
    open() {
      if (this.wsArr.length) {
        this.wsArr[0].send('start');
        this.dialogVisible = false;
      }

    },
    wsInit(wsUrl, protojs) {
      let wsCar = new WebSocket(wsUrl), that = this;
      this.wsArr.push(wsCar);
      wsCar.binaryType = "arraybuffer";
      switch (wsCar.readyState) {
        case WebSocket.CONNECTING:
          console.log("正在连接");
          break;
        case WebSocket.OPEN:
          console.log("连接成功");
          break;
        case WebSocket.CLOSING:
          console.log("正在关闭");
          break;

        case WebSocket.CLOSED:
          console.log("已经关闭，或者打开连接失败");
          break;
        default:
          // this never happens
          break;
      }

      wsCar.onerror = function (params) {
        console.log('error')
      };
      wsCar.onclose = function () {
        console.log('close')

      };
      wsCar.onopen = function () {
        // wsCar.send("start");

      };
      wsCar.onmessage = function (event) {

        if (event.data instanceof ArrayBuffer) {
          let arraybuffer = event.data;
          let dataView = new DataView(arraybuffer.slice(0, 8));
          let int32_length = dataView.getInt32(0);
          // let int32_cmd = dataView.getInt32(4);
          let locationInfo = null;
          let source = new Uint8Array(arraybuffer.slice(8, int32_length + 4));
          locationInfo = protojs.lookupType(
            "com.beidou.proto.local.RealTimeRfidInfoResponse"
          )

          let message = locationInfo.decode(source);
          if (message.cardReadable === -1) {
            that.dialogVisible = false;
            return;
          } else if (message.cardReadable === -2) {
            that.dialogVisible = true;
            return;
          } else if ((message.cardReadable === 1) && that.dialogVisible == false) {
            that.dealWebSocketData(message.rfid);
          } else if (message.cardReadable === 2 && that.dialogVisible == false) {
            if (that.facePersonRelationData.hasOwnProperty(message.enrollId)) {
              that.dealWebSocketData(that.facePersonRelationData[message.enrollId]);
            }
          }

        }
      };
    },
    // ws 处理
    dealWebSocketData(rfid) {
      if (this.confirmShow == true) {
        return;
      }

      //如果还有请求未处理完则不进行下一步
      if (this.cancelRequest1 || this.cancelRequest2 || this.cancelRequest3) {
        return;
      }
      //没提交成功前不允许刷卡，并且这不能添加showDialog，否则会随着推送不停的弹出
      if (this.finish == 0 && this.procedureList[3].rfid) {
        return;
      }
      //刷了押运员的没刷枪的情况下，下一步只能刷枪,否则返回不执行任何操作
      if (this.procedureList[2].status == 1 && !this.procedureList[3].rfid) {
        if (rfid.indexOf('47554E3031') == -1) {
          return;
        }
      }
      //管理员重复刷卡不进行下一步
      if (rfid == this.procedureList[0].rfid || rfid == this.procedureList[1].rfid) {
        return;
      }
      //押运员重复刷卡(且需要满足押运员正常刷卡，有任务)不进行下一步
      if (rfid == this.procedureList[2].rfid && this.procedureList[2].status == 1 && !this.procedureList[3].rfid) {
        return;
      }
      //押运员刷卡步骤还没做，所有的卡会被限制一定时间后才能再刷
      // if (this.procedureList[0].rfid && this.procedureList[1].rfid && (!this.procedureList[2].rfid||!this.procedureList[3].rfid || this.finish == 1)) {
      // let time = new Date();
      // if (!this.scanRfid.hasOwnProperty(rfid) || (this.scanRfid.hasOwnProperty(rfid) && ((time - this.scanRfid[rfid]) >= 30000))) {
      //   this.scanRfid[rfid] = time;
      // } else {
      //   //小于20秒一直重复刷卡直接返回
      //   return;
      // }
      // }

      //所有的卡被锁定一段时间不能再刷
      let time = new Date();
      if (this.scanRfid.hasOwnProperty(rfid) && ((time - this.scanRfid[rfid]) < 30000)) {
        return;
      }

      //如果是重复的rfid不作任何处理
      if (this.rfid === rfid) {
        return;
      };
      //记录本次刷卡的rfid
      this.rfid = rfid;

      this.queryByRfid(this.rfid);

    },
    updateTime() {
      let time = new Date();
      if (!this.scanRfid.hasOwnProperty(this.rfid) || (this.scanRfid.hasOwnProperty(this.rfid) && ((time - this.scanRfid[this.rfid]) >= 30000))) {
        this.scanRfid[this.rfid] = time;
      }
    },
    restart1() {
      if (this.wsArr.length) {
        this.wsArr[0].send('start');
        this.dialogVisible = false;
        this.scanRfid = {};
      }
      this.rfid=null;
      this.restart();
    },
    restart() {
      // this.procedureList = [{ item: '管理员' }, { item: '管理员' }, { item: '押运员' }, { item: '枪支' }];
      this.procedureList.splice(2, 2, { item: '押运员' }, { item: '枪支' })
      this.taskList = [];
      this.finish = 0;
      if (this.procedureList[1].rfid) {
        this.showText = "等待押运员刷卡中......"

      } else {
        this.showText = "等待管理员刷卡中......"

      }

      // this.rfid = null;
      this.personDetail = {};
      this.showRunType = "";
    },
    formatDate1(time) {
      return formatDate(time, 1)
    },
    queryByRfid(rfid) {
      let that = this;

      if (this.cancelRequest1) {
        this.cancelRequest1();
        this.cancelRequest1 = null;
      }

      queryR({
        rfid: this.rfid,
      }, new axios.CancelToken((c) => {
        this.cancelRequest1 = c    //保存当前请求
      }))
        .then(res => {
          if (res.status == 0) {
            that.cancelRequest1 = null;//请求成功后初始化请求
            if (res.data && res.data.length == 0) {
              that.showDialog('未查询到人员信息');
              return;
            }
            let resData = res.data[0];

            if (!resData.linkId) {
              that.showDialog('未查询到身份信息');
              return;
            }
            if (resData.workTypes == null) {
              resData.workTypes = [];
            }
            //普通员工（押运员）刷卡
            if (resData.workTypes.indexOf(3) !== -1 && resData.workTypes.indexOf(6) == -1) {
              //如果管理员没刷卡，不允许押运员刷卡
              if (!that.procedureList[1].rfid) {
                that.showDialog('管理员还未刷卡，请管理员刷卡');
                return;
              }
              that.updateTime();
              that.staffSwipe(resData, rfid);
            } else if (resData.rfidType !== 80 && resData.workTypes.indexOf(6) == -1 && resData.workTypes.indexOf(3) == -1) {
              //既不是押运员也不是枪支和枪管的其他类型的刷卡
              that.showDialog('身份错误');
            } else if (resData.rfidType === 80) {//枪支刷卡
              //押运员刷卡完成，有任务，流程未完成情况下允许刷枪
              if (that.procedureList[2].status && that.taskList.length != 0 && !that.finish && !that.procedureList[3].status) {
                that.updateTime();
                that.gunSwipe(resData, rfid)
              }
            } else if (resData.workTypes.indexOf(6) !== -1 && resData.workTypes.indexOf(3) == -1) {
              //是枪管但不是押运员的情况

              that.updateTime();
              that.adminSwipe(resData, rfid);
            } else if (resData.workTypes.indexOf(6) !== -1 && resData.workTypes.indexOf(3) !== -1) {
              if (!that.procedureList[0].status || !that.procedureList[1].status) {

                that.updateTime();
                that.adminSwipe(resData, rfid);
              } else {
                that.updateTime();
                that.staffSwipe(resData, rfid);
              }
            }
          } else {
            this.$message.error(res.msg);
          }
        }).catch(error => {
        })
        .finally(() => {
          this.cancelRequest1 = null;

        });
    },
    //普通员工刷卡
    staffSwipe(resData, rfid) {
      this.showRunType = "";
      let changeObj = {};
      this.personDetail = {}

      this.taskList = [];
      this.finish = 0;
      // this.procedureList = [{ item: '押运员' }, { item: '枪支' }, { item: '管理员' }, { item: '管理员' }]
      changeObj = {
        item: '押运员',
        itemName: resData.linkName,
        time: this.formatDate1(new Date().getTime()),
        status: 0,
        linkId: resData.linkId,
        bullet1: 4,
        bullet2: 5,
        rfid: rfid,
      }
      this.procedureList.splice(2, 2, changeObj, { item: '枪支' });
      let name = this.procedureList[2].itemName
      this.showText = name + '刷卡';
      this.speak(this.showText)
      let p1 = this.queryPersonDetail(resData.linkId);
      // let p2 = this.querySchedule({ personId: resData.linkId, time: new Date().getTime() });
      let p2 = this.queryTaskList({ personId: resData.linkId });

      //获取人员照片
      p1.then((res) => {
        this.queryPhoto(res).then((res1) => {
          this.showPhoto(res1);
        });
      })
      Promise.all([p1, p2]).then((values) => {
        if (this.taskList.length) {
          this.$set(this.procedureList[2], 'status', 1);
        } else {
          let name = this.procedureList[2].itemName;
          this.showText = name + '无任务信息，请下一位押运员刷卡';
          this.showDialog(this.showText);
          this.restart();
        }
        //无任务卡一直放在卡机上的情况，这样使得过一段时间后仍能正常刷卡
        this.rfid = null;
      }).catch((err) => {
        this.showDialog('获取任务或人员详情出错');
      });

    },
    //枪支刷卡
    gunSwipe(resData, rfid) {
      let changeObj = {}, that = this;
      let p1 = this.gunSwipeStatus(resData.linkId);
      p1.then((p1Data) => {
        changeObj = {
          item: '枪支',
          itemName: resData.linkName,
          time: this.formatDate1(new Date().getTime()),
          status: 0,
          linkId: resData.linkId,
          bullet1: 4,
          bullet2: 5,
          rfid: rfid,
          gunImei: p1Data.iMei,
          type: p1Data.type,
          code: p1Data.code
        }

        if ((p1Data.type == 1 && p1Data.iMei == that.procedureList[2].gunImei) || p1Data.type == 2) {
          that.procedureList.splice(3, 1, changeObj);
          that.showRunType = that.getSwipeStatusText(p1Data.runType);
          that.$set(that.procedureList[3], 'status', 1);
          this.handleInOut({
            safetyId: this.procedureList[2].linkId,
            adminId: this.procedureList[0].linkId,
            gunId: this.procedureList[3].linkId,
            additionAdminId: this.procedureList[1].linkId,
            killBullet: 4,
            kineticBullet: 1,
          })
        } else {
          //枪支不匹配也允许领枪，只是会给出一个枪支不匹配的提示
          that.speak('枪支不匹配');
          that.confirmShow = true;
          that.$alert(`<div class="t-alert-content"">枪支不匹配(该枪号${p1Data.code}),请确定是否允许继续出入库</div>`, '提示', {
            dangerouslyUseHTMLString: true,
            showCancelButton: true,
            confirmButtonText: '允许',
            cancelButtonText: '不允许',
            customClass: 't-confirm',
            showClose: false,
            type: 'warning',
          }).then(action => {
             that.procedureList.splice(3, 1, changeObj);
                that.showRunType = that.getSwipeStatusText(p1Data.runType);
                that.$set(that.procedureList[3], 'status', 1);
                this.handleInOut({
                  safetyId: this.procedureList[2].linkId,
                  adminId: this.procedureList[0].linkId,
                  gunId: this.procedureList[3].linkId,
                  additionAdminId: this.procedureList[1].linkId,
                  killBullet: 4,
                  kineticBullet: 1,
                })
            that.confirmShow = false;

          }).catch(()=>{
            that.confirmShow = false;

          });
        }
      }).catch(() => {
        that.showDialog('获取枪支详情出错啦');
      })

    },

    gunSwipeStatus(id) {
      return new Promise((resolve, reject) => {
        gunDetail({ gunId: id }, new axios.CancelToken((c) => { }))
          .then(res => {
            if (res.status == 0) {
              resolve(res.data);
            }
          }).catch(error => {
          })
          .finally(() => {
          });
      });
    },
    //枪管刷卡
    adminSwipe(resData, rfid) {
      let changeObj = {};
      //如果司机已经刷卡，则显示车管信息
      changeObj = {
        item: '管理员',
        itemName: resData.linkName,
        time: this.formatDate1(new Date().getTime()),
        status: 1,
        linkId: resData.linkId,
        rfid: rfid
      }
      if (!this.procedureList[0].itemName) {
        this.procedureList.splice(0, 1, changeObj);
      } else if (this.procedureList[0].itemName && !this.procedureList[1].itemName) {
        this.procedureList.splice(1, 1, changeObj);
      } else if (this.procedureList[0].itemName && this.procedureList[1].itemName) {
        this.procedureList.splice(0, 1, this.procedureList[1]);
        this.procedureList.splice(1, 1, changeObj);
      }

      if (this.procedureList[1].rfid) {
        this.showText = '管理员' + resData.linkName + '刷卡,等待押运员刷卡中......'
      } else {
        this.showText = '管理员' + resData.linkName + '刷卡'
      }
      this.speak(this.showText);

    },
    // 通过人员详情查询枪支
    queryPersonDetail(personId) {
      return new Promise((resolve, reject) => {
        personsDetail({ personId: personId }, new axios.CancelToken((c) => {
          this.cancelRequest2 = c    //保存当前请求
        }))
          .then(res => {
            if (res.status == 0) {
              this.personDetail.name = res.data.name
              this.personDetail.age = res.data.age
              this.personDetail.sex = res.data.sex
              this.procedureList[2].gunImei = res.data.gunImei
              this.procedureList[2].code = res.data.gunCode
              this.procedureList[2].type = res.data.gunType

            }
            resolve(res.data)
          }).catch(error => {

          })
          .finally(() => {
            this.cancelRequest2 = null;
          });
      });

    },
    // 查询人员照片信息
    queryPhoto(row) {
      return new Promise((resolve, reject) => {
        if (row.photoId == null) {
          return;
        }
        photoInfosQuery({ id: row.photoId, type: [1] })
          .then(res => {
            if (res.status == 0) {
              if (res.data && res.data.length == 0) {
                return;
              }
              resolve(res);

            }
          })
          .catch(() => {
          }).finally(() => {

          });
      });

    },
    // 显示人员照片
    showPhoto(res) {
      return new Promise((resolve, reject) => {

        imgDownload({ path: res.data[0].path, fileName: res.data[0].fileName })
          .then(res1 => {
            if (this.procedureList[2].rfid) {
              this.$set(this.personDetail, "taskSrc", window.URL.createObjectURL(res1))

            }

          })
          .finally(() => {
          });
      });

    },
    //任务查询
    queryTaskList(obj) {
      return new Promise((resolve, reject) => {
        queryTaskByPersonId(obj, new axios.CancelToken((c) => {
          this.cancelRequest3 = c    //保存当前请求
        }))
          .then(res => {
            if (res.status == 0) {
              for (let i = res.data.length - 1; i >= 0; i--) {
                if (res.data[i].status == 2 || res.data[i].status == 4 || res.data[i].status == 6 || (res.data[i].safetyId != this.procedureList[2].linkId && res.data[i].additionSafetyId != this.procedureList[2].linkId)) {
                  res.data.splice(i, 1);
                }

              }
              this.taskList = res.data;

            } else {
              this.taskList = [];

            }
            resolve()
          }).catch(error => {

          })
          .finally(() => {
            this.cancelRequest3 = null;

          });
      });

    },
    //领取和归还提交
    handleInOut(row, type) {

      myLoading = this.$loading({ target: "#myDialog .el-dialog" });

      gunPerform(row)
        .then(res => {
          if (res.status == 0) {
            let text = (type == 1 ? '领取' : '归还'), name = this.procedureList[2].itemName;
            this.$message.success(res.data);
            this.finish = 1;
            this.showText = name + res.data + ',等待下一位押运员刷卡';
            this.showRunType = "";
            this.speak(this.showText);
          } else {
            this.taskList = [];

          }
        }).catch(error => {
          this.showDialog('提交出错啦');
          this.restart();
          this.cancelRequest1 = null;
        })
        .finally(() => {
          myLoading.close();

        });
    },

    //消息弹窗内容
    showDialog(text) {
      clearTimeout(this.dialogTimer);
      this.centerDialogVisible = true;
      this.dialogTimer = setTimeout(() => {
        this.centerDialogVisible = false;
      }, 5000)
      this.dialogText = text;
    },
    getSwipeStatusText(status) {
      //实际上枪支列表当前出入库状态0是归还，1是领取，但是来领还枪的时候提示语就应该正好相反
      let text = '';
      switch (status) {
        case 0:
          text = '领取';
          break;
        case 1:
          text = '归还';
          break
        case 2:
          text = '维修';
          break
        case 3:
          text = '保养中';
          break
        case 4:
          text = '报废';
          break
      }
      return text

    },
    getFacePersonRelation() {
      return new Promise((resolve, reject) => {
        facePersonRelation()
          .then(res => {
            res.data.forEach((item, index) => {
              this.facePersonRelationData[item.enrollId] = item.rfid;

            })
            resolve()
          })
          .catch(() => {
            reject();
          })
          .finally(() => {
          });
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }
      return "";
    },

  }

};
</script>
