import request from '../../request'
//库包查询，带分页功能
export function queryP(data) {
    return request({
        url: '/eoms/package/query',
        method: 'post',
        data,
        headers: {
            'content-type': 'application/json'
        },


    })
}
//库包分页
export function queryPpage(data) {
    return request({
        url: '/eoms/package/page',
        method: 'post',
        data,
        headers: {
            'content-type': 'application/json'
        },


    })
}

