  //根据rfid批量查询库包
  export function pacQueryByRfids(data,cancelToken) {
    return request({
      url: '/eoms/package/queryByRfids',
      method: 'post',
      data,
      headers: {
        'content-type': 'application/json'
    },
      cancelToken: cancelToken

      
    })
  }