<script>
import Pagination from "../../../components/Pagination"; // secondary package based on el-pagination

import { queryActually, updateActuallyTask } from "../../../api/schedule/schedule";
import { carGroupAlllist } from "../../../api/managementArchives/unitManagement";
import { personsSelect, carsSelect } from "../../../api/managementArchives/common"; //各种下拉数据
import { renderTime } from "../../../../src/common";
import { bankschedulingList, bankschedulingGenerate, bankschedulingDrop } from "../../../api/dispatchManagement/bankOrder";


let myLoading = undefined; // 弹窗服务调用

export default {
  name: "bankOrder",
  components: { Pagination },

  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      tableKey1: 1,

      list: null, //大队列表
      total: 0, //大队数量
      indexMethod: 1, // 序号
      listLoading: true,
      listQuery: {
        page: 1,
        limit: 10,
        status: 1
      },
      //添加数据
      temp: {

      },

      statusOptions: [{ value: 1, key: "未发布" }, { value: 2, key: "已取消" }, { value: 3, key: "已处理" }],
      addVisible: false,
      addTemp: { addDate: this.formatDateTime(new Date()) },
      addRules: {
        idIdentify: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        addDate: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        routeId: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        carId: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        leaderId: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        type: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        status: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ]
      },
      showPublish: false,
      scheduleOptions: [],
      scheduleObj:{},
      scheduleSelectDate: new Date().setHours(0, 0, 0, 0),
         scheduleList: [],
      currentRow: null,
      scheduleListLoading: false,

    };
  },
  computed: {

  },
  created() {
    this.getList(); //初始化获取列表
    sessionStorage.removeItem('schedule')
    sessionStorage.removeItem('schedulingId')
    sessionStorage.removeItem('time')
    sessionStorage.removeItem('schedulingDate')
  },
  methods: {
    statusChange(){
      this.listQuery.page=1;
      this.listQuery.limit=10;
      this.getList();
    },
       chooseInstance(val) {
      if (val.length > 1) {

        this.$refs.singleTable.clearSelection()
        this.$refs.singleTable.toggleRowSelection(val.pop())
        this.currentRow = val[0];
      } else if (val.length == 1) {
        this.currentRow = val[0]
      } else {
        this.currentRow = null
      }
    },
    scheduleChange(val){
    this.addTemp.addBusinessName=this.scheduleObj[val].addBusinessName;
    this.addTemp.additionSafetyName=this.scheduleObj[val].additionSafetyName;
    this.addTemp.businessName=this.scheduleObj[val].businessName;
    this.addTemp.carLicenseNo=this.scheduleObj[val].carLicenseNo;
    this.addTemp.driverName=this.scheduleObj[val].driverName;
    this.addTemp.leaderName=this.scheduleObj[val].leaderName;
    this.addTemp.safetyName=this.scheduleObj[val].safetyName;
    },
    formateTaskFinish(finish) {
      let text = ''
      switch (finish) {
        case 0:
          text = '未知';
          break;
        case 1:
          text = '初始化';
          break;
        case 2:
          text = '取消';
          break;
        case 3:
          text = '库管出库';
          break;
        case 5:
          text = '银行入库';
          break;
        case 4:
          text = '库管入库';
          break;
        case 6:
          text = '银行出库';
          break;
      }
      return text;
    },
    formateTaskStatus(type) {
      let text = ''
      switch (type) {
        case 1:
          case 3:
            case 5:
          text = '正常'
          break;
        case 2:
         case 4:
            case 6:
          text = '取消'
          break;
      }
      return text;
    },
    formateOrderStatus(status) {
      let text = ''
      switch (status) {
        case 0:
          text = '初始化 '
          break;
        case 1:
          text = '待发布'
          break;

        case 2:
          text = '已取消'
          break;
        case 3:
          text = '已处理'
          break;
      }
      return text;
    },
    //任务起点
    formateTaskStartPoint(row) {
      let text = '', type = row.type;
      if (type == 2) {
        text = '宏达押运'
      } else if (type == 1 || type == 5 || type == 6 || type == 8) {
        text = row.destinationName
      } else if (type == 7) {
        text = row.otherDestination
      }
      return text;
    },
    //任务终点
    formateTaskEndPoint(row) {
      let text = '', type = row.type;
      if (type == 1) {
        text = '宏达押运'
      } else if (type == 2 || type == 7) {
        text = row.destinationName
      } else if (type == 5 || type == 6 || type == 8) {
        text = row.otherDestination
      }
      return text;
    },
    formateStartPoint(row) {
      let text = '', type = row.type;
      if (type == 1) {
        text = row.destinationName
      } else if (type == 2) {
        text = '宏达押运'
      } else {
        text = row.startPointName
      }
      return text;
    },
    formateEndPoint(row) {
      let text = '', type = row.type;
      if (type == 1) {
        text = '宏达押运'
      } else if (type == 2) {
        text = row.destinationName
      } else {
        text = row.endPointName
      }
      return text;

    },
    formateTaskType(type) {
      let text = ''
      switch (type) {
        case 1:
          text = '晚收'
          break;
        case 2:
          text = '早送'
          break;

        case 5:
          text = '加钞'
          break;
        case 6:
          text = '缴款'
          break;

        case 7:
          text = '送款'
          break;

        case 8:
          text = '上门收款'
          break;

      }
      return text;
    },
        dateChange(val) {
    
    
      this.getSchedule();
    },

    // //获取列表
    // getSchedule(row) {
    //   let that = this;
    //   this.listLoading = true;
    //   this.showPublish = false;
    //   this.scheduleOptions = [];
    //   this.scheduleObj={};

    //   queryActually({
    //     // times: [new Date(row.planYear + '-' + row.planMonth + '-' + row.planDay).getTime()]
    //      times: [this.scheduleSelectDate]
    //   })
    //     .then(res => {
    //       if (res.status == 0) {
    //         let list = []
    //         if (res.data) {
    //           for (let index in res.data) {
    //             for (let i = 0; i < res.data[index].length; i++) {
    //               res.data[index][i].sortDate = parseInt(index);
    //             }
    //             list = list.concat(res.data[index]);
    //           }
    //           list.sort((a, b) => {
    //             return a.sortDate - b.sortDate
    //           })
    //           list.forEach((item, index) => {
    //             // if (item.type == row.type) {
    //               item.name=item.routeName + '类型：' + (item.type == 1 ? '晚接' : '早送')
    //               item.value=item.idIdentify
    //               this.scheduleObj[item.idIdentify]=item;
    //             // this.scheduleOptions.push({ id: item.idIdentify, name: item.routeName + '-' + (item.type == 1 ? '晚接' : '早送') })
    //               this.scheduleOptions.push(item)

    //             // }
    //             // return item.type == row.type;
    //           })


    //         }
    //       } else {
    //         this.$message.error(res.msg);
    //       }
    //     })
    //     .finally(() => {
    //       that.listLoading = false;
    //     });
    // },
        getSchedule(row) {
        if (this.$refs.singleTable) {
        this.$refs.singleTable.clearSelection()

      }

      this.currentRow = null;
      this.scheduleListLoading = true;
      this.scheduleList=[]
      let that = this;
      queryActually({
       times: [new Date(row.planYear + '-' + row.planMonth + '-' + row.planDay).getTime()]
      })
        .then(res => {
          if (res.status == 0) {
            that.scheduleList = [];
            if (res.data) {
              for (let index in res.data) {
                for (let i = res.data[index].length - 1; i >= 0; i--) {
                  let imteI = res.data[index][i]
                  res.data[index][i].sortDate = parseInt(index);
                  if (res.data[index][i].status == 2 || res.data[index][i].status == 4 || sessionStorage.schedulingId == res.data[index][i].idIdentify || (that.list && that.list.length && that.list[0].carLicenseNo == res.data[index][i].carLicenseNo)) {
                    res.data[index].splice(i, 1);
                  }
                }
                that.scheduleList = that.scheduleList.concat(res.data[index]);
              }
              that.scheduleList.sort((a, b) => {
                return a.sortDate - b.sortDate
              })
              that.scheduleList.filter((item) => {
                return item.status !== 2 && item.status !== 4
              })

            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {

          this.scheduleListLoading = false;

        });
    },

    formatTime(time) {
      return renderTime(time);
    },

    handleCreate(row) {
      // this.addTemp = {};
      this.addVisible = true;
      // this.$nextTick(() => {
      //   this.$refs["addForm"].clearValidate();
      // });
      this.addTemp.bankPlanId = row.id
      this.getSchedule(row);

    },
    handleDelete(row) {

      bankschedulingDrop({ id: row.id })
        .then(res => {
          if (res.status == 0) {

            this.getList()
            this.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          // myLoading.close();
        });
    },
    handleDeleteTask(row) {

      let status;
      if (row.status == 5) {
        status = 6;
      }

      updateActuallyTask({ id: row.id, status: status, finish: row.finish })
        .then(res => {
          if (res.status == 0) {
            this.getList();
            this.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
        });
    },
    //添加功能
    createData() {
      // this.$refs["addForm"].validate(valid => {
      //   if (valid) {
      //     this.addOrEdit(this.addTemp);
      //   } else {
      //     return false;
      //   }
      // });
       if (this.currentRow) {
         
          this.addOrEdit(this.currentRow);
        } else {
          this.$message.warning('请选择排班')
        }
    },


    //添加或者是保存数据处理和提交
    addOrEdit(data) {
myLoading = this.$loading({ target: "#mydialog .el-dialog" });
      bankschedulingGenerate({ idIdentify: this.currentRow.idIdentify, bankPlanId: this.addTemp.bankPlanId })
        .then(res => {
          if (res.status == 0) {
            this.getList()
            this.addVisible = false;
            // this.getList();
            this.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          myLoading.close();
        });


    },



    //格式化时间
    formatDateTime(date) {

      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      // m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      // d = d < 10 ? "0" + d : d;
      var h = date.getHours();
      var minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return y + "_" + m + "_" + d;
    },

    //获取列表
    getList() {
      let that = this;
      this.listLoading = true;
      this.showPublish = false;
      this.list = [];
      bankschedulingList({
        status: this.listQuery.status,
        page: this.listQuery.page,
        limit: this.listQuery.limit
      })
        .then(res => {
          if (res.status == 0) {
            that.list = [];
            this.total = res.data.total;


            if (res.data.records && res.data.records.length) {
              that.list = res.data.records
            }
            this.indexMethod = (this.listQuery.page - 1) * this.listQuery.limit + 1;

          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          that.listLoading = false;
        });
    },




    //重置
    resetTemp() {
      this.temp = {

      };
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },

  }
};
