<template>
  <div class="app-container" id="task" :class="{adminClass:(power!==1&&power!==2)}">
    <div class="filter-container">
      <div class="selectBox">
        <el-form :inline="true" class="mdElForm" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
          <el-form-item prop="status" v-if="!scheduleType">
            <el-select v-model="mdForm.routeId" class="filter-item" placeholder="按路线筛选" collapse-tags @change="handleFilter">
              <el-option v-for="item in geometryOptions" :key="item.id" :label="item.name" :value="item.id" />
            </el-select>
          </el-form-item>
          <el-form-item prop="date" v-if="!scheduleType">
            <el-date-picker v-model="mdForm.date" type="date" placeholder="选择日期" @change="handleFilter" :clearable="false">
            </el-date-picker>
          </el-form-item>
          <el-form-item prop="status" v-if="!scheduleType">
            <el-select v-model="mdForm.status" class="filter-item" placeholder="请选择" multiple collapse-tags @change="handleFilter">
              <el-option v-for="item in statusOptions" :key="item.id" :label="item.name" :value="item.id" />
            </el-select>
          </el-form-item>

          <el-button class="filter-item" type="primary" @click="handleCreate" v-if="(scheduleType=='schedualChild'&&schedulePublish!==1)||scheduleType=='schedule'" icon="el-icon-circle-plus-outline" :disabled="dateDisabled">添加</el-button>
          <el-button class="filter-item" type="primary" @click="handleCreateR" v-if="(scheduleType=='schedualChild'&&schedulePublish!==1)||scheduleType=='schedule'" icon="iconfont iconpiliangtianjia" :disabled="dateDisabled">批量添加</el-button>
          <el-button class="filter-item el-icon-refresh" type="primary" :disabled="dateDisabled||!taskIds.length" v-show="scheduleType=='schedule'" @click="handleTransform()">任务转换</el-button>
          <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" v-if="scheduleType" icon="iconfont icondaochu" :loading="exportLoading1">导出</el-button>
          <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel('all')" v-if="!scheduleType" icon="iconfont icondaochu" :loading="exportLoading2">任务导出</el-button>
          <el-button class="filter-item filter-item-btn bigBtn" type="primary" @click="exportImg" v-if="!scheduleType&&(power==1||power==2||power==5)" icon="iconfont icondaochu" :loading="exportLoading3">交接凭证导出</el-button>

          <el-button class="filter-item filter-item-btn" type="primary" @click="$router.go(-1)" v-if="scheduleType">返回</el-button>

          <!-- <el-button class="filter-item" type="primary" v-if="!scheduleType" @click="handleAddUnexpectedTask" icon="el-icon-circle-plus-outline" :disabled="dateDisabled">添加</el-button> -->
          <!-- <el-button class="filter-item filter-item-btn bigBtn" type="primary" @click="exportExcel1" v-if="!scheduleType" icon="iconfont icondaochu">库包分配表导出</el-button> -->

        </el-form>
      </div>
    </div>
    <div class="tableBox">
      <el-table ref="taskForm" :key="tableKey" height="100%" v-loading="listLoading" :data="scheduleType=='schedule'?list:list.slice((currpage - 1) * pagesize, currpage * pagesize)" fit highlight-current-row :row-class-name="tableRowClassName" @selection-change="handleSelectionChange">
        <el-table-column type="selection" :selectable="selectable" width="55" v-if="scheduleType=='schedule'">
        </el-table-column>
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <!-- <el-table-column label="路线" width="100px" align="center" :filter-method="filterHandler" :filters="routeArr"> -->
        <el-table-column label="id" width="80px" align="center" v-if="power==1">

          <template slot-scope="scope">
            <span>{{ scope.row.id }}</span>
          </template>
        </el-table-column>
         <el-table-column label="任务单编号" width="80px" align="center">

          <template slot-scope="scope">
            <span>{{ ''+scope.row.taskYear+addZero(scope.row.taskMonth)+addZero(scope.row.taskDay)+scope.row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column label="路线" width="70px" align="center">

          <template slot-scope="scope">
            <span>{{ scope.row.routeName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车牌号" width="90px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.carLicenseNo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="所属银行" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.parentBankName}}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="网点" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.destinationName }}</span>
          </template>
        </el-table-column> -->
        <el-table-column label="任务起点" min-width="100px" align="center">
          <template slot-scope="scope">
            <span>{{ formateTaskStartPoint(scope.row)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="任务终点" min-width="100px" align="center">
          <template slot-scope="scope">
            <span>{{ formateTaskEndPoint(scope.row) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="预约发起点" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.comeFrom }}</span>
          </template>
        </el-table-column>

        <el-table-column label="任务类型" min-width="60px" align="center" prop="listProp">
          <template slot-scope="scope">
            <span>{{ formateTaskType(scope.row.type) }}</span>
          </template>
        </el-table-column>

        <el-table-column label="正常/取消" width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ statusOptions[row.status-1].name}}</span>
          </template>
        </el-table-column>

        <!-- <el-table-column label="任务类型" width="80px" align="center">
          <template slot-scope="{row}">
            <span v-if="row.status==1||row.status==2">{{ '正常任务'}}</span>
            <span v-if="row.status==3||row.status==4" class="green">{{ '临时任务'}}</span>
            <span v-if="row.status==5||row.status==6" class="blue">{{ '银行任务'}}</span>

          </template>
        </el-table-column>
        <el-table-column label="是否取消" width="60px" align="center">
          <template slot-scope="{row}">
            <span v-if="row.status==2||row.status==4||row.status==6" class="red">{{'已取消'}}</span>
            <span v-else>{{'正常'}}</span>
          </template>
        </el-table-column> -->
        <el-table-column label="库包" width="200px" align="center" v-if="!scheduleType">
          <template slot-scope="{row}">
            <span>{{ row.pakNames}}</span>
          </template>
        </el-table-column>
        <el-table-column label="周转箱数量" width="70px" align="center" v-if="!scheduleType">
          <template slot-scope="{row}">
            <span>{{ row.templateBoxNum}}</span>
          </template>
        </el-table-column>
        <el-table-column label="周转箱" width="100px" align="center" v-if="!scheduleType">
          <template slot-scope="{row}">
            <span>{{ rfidNameObj[row.templateBox]}}</span>
          </template>
        </el-table-column>
        <el-table-column label="凭证袋数量" width="70px" align="center" v-if="!scheduleType">
          <template slot-scope="{row}">
            <span>{{ row.nullNamePackage}}</span>
          </template>
        </el-table-column>
        <el-table-column label="临时箱数量" width="70px" align="center" v-if="!scheduleType">
          <template slot-scope="{row}">
            <span>{{ row.nullNameBox}}</span>
          </template>
        </el-table-column>

        <el-table-column label="任务进展更新时间" min-width="150px" align="center" v-if="scheduleType&&power!=4">
          <template slot-scope="{row}">
            <span>{{ formatTime(row.updateTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="预计到达时间" min-width="80px" align="center" v-if="scheduleType&&power!=4">
          <template slot-scope="scope">
            <span>{{ scope.row.planTime }}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="入库签名" min-width="80px" align="center" fixed="right" v-if="(power==1||power==2||power==4)&&scheduleType">
          <template slot-scope="{row}">
            <span @click="popShow(row,1)" v-if="row.type==1">点击显示</span>
            <span v-else>-</span>
          </template>

        </el-table-column>
        <el-table-column label="出库签名" min-width="80px" align="center" fixed="right" v-if="(power==1||power==2||power==4)&&scheduleType">
          <template slot-scope="{row}">
            <span @click="popShow(row,2)" v-if="row.type==2">点击显示</span>
            <span v-else>-</span>
          </template>

        </el-table-column>
        <el-table-column label="银行出包签名" min-width="80px" align="center" fixed="right" v-if="(power==1||power==2||power==4)&&scheduleType">
          <template slot-scope="{row}">
            <span @click="popShow(row,3)" v-if="row.type==1">点击显示</span>
            <span v-else>-</span>
          </template>

        </el-table-column>
        <el-table-column label="银行入包签名" min-width="80px" align="center" fixed="right" v-if="(power==1||power==2||power==4)&&scheduleType">
          <template slot-scope="{row}">
            <span @click="popShow(row,4)" v-if="row.type==2">点击显示</span>
            <span v-else>-</span>
          </template>

        </el-table-column> -->
        <el-table-column label="出/入库签名" min-width="76px" align="center" fixed="right" v-if="!scheduleType">
          <template slot-scope="{row}">
            <span @click="popShow(row,1)">点击显示</span>
            <!-- <span v-else>-</span> -->
          </template>

        </el-table-column>
        <el-table-column label="银行出/入包签名" min-width="76px" align="center" fixed="right" v-if="!scheduleType">
          <template slot-scope="{row}">
            <span @click="popShow(row,2)">点击显示</span>
            <!-- <span v-else>-</span> -->
          </template>

        </el-table-column>
        <el-table-column label="进展" width="76px" align="center" fixed="right">
          <template slot-scope="{row}">
            <span>{{ formateFinish(row.finish)}}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="操作" align="center" min-width="300" class-name="small-padding fixed-width" fixed="right" v-if="scheduleType"> -->
        <!-- <el-table-column label="操作" align="center" min-width="120" class-name="small-padding fixed-width" fixed="right" v-if="scheduleType!=='schedualChild'"> -->
        <el-table-column label="操作" align="center" min-width="160" class-name="small-padding fixed-width" fixed="right" v-if="power!=5">

          <template slot-scope="{row}">
            <!-- <el-button type="warning" size="mini" :disabled="dateDisabled|| row.status==2||row.status==4||row.status==6" v-show="scheduleType=='schedule'" @click="handleTransform(row)" class="el-icon-refresh">任务转换</el-button> -->
            <el-button type="primary" size="mini" :disabled="row.status==2||row.status==4" @click="handleUpdatePerson(row)" v-if="scheduleType=='schedule'">编辑人员</el-button>

            <el-button type="danger" size="mini" icon="el-icon-delete" :disabled="dateDisabled|| row.status==2||row.status==4||row.status==6" @click="handleDelete(row)" v-if="(scheduleType=='schedualChild'&&schedulePublish!==1)||scheduleType=='schedule'">删除</el-button>

            <!-- <el-button type="primary" size="mini" :disabled="row.status==2||row.status==4" @click="handleUpdate(row)" v-if="(scheduleType=='schedualChild'&&schedulePublish!==1)||scheduleType=='schedule'">编辑</el-button> -->
            <el-button type="primary" size="mini" :disabled="dateDisabled|| row.status==2||row.status==4||row.status==6||row.finish!==1||scheduleType=='schedualChild'||row.type==1" v-show="packShow&&!scheduleType" @click="handleBind(row)" icon="el-icon-edit-outline">库包管理</el-button>

          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />
    <el-dialog id="myDialog" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <el-form-item label="任务类型" prop="status" v-if="(dialogStatus=='create')">
          <el-select filterable v-model.number="temp.status" class="filter-item" placeholder="请选择" @change="statusChange">
            <el-option v-for="item in taskStatusOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="网点" prop="destinationId" v-if="(dialogStatus=='create')&&(temp.status==1||temp.status==2)">
          <el-select filterable v-model.number="temp.destinationId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="任务起点" prop="startPoint" v-if="(dialogStatus=='create')&&!(temp.status==1||temp.status==2)&&temp.status">
          <el-select filterable v-model="temp.startPoint" class="filter-item" placeholder="请选择" multiple collapse-tags @change="changeCheck($event,'startPoint')">
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="任务终点" prop="endPoint" v-if="(dialogStatus=='create')&&!(temp.status==1||temp.status==2)&&temp.status">
          <el-select filterable v-model="temp.endPoint" class="filter-item" placeholder="请选择" multiple collapse-tags @change="changeCheck($event,'endPoint')">
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>

        <el-form-item prop="planTime" label="预计到达时间" v-if="(dialogStatus=='create')">
          <el-time-picker v-model="temp.planTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder="任务预计到达时间"></el-time-picker>
        </el-form-item>
        <!-- <el-form-item label="发起人" prop="bankerId" v-if="(dialogStatus=='create')&&(temp.status!==1&&temp.status!==2&&temp.status)">
          <el-select filterable v-model="temp.bankerId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in bankerOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item> -->
        <el-form-item label="完成状态" prop="finish" v-if="((dialogStatus=='update')&&scheduleType=='schedule')">
          <el-select filterable v-model.number="temp.finish" class="filter-item" placeholder="请选择">
            <el-option v-for="item in finishOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="任务类型" prop="status" v-if="(dialogStatus=='update')">
          <el-select filterable v-model.number="temp.status" class="filter-item" placeholder="请选择">
            <el-option v-for="item in statusOptions" :key="item.id" :label="item.name" :disabled="dialogStatus=='update'&&statusShow(temp.status,item.id)" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="库包" v-if="(dialogStatus=='bind')">
          <el-select v-model="temp.rfids" class="filter-item" filterable multiple collapse-tags placeholder="请选择">
            <el-option v-for="item in bindOptions" :key="item.rfid" :label="item.name" :value="item.rfid" />
          </el-select>
        </el-form-item>
        <el-form-item label="凭证袋数量" v-if="(dialogStatus=='bind')" prop="nullNamePackage">
          <el-input v-model.number="temp.nullNamePackage" />
        </el-form-item>
        <el-form-item label="临时箱数量" v-if="(dialogStatus=='bind')" prop="nullNameBox">
          <el-input v-model.number="temp.nullNameBox" />
        </el-form-item>
        <el-form-item label="周转箱" v-if="(dialogStatus=='bind')">
          <el-select v-model="temp.templateBox" class="filter-item" filterable placeholder="请选择">
            <el-option v-for="item in rfidOptions1" :key="item.rfid" :label="item.name" :value="item.rfid" />
          </el-select>
        </el-form-item>
        <el-form-item label="周转箱数量" v-if="(dialogStatus=='bind')" prop="templateBoxNum">
          <el-input v-model.number="temp.templateBoxNum" />
        </el-form-item>
        
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleSubmit()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="批量添加所属路线下任务" :visible.sync="dialogFormVisibleR" center>
      <el-form ref="dataFormR" :rules="rules" :model="tempR" label-position="left" class="formStyle">
        <el-form-item label="路线" prop="routeId">
          <el-select v-model.number="tempR.routeId" class="filter-item" placeholder="请选择" @change="routeChange">
            <el-option v-for="item in geometryOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item prop="planTime" label="预计到达时间">
          <el-time-picker v-model="tempR.planTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder="任务预计到达时间"></el-time-picker>
        </el-form-item>

        <el-form-item label="网点" prop="destinationIds">
          <el-select v-model="tempR.destinationIds" class="filter-item" placeholder="请选择" filterable multiple collapse-tags>
            <el-option v-for="item in networkOptionsR" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="任务类型" prop="status">
          <el-select v-model="tempR.status" class="filter-item" placeholder="请选择" filterable multiple collapse-tags>
            <el-option v-for="item in taskStatusOptions1" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleSubmitR()">确定</el-button>
      </div>
    </el-dialog>

    <el-dialog id="myDialog1" title="图片" :visible.sync="imgVisible" center>
      <img :src="taskSrc" class="taskSrc" alt="暂无图片">
    </el-dialog>
    <!-- <el-dialog id="myDialog2" title="添加任务" :visible.sync="addVisible" center>
      <el-form ref="unexpectedTaskDataForm" :rules="addRules" :model="addTemp" label-position="left">
        <el-form-item label="网点" prop="destinationId">
          <el-select v-model.number="temp.destinationId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleSubmit()">确定</el-button>
      </div>
    </el-dialog> -->

    <el-dialog title="添加任务" :visible.sync="addVisible" append-to-body class="addTaskBox" center>
      <el-form ref="addForm" :model="addTemp" label-position="left" :rules="addRules" class="formStyle">
        <el-form-item label="车辆" prop="carId">
          <el-select filterable v-model.number="addTemp.carId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in carOptions" :key="item.id" :label="item.licenseNo" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="车组长" prop="leaderId">
          <el-select filterable v-model.number="addTemp.leaderId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in teamLeaderOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="司机" prop="driverId">
          <el-select filterable v-model.number="addTemp.driverId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in driverOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="押运员1" prop="safetyId">
          <el-select filterable v-model.number="addTemp.safetyId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in safetyOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="押运员2" prop="additionSafetyId">
          <el-select filterable v-model.number="addTemp.additionSafetyId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in safetyOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="业务员1" prop="businessId">
          <el-select filterable v-model.number="addTemp.businessId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in businessOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="业务员2" prop="addBusinessId">
          <el-select filterable v-model.number="addTemp.addBusinessId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in businessOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="排班类型" prop="type">
          <el-select v-model.number="addTemp.type" class="filter-item" placeholder="请选择">
            <el-option v-for="item in typeOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="任务类型" prop="taskStatus">
          <el-select v-model.number="addTemp.taskStatus" class="filter-item" placeholder="请选择">
            <el-option v-for="item in taskStatusOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="网点" prop="destinationId">
          <el-select filterable v-model.number="addTemp.destinationId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
