    handleExport() {
      myLoading = this.$loading({ target: "#myDialog3 .el-dialog" });
      this.exportLoading3 = true;
      let proArr = [];
      this.exportTemp.type.forEach((item, index) => {
        proArr[index] = new Promise((resolve, reject) => {
          queryEvidenceInfos({ taskIds: this.idArr, type: item })
            .then(res => {
              if (res.status == 0) {
                if (res.data && res.data.length) {
                  let mapData;

                  mapData = res.data.map((item, index) => {
                    return { path: item.path, filename: item.fileName,type:item.type ,date:''+this.mdForm.date.getFullYear()+(this.mdForm.date.getMonth()+1)+this.mdForm.date.getDate()}
                  })

                  resolve(mapData)

                } else {
                  resolve([])
                }
              } else {
                reject(res.msg)
              }
            })
            .catch((res) => {
              reject()
            });
        })
      })
      Promise.all(proArr).then((info) => {
        let infosArr = [].concat(...info);
        if (!infosArr.length) {
          this.$message.warning('无数据');
          myLoading.close();
          this.exportLoading3 = false;
          this.exportVisible = false;
          return;
        }
        downloadImageZip([].concat(...info))
          .then(res1 => {
            try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res1)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });
            } catch (error) {
              let disposition = res1.headers;
              let fileName = "签名" + ".zip";
              let blob = new Blob([res1], { type: "application/zip" });
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              link.remove();
            }
          })
          .catch((error) => {
            this.$message.error(error);
          }).finally(() => {
            myLoading.close();
            this.exportLoading3 = false;

            this.exportVisible = false;
          });
      }).catch((err) => {
        this.$message.error(err)
        myLoading.close();
        this.exportLoading3 = false;

      })


    },

    //type 1:入库签名 2:出库签名 1:银行出包签名 1:银行入包签名


    //type1:1：出入库签名 2：银行出入包签名
    //row.type:1：晚接 2：早送

    popShow(row, type1) {
      let type;
      if (type1 == 1 && row.type == 1) {//入库签名
        type = 1;
      } else if (type1 == 1 && row.type == 2) {//出库签名
        type = 2;
      } else if (type1 == 2 && row.type == 1) {//银行出包签名
        type = 3;
      } else if (type1 == 2 && row.type == 2) {//银行入包签名
        type = 4;
      }
      this.taskSrc = "";
      let that = this;
      const loading = this.$loading({
        lock: true,
        text: '加载中',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      queryEvidenceInfo({ taskId: row.id, type: type })
        .then(res => {
          if (res.status == 0) {
            if (res.data && res.data.length) {
              imgDownload({ path: res.data[0].path, fileName: res.data[0].fileName })
                .then(res1 => {
                  this.imgVisible = true;
                  that.taskSrc = window.URL.createObjectURL(res1)
                }).catch(() => {
                  this.$message.error('查看失败');
                })
                .finally(() => {
                  loading.close();
                });
            } else {
              loading.close();
              this.$message.warning('无数据');
            }


          } else {
            loading.close();
            this.$message.error(res.msg);
          }
        })
        .catch(() => {
          loading.close();
        })
        .finally(() => {
        });
