        if (valid) {
          let request, subData = {}, unbindData = [];
          //找出需要解绑的库包
          for (let i = 0; i < this.rfidsRow.length; i++) {
            if (this.temp.rfids.indexOf(this.rfidsRow[i]) == -1) {
              unbindData.push(this.rfidsRow[i]);
            }
          }
          subData.taskId = this.temp.id
         
            request = bindPck;
            if (this.temp.rfids.length) {
              subData.rfids = this.temp.rfids;


            }
            if (this.temp.nullNamePackage || this.temp.nullNamePackage == 0) {
              subData.nullNamePackage = this.temp.nullNamePackage;
            }
            if (this.temp.nullNameBox || this.temp.nullNameBox == 0) {
              subData.nullNameBox = this.temp.nullNameBox;
            }


            if (this.temp.templateBoxNum && !this.temp.templateBox) {


              this.$message.warning('周转箱数量与周转箱填写不符合规范');
              return;

            }

            if (this.temp.templateBox && !this.temp.templateBoxNum) {

              this.$message.warning('周转箱数量与周转箱填写不符合规范')
              return;
              // subData.templateBox = this.findItem(this.rfidOptions, 'id', this.temp.templateBox).rfid
            }
            if (this.temp.templateBoxNum || this.temp.templateBoxNum == 0) {
              subData.templateBoxNum = this.temp.templateBoxNum;
            }
            if (this.temp.templateBox || this.temp.templateBox === "" || this.temp.templateBox === null) {
