    sortByKey(list, cur, key) {
      let arr = list;
      let push = [];
      let otherTeam = [];
      for (let x in arr) {
        if (this.inArray(arr[x][key], cur)) {
          arr[x]["sort"] = cur.indexOf(arr[x][key]);
          push.push(arr[x]);
        } else {
          otherTeam.push(arr[x]);
        }
      }
      push = push.sort(function (a, b) {
        if (a.sort < b.sort) {
          return -1;
        } else if (a.sort > b.sort) {
          return 1;
        }
        return 0;
      });
      return push.concat(otherTeam);
    },
    //判断是否在数组里面
    inArray(search, array) {
      for (let i in array) {
        if (array[i] == search) {
          return true;
        }
      }
      return false;
    },
    //获取列表
