    //获取整个任务列表绑定的库包，并且每个任务绑定的库包有多个，所以库包是数组类型
    getBindPak(idArr) {
      queryByTaskIds(idArr, new axios.CancelToken((c) => {
        this.cancelRequest = c; //保存当前请求
      }))
        .then(res => {
          if (res.status == 0) {
            for (let i = 0; i < this.list.length; i++) {
              //过滤掉已经删除的库包，并且把库包数据放入任务列表
              this.list[i].rfids = res.data[idArr[i]].filter((item, index) => {
                return item.status !== 2
              });
              if (res.data[idArr[i]].length) {
                //直接给数组项赋值视图不更新，采用this.$set，需要过滤出来库包名称packageName放到任务列表
                this.$set(this.list[i], 'pakNames', this.list[i].rfids.map((item, index) => {
                  return item.packageName
                }).join())
              }
            }

            this.packShow = true;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          this.listLoading = false;
        });
    },
    //获取凭证袋等的数量
    getNum(idArr) {
      queryExtRelByTaskIds(idArr, new axios.CancelToken((c) => {
        this.cancelRequest1 = c; //保存当前请求
      }))
        .then(res => {
          if (res.status == 0) {
            for (let i = 0; i < this.list.length; i++) {
              for (let j = 0; j < res.data.length; j++) {
                if (this.list[i].id == res.data[j].taskId) {
                  this.$set(this.list[i], 'isAdd', true);
                  this.$set(this.list[i], 'nullNameBox', res.data[j].nullNameBox)
                  this.$set(this.list[i], 'nullNamePackage', res.data[j].nullNamePackage)
                  this.$set(this.list[i], 'templateBox', res.data[j].templateBox)
                  this.$set(this.list[i], 'templateBoxNum', res.data[j].templateBoxNum)
                }

              }



            }
   
            

            // this.packShow = true;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          this.listLoading = false;
        });
    },
