              this.tempR.planOrActually = 0
            } else if (this.scheduleType == 'schedule') {
              this.tempR.planOrActually = 1
            }


            let schedulingDate = sessionStorage.schedulingDate
            let taskYear = parseInt(schedulingDate.split('_')[0])
            let taskMonth = parseInt(schedulingDate.split('_')[1])
            let taskDay = parseInt(schedulingDate.split('_')[2])
            let parentBankObj = this.findItem(this.networkOptionsR, 'id', destinationId)
            let parentBankName = parentBankObj.parentBankName
            let parentBankId = parentBankObj.parentBankId
            let p1 = new Promise((resolve, reject) => {
