                taskYear: taskYear,
                taskMonth: taskMonth,
                taskDay: taskDay,
                parentBankId: parentBankId,
                parentBankName: parentBankName,
                planTime: this.tempR.planTime
              })
                .then(res => {
                  if (res.status == 0) {
                    resolve('成功了')

                  }
                })


            })
            promises.push(p1)
          }
          Promise.all(promises).then((result) => {
            this.getList();
            this.$message.success("批量添加成功");
            this.dialogFormVisible = false;
          }).catch((error) => {
            this.$message.success("添加出错喽");
