    //增加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          for (let i = 0; i < this.networkOptions.length; i++) {
            if (this.networkOptions[i].id == this.temp.destinationId) {

              this.temp.destinationName = this.networkOptions[i].name;
              if (this.networkOptions[i].id == -1) { this.temp.destinationName = "" }
              break;
            }
          }
          if (this.scheduleType == 'schedualChild') {
            this.temp.planOrActually = 0
          } else if (this.scheduleType == 'schedule') {
            this.temp.planOrActually = 1
          }


          let schedulingDate = sessionStorage.schedulingDate
          let taskYear = parseInt(schedulingDate.split('_')[0])
          let taskMonth = parseInt(schedulingDate.split('_')[1])
          let taskDay = parseInt(schedulingDate.split('_')[2])

          //正常任务
          if (this.temp.status == 1) {
            let parentBankObj = this.findItem(this.networkOptions, 'id', this.temp.destinationId)
            let parentBankName = parentBankObj.parentBankName
            let parentBankId = parentBankObj.parentBankId
            addTask({
              idIdentify: this.schedulingId,
              destinationId: this.temp.destinationId,
              destinationName: this.temp.destinationName,
              finish: this.temp.finish,
              status: this.temp.status,
              planOrActually: this.temp.planOrActually,
              taskYear: taskYear,
              taskMonth: taskMonth,
              taskDay: taskDay,
              parentBankId: parentBankId,
              parentBankName: parentBankName,
              planTime: this.temp.planTime


            })
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success("添加成功");
                  this.dialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          } else {//预约任务
            let orderData = {
              type: this.temp.status,
              year: taskYear,
              month: taskMonth,
              day: taskDay,
              startPoint: JSON.stringify(this.temp.startPoint),
              identify: this.schedulingId
            }
            if (this.temp.planTime) {
              orderData.planTime = this.temp.planTime
            }
            if (this.temp.endPoint && this.temp.endPoint.length) {
              orderData.endPoint = JSON.stringify(this.temp.endPoint)
            }
            if (this.temp.bankerId) {
              orderData.bankerId = this.temp.bankerId
            }
            addOrder(orderData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success("添加成功");
                  myLoading.close();
                  this.dialogFormVisible = false;
                  // //添加预约成功直接绑定该任务的排班
                  // bankschedulingGenerate({ idIdentify: this.schedulingId, bankPlanId: res.data.id })
                  //   .then(res1 => {
                  //     if (res1.status == 0) {
                  //       this.getList();
                  //       this.$message.success("添加成功");
                  //       myLoading.close();
                  //       this.dialogFormVisible = false;
                  //     }
                  //   }).catch(() => {
                  //     //添加失败则把预约删除
                  //     bankschedulingDrop({ id: res.data.id })
                  //       .finally(() => {
                  //         myLoading.close();
                  //       })
                  //   })

                } else {
                  this.$message.error(res.msg);
                }
              }).catch(() => {

                myLoading.close();
              })

          }

        } else {
          return false;
        }
      });
    },
    findItem(locations, key, val) {
      let obj = {};
      obj = locations.find((item) => {
        return item[key] === val;

      });
      return obj;
    },

    //点击编辑
    handleUpdate(row) {
      this.temp = {
        id: row.id,
        status: row.status,
        finish: row.finish
      }
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });


    },
    //点击编辑人员
    handleUpdatePerson(row) {
      this.editPersonTemp = {
        id:row.id,
        leaderId :row.leaderId,
        driverId  :row.driverId ,
        safetyId  :row.safetyId ,
        additionSafetyId  :row.additionSafetyId ,
        businessId  :row.businessId ,
        addBusinessId :row.addBusinessId,

      }
      this.editPersonVisible  = true;
         this.$nextTick(() => {
        this.$refs["editPersonForm"].clearValidate();
      });


    },
    //编辑
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {

          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          //预排班页面进来
          let updateReq, subData;
          if (this.scheduleType == "schedualChild") {
            updateReq = updatePlanTask;
            subData = {
              id: this.temp.id,
              status: this.temp.status

            }
          } else if (this.scheduleType == "schedule") {
            updateReq = updateActuallyTask;
            subData = {
              id: this.temp.id,
              status: this.temp.status,
              finish: this.temp.finish

            }
          }
          updateReq(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    exportExcel(type) {
      let _t = this, subData;
      // 查找数据
      if (type == 'all') {
        this.exportLoading2 = true;
        subData = { excelType: 30, title: '任务表', status: this.mdForm.status, times: [this.mdForm.date.getTime().toString()]};
        if(JSON.parse(sessionStorage.getItem("userInfo")).Power==5){
        subData.bankerId=JSON.parse(sessionStorage.getItem("userInfo")).PersonId
        subData.excelType=33;
        }
        if(this.mdForm.routeId){
        subData.routeId=this.mdForm.routeId
        }

      } else {
        this.exportLoading1 = true;
        subData = { excelType: 20, title: '任务表', uuid: sessionStorage.schedulingId, status: [1,2,3,4,5,6]};
      }
      exportExcelReq(subData).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "任务表" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      }).finally(() => {
        if (type == 'all') {
          this.exportLoading2 = false;

        } else {
          this.exportLoading1 = false;

        }
      });
    },
    exportExcel1() {
      let _t = this;
      // 查找数据
      exportExcelReq({ excelType: 23, title: '任务库包分配表', times: [this.mdForm.date.getTime().toString()] }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "任务库包分配表" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      });
    }
  }
};
</script>
