<template>
  <div class="app-container" id="carInOut" :class="{adminClass:(power!==1&&power!==2)}">

    <div class="filter-container">

      <!-- <el-input v-model="listQuery.keyword" placeholder="枪号" class="filter-item search" @keyup.enter.native="handleFilter" />
      <el-button class="filter-item" type="primary" @click="handleFilter">搜索</el-button> -->
      <div class="multidevice filter-container">
        <!-- 查找面板 -->
        <div class="selectBox">
          <el-form :inline="true" class="mdElForm" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
            <el-form-item prop="searchDate">
              <el-date-picker v-model="mdForm.searchDate" type="date" placeholder="选择日期" :clearable="false">
              </el-date-picker>
            </el-form-item>
            <el-button class="filter-item" type="primary" @click="getList" icon="el-icon-search">搜索</el-button>

          </el-form>
        </div>
      </div>

    </div>
    
    <div class="inOutNum">
      <div>总数 {{this.totalCount}} 辆车</div>
      <div>共出库 {{this.totalOutbound}} 次</div>
      <div>共入库 {{this.totalInbound}} 次</div>
    </div>
    <div class="tableBox">

      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <!-- <el-table-column label="大队" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span class="link-type" @click="$router.push('/gunInOutChild')">{{ row.teamName }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="总数" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.count }}</span>
          </template>
        </el-table-column> -->
        <el-table-column label="车牌号" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.code }}</span>
          </template>
        </el-table-column>
        <el-table-column label="出库" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.outbound }}</span>
          </template>
        </el-table-column>
        <el-table-column label="入库" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.inbound }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" /> -->

  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { carInOutCount } from "../../api/inOut";//出入库统计



export default {
  name: "carInOut",
  components: { Pagination },
  data() {
    //时间验证
    let timeRule = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请选择设备"));
      } else {
        callback();
      }

    };
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //大队列表
      total: 0, //大队数量
      indexMethod: 1, // 序号
      listLoading: true,
      listQuery: {
        page: 1,
        limit: 10,
        keyword: undefined
      },

     
      dialogFormVisible: false, //添加或编辑窗口显隐

      mdForm: {
        searchDate: new Date()
      },
      mdRule: {
        searchDate: [{ trigger: "change", message: "请选择时间", validator: timeRule, }],
      },

      totalCount: 0,
      totalInbound: 0,
      totalOutbound: 0,
    };
  },
  created() {
    this.getList(); //初始化获取列表

  },
  methods: {

    getList() {
      let that = this;
      this.listLoading = true;
      carInOutCount({
        date: this.mdForm.searchDate.getTime()
      })
        .then(res => {
          if (res.status == 0) {
            that.listLoading = false;
            that.list = res.data.classify;
            that.totalCount = res.data.totalCount,
              that.totalInbound = res.data.totalInbound,
              that.totalOutbound = res.data.totalOutbound,
              that.indexMethod =
              (that.listQuery.page - 1) * that.listQuery.limit + 1;
          } else {
            // this.$message.error(res.msg);
          }
        })
        .finally(() => {
          that.listLoading = false;
        });
    },
    //点击搜索
    handleFilter() {
      this.$router.push({ name: 'gunInoutChild', params: { keyword: this.listQuery.keyword } });

    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }
      return "";
    },

  }
};
</script>
<style lang="scss">
#carInOut {
  .filter-container {
    height: 70px;
  }
  .selectBox {
    display: flex;
    align-items: center;
    .pauseSelect {
      width: 165px !important;
      .el-input__inner,
      .el-form-item__content,
      .el-cascader,
      .el-input,
      .el-select,
      .el-form-item {
        width: 165px !important;
      }
    }
    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: 300px;
    }
  }
  .search {
    margin-left: -200px;
  }
  .inOutNum {
    & > div {
      display: inline-block;
      margin-right: 10px;
      &:nth-of-type(1) {
        color: #666666;
      }
      &:nth-of-type(2) {
        color: #3fc349;
      }
      &:nth-of-type(3) {
        color: #ff4a4a;
        margin-right: 131px;
      }
    }

    width: 100%;
    height: 70px;
    line-height: 70px;
    margin-top: -70px;
    text-align: right;
    &.inOutNum1 {
      text-align: left;
    }
  }
}
</style>









