<template>
  <div class="app-container" id="gunInOut" :class="{adminClass:(power!==1&&power!==2)}">

    <div class="filter-container">
      <!-- 查找面板 -->
      <div class="selectBox">
        <el-form :inline="true" class="mdElForm" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
          <el-form-item prop="searchDate">
            <el-date-picker v-model="mdForm.searchDate" type="date" @change="getList"  placeholder="选择日期" :clearable="false">
            </el-date-picker>
          </el-form-item>
          <el-button class="filter-item filter-item-btn" type="primary" icon="iconfont icondaochu" @click="exportExcel" :loading="exportLoading">导出</el-button>
        </el-form>
      </div>

    </div>
    <div class="tableBox">
      <div class="tableOne" v-if="power!=6">
        <div class="title">枪支弹药领用通知书</br>
          (押运部)
          <div style="width:100%;text-align:right">N0:{{gunBulletReceiveNo}}</div>
        </div>
        <el-table ref="table1" border key='0' v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
          <el-table-column label="押运线路">
            <template slot-scope="scope">
              <span>{{ scope.row.escortRouteName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="领用时间" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortGunReceiveTime }}</span>
            </template>
          </el-table-column>
          <el-table-column label="枪支领用人" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortGunReceiver1 }}</span>
            </template>
          </el-table-column>
          <el-table-column label="枪支领用人" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortGunReceiver2 }}</span>
            </template>
          </el-table-column>

          <el-table-column label="领用枪支名称" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortGunName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="枪支数量" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortGunNumber }}</span>
            </template>
          </el-table-column>
          <el-table-column label="动能弹数量" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortBulletNumber }}</span>
            </template>
          </el-table-column>
          <el-table-column label="用途" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortUse }}</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="tableTwo" :class="{specialPower:power==6}">
        <div class="title">枪支弹药领用通知书</br>
          (枪弹库)
          <div style="width:100%;text-align:right">N0:{{gunBulletReceiveNo}}</div>
        </div>
        <el-table border ref="table2" key="1" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
          <el-table-column label="押运线路">
            <template slot-scope="scope">
              <span>{{ scope.row.escortRouteName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="领用时间" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortGunReceiveTime }}</span>
            </template>
          </el-table-column>
          <el-table-column label="枪支领用人" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortGunReceiver1 }}</span>
            </template>
          </el-table-column>
          <el-table-column label="枪支领用人" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortGunReceiver2 }}</span>
            </template>
          </el-table-column>

          <el-table-column label="领用枪支名称" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortGunName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="枪支数量" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortGunNumber }}</span>
            </template>
          </el-table-column>
          <el-table-column label="动能弹数量" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortBulletNumber }}</span>
            </template>
          </el-table-column>
          <el-table-column label="用途" min-width="80px" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.escortUse }}</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination

import { queryGunBulletNotice } from "../../../api/dispatchManagement/receiveNotice";//出入库统计
import { exportExcelReq } from "../../../api/managementArchives/common";
import { formatDate } from "../../../../src/common";


export default {
  name: "gunInOut",
  components: { Pagination },
  data() {
    //时间验证
    let timeRule = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请选择设备"));
      } else {
        callback();
      }

    };
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null,
      total: 0,
      indexMethod: 1, // 序号
      listLoading: true,
      listQuery: {
        page: 1,
        limit: 10,
        keyword: undefined
      },
      mdForm: {
        searchDate: new Date()
      },
      mdRule: {
        searchDate: [{ trigger: "change", message: "请选择时间", validator: timeRule, }],
      },
      totalCount: 0,
      totalInbound: 0,
      totalOutbound: 0,
      exportLoading: false,
      gunBulletReceiveNo: '',

    };
  },
  created() {
    this.getList(); //初始化获取列表

  },
  methods: {

    formatDate,
    getList() {
      let that = this;
      this.listLoading = true;
      queryGunBulletNotice({
        actuallyTime: this.formatDate(this.mdForm.searchDate) + ' 00:00:00'
      })
        .then(res => {
          if (res.status == 0) {
            that.listLoading = false;
            that.gunBulletReceiveNo = res.data.gunBulletReceiveNo;
            that.list = res.data.maplist || [];
          } else {

          }
        })
        .finally(() => {
          that.listLoading = false;
        });
    },

    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      // 查找数据
      _t.$refs["mdForm"].validate(valid => {
        if (valid) {
          this.exportLoading = true;
          exportExcelReq({ excelType: 42, dates: [this.formatDate(this.mdForm.searchDate) + ' 00:00:00'] }).then(res => {
            try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
              let fileName = "枪弹领用通知单.xls";
              let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              link.remove();
            }



          }).finally(() => {
            this.exportLoading = false;
          });
        }
      })
    }

  }
};
</script>
<style lang="scss">
#gunInOut {
  .filter-container {
    height: 70px;
  }
  .selectBox {
    display: flex;
    align-items: center;
    .pauseSelect {
      width: 165px !important;
      .el-input__inner,
      .el-form-item__content,
      .el-cascader,
      .el-input,
      .el-select,
      .el-form-item {
        width: 165px !important;
      }
    }
    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: 300px;
    }
  }
  .search {
    margin-left: -200px;
  }
  .inOutNum {
    & > div {
      display: inline-block;
      margin-right: 10px;
      &:nth-of-type(1) {
        color: #666666;
      }
      &:nth-of-type(2) {
        color: #3fc349;
      }
      &:nth-of-type(3) {
        color: #ff4a4a;
        margin-right: 131px;
      }
    }

    width: 100%;
    height: 70px;
    line-height: 70px;
    margin-top: -70px;
    text-align: right;
    &.inOutNum1 {
      text-align: left;
    }
  }
  .el-table--border,
  .el-table--group {
    border: 0.00521rem solid #ebeef5 !important;
  }
}
</style>
<style scoped lang="scss">
.filter-container {
  border-bottom: 0;
}
.tableBox {
  display: flex;
  height: calc(100vh - 220px);
  .title {
    max-height: 110px;
    padding: 10px;
    text-align: center;
    line-height: 30px;
    font-size: 16px;
    font-weight: bold;
  }
}
.tableOne {
  width: calc(50% - 20px);
  margin-right: 20px;
  //    border-right:1px solid lightgrey;
}
.divider {
  height: 100%;
}
.tableTwo {
  width: calc(50% - 20px);
  margin-left: 20px;
  //    border-left:1px solid lightgrey;
}
.specialPower {
  width: 100%;
  margin-left: 0;
}
</style>










