<template>
  <div class="app-container" id="networkHandoverRecord">
    <div class="multidevice filter-container">
      <!-- 查找面板 -->
      <div class="selectBox">
        <el-form :inline="true" class="mdElForm" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
          <el-form-item prop="destinationId">
            <el-select v-model="mdForm.destinationId" class="filter-item" placeholder="按网点搜索">
              <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
            </el-select>
          </el-form-item>
          <el-input v-model="mdForm.packageName" placeholder="按库包名称搜索" class="filter-item" />
          <el-form-item prop="searchDate">
            <el-date-picker v-model="mdForm.searchDate" type="daterange" placeholder="选择日期" :clearable="false" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
          <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>
        </el-form>
      </div>
    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>

        <el-table-column label="网点" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.destinationName }}</span>
          </template>
        </el-table-column>

        <el-table-column label="库包名称" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.packageName }}</span>
          </template>
        </el-table-column>

        <el-table-column label="所属银行" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.parentBankName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="交接类型" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{  scope.row.inOutType==3?'入网点':'出网点'}}</span>
          </template>
        </el-table-column>
        <el-table-column label="交接时间" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{  scope.row.recordTime}}</span>
          </template>
        </el-table-column>

        <!-- <el-table-column label="日期" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.taskYear+'-'+scope.row.taskMonth+'-'+scope.row.taskDay }}</span>
          </template>
        </el-table-column> -->

      </el-table>
    </div>
    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { packageRecordInfo } from "../../api/dataCenter/networkHandoverRecord";//出入库记录列表
import { formatDate } from "../../../src/common"; //格式化时间
import { exportExcelReq, queryTaskByIds } from "../../api/managementArchives/common";
import {
  allDestination
} from "../../api/managementArchives/network";
export default {
  name: "networkHandoverRecord",
  components: { Pagination },
  data() {
    //时间验证
    let timeRule = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请选择设备"));
      } else {
        callback();
      }

    };
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      bankerId: JSON.parse(sessionStorage.getItem("userInfo")).PersonId,
      tableKey: 0,
      list: null,
      total: 0,
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10
      },
      mdForm: {
        searchDate: [new Date(), new Date()],
        packageName: '',
        destinationId: null,

      },
      mdRule: {
        searchDate: [{ trigger: "change", message: "请选择时间", required: true }],
      },
      exportLoading: false,
      networkOptions: [],


    };
  },
  created() {
    this.getSelect();
    this.getList();

  },
  methods: {
    handleFilter() {
      this.listQuery.page = 1;
      this.getList();
    },
    getSelect() {
      let that = this;
      allDestination({ type: -1 }).then(res => {
        if (res.status == 0) {
          if (res.data.records === null) {
            that.networkOptions = [{ id: null, name: "请选择" }];
            return;
          }
          that.networkOptions = res.data;
          that.networkOptions.unshift({ id: null, name: "请选择" });
        } else {
          if (res.data == null) {
            this.networkOptions = [];
          }
          this.$message.error(res.msg);
        }
      });



    },
    formatDate,
    formatDate1(time) {
      if (!time) {
        return '--'
      }
      return formatDate(time, 1)
    },
    getList() {
      let that = this;
      this.listLoading = true;
      let subData = {
        current: this.listQuery.page,
        limit: this.listQuery.limit,
        packageName: this.mdForm.packageName,
        startTime: this.formatDate(this.mdForm.searchDate[0]) + ' 00:00:00',
        endTime: this.formatDate(this.mdForm.searchDate[1]) + ' 00:00:00',
        recordType: 2,
      }
      if (this.mdForm.destinationId) {
        subData.pointId = this.mdForm.destinationId
      }
      packageRecordInfo(subData)
        .then(res => {
          if (res.status == 0) {
            that.list = res.data.data;
            that.total = res.data.count;
            let taskIds = [];
            that.list.forEach((item, index) => {
              taskIds.push(item.taskId)
            })
            if (Array.isArray(that.list) && that.list.length) {
              // this.queryTasksByTasks(taskIds);
            }
            that.indexMethod =
              (that.listQuery.page - 1) * that.listQuery.limit + 1;
          } else {
          }
        })
        .finally(() => {
          that.listLoading = false;
        });

    },

    queryTasksByTasks(taskIds) {
      queryTaskByIds({ ids: taskIds })
        .then(res => {
          if (res.status == 0) {
            let idIdentify;
            for (let i = 0; i < this.list.length; i++) {
              idIdentify = this.list[i].idIdentify;
              for (let j = 0; j < res.data.length; j++) {
                if (res.data[j].id == this.list[i].taskId) {
                  let pacTypeText = '';
                  if (res.data[j].type == 1) {
                    pacTypeText = '出网点'
                  } else if (res.data[j].type == 2) {
                    pacTypeText = '入网点'

                  }
                  this.$set(this.list[i], 'pacTypeText', pacTypeText)

                }
              }
            }
          }
        })
        .catch(() => {

        });
    },


    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }
      return "";
    },
    exportExcel() {
      let _t = this;
      // 查找数据
      _t.$refs["mdForm"].validate(valid => {
        if (valid) {
          this.exportLoading = true;
          let subData = {
            excelType: 34,
            power: _t.power,
            title: '网点交接记录表',
            startDate: this.formatDate(this.mdForm.searchDate[0]) + ' 00:00:00',
            endDate: this.formatDate(this.mdForm.searchDate[1]) + ' 00:00:00',
            personId: this.bankerId,
            packageName: this.mdForm.packageName,
            recordType: 2,
          }
          if (this.mdForm.destinationId) {
            subData.destinationId = this.mdForm.destinationId
          }


          exportExcelReq(subData).then(res => {
            try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
              let disposition = res.headers;
              let fileName = "网点交接记录表" + ".xls";
              let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              link.remove();
            }
          }).finally(() => {
            this.exportLoading = false;
          });
        }
      })
    }
  }
};
</script>
<style lang="scss">
#networkHandoverRecord {
  .filter-container {
    height: 70px;
  }

  .selectBox {
    display: flex;
    align-items: center;
    .el-input__inner {
      background-color: white;
    }
    .el-range-separator,
    .el-range__icon,
    .el-input__icon {
      height: 30px !important;
      line-height: auto;
    }
    .el-range-separator {
      width: 8%;
    }
    .el-cascader__search-input {
      background-color: white;
    }
    .pauseSelect {
      width: 165px !important;
      .el-input__inner,
      .el-form-item__content,
      .el-cascader,
      .el-input,
      .el-select,
      .el-form-item {
        width: 165px !important;
      }
    }
    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: 300px;
    }
  }

  .inOutNum {
    & > div {
      display: inline-block;
      margin-right: 10px;
      &:nth-of-type(1) {
        color: #666666;
      }
      &:nth-of-type(2) {
        color: #3fc349;
      }
      &:nth-of-type(3) {
        color: #ff4a4a;
        margin-right: 50px;
      }
    }

    width: 100%;
    height: 70px;
    line-height: 70px;
    margin-top: -70px;
    text-align: right;
    &.inOutNum1 {
      text-align: left;
    }
  }
}
</style>









