import request from '../../request'
//预约列表
export function bankschedulingList(data) {
    return request({
        url: '/eoms/bankscheduling/list',
        method: 'post', 
        data

    })
}
//根据预约计划
export function bankschedulingDrop(data) {
    return request({
        url: '/eoms/bankscheduling/drop',
        method: 'post', 
        data

    })
}


export function bankschedulingGenerate(data) {
    return request({
        url: '/eoms/bankscheduling/generate',
        method: 'post', 
        data

    })
}