import request from '../../../request'

export function queryPointPackageReport(data) {
    return request({
      url: '/eoms/report/queryPointPackageReport',
      method: 'post',
      data,
    })
}
export function queryByParentBankId(data) {
    return request({
      url: '/map/queryByParentBankId',
      method: 'post',
      data,
    })
}
