<template>
  <div id="mainRealTimeData">
    <div class="topBg">
      <!-- <div class="leftBg">
        <div class="bg1"></div>
        <div class="emptyBox"></div>
        <div class="bg2"></div>
        <div class="emptyBox"></div>
        <div class="bg2"></div>
        <div class="emptyBox1"></div>
      </div>
      <div class="rightBg">
        <div class="emptyBox1"></div>
        <div class="bg3"></div>
        <div class="emptyBox"></div>
        <div class="bg3"></div>
        <div class="emptyBox"></div>
        <div class="bg4"></div>
      </div> -->
      <span class="text">押运智能管理系统</span>
    </div>
    <div class="mainBox">
      <div class="leftChartBox">
        <div>
          <div class="titleBox">
            <div class="divider divider-left"></div>
            <div class="titleText">枪支出库状态</div>
            <div class="divider divider-right"></div>
          </div>
          <div id="myChartGun"></div>
          <!-- <ul class="chartValBox">
            <li>{{gunNmus.out}}</li>
            <li>{{gunNmus.in}}</li>
          </ul> -->
        </div>
         <div>
          <div class="titleBox">
            <div class="divider divider-left"></div>
            <div class="titleText">库包出库状态</div>
            <div class="divider divider-right"></div>
          </div>
          <div id="myChartPac"></div>
          <!-- <ul class="chartValBox">
            <li>{{pacNmus.out}}</li>
            <li>{{pacNmus.in}}</li>
          </ul> -->
        </div>
        <div>
          <div class="titleBox">
            <div class="divider divider-left"></div>
            <div class="titleText">车辆出库状态</div>
            <div class="divider divider-right"></div>
          </div>
          <div id="myChartCar"></div>
          <!-- <ul class="chartValBox">
            <li>{{carNmus.out}}</li>
            <li>{{carNmus.in}}</li>
          </ul> -->
        </div>
       
      </div>
      <div class="centerChartBox">

        <div class="mapBox" :class="{parking:!mapShow}">
          <div class="titleBox" v-if="!mapShow">
            <div class="divider divider-left"></div>
            <div class="titleText">网点报站信息</div>
            <div class="divider divider-right"></div>
          </div>
          <div class="carParking" v-show="!mapShow">
            <div id="flip-list-demo" class="demo">
              <transition-group class="content" name="list-complete" tag="div">
                <div v-for="item in list" v-bind:key="item.value" class="list-complete-item">
                  <!-- <div>{{ '粤U1111' }}</div>
                <div>{{ '2020-12-24 11:25:35' }}</div><div>{{ '到达' }}</div><div>{{ '中国人民银行网点1' }}</div> -->
                  <div>{{ item.licenceNo }}</div>
                  <div>{{ getTime(item.time) }}</div>
                  <div>{{ item.type==1?'进入':'离开' }}</div>
                  <div>{{ item.destinationName }}</div>
                </div>
              </transition-group>
            </div>
          </div>
          <div id="mapCanvas" :class="{none:!mapShow}" @click="mapClick">

          </div>

          <!-- <div class="scrollBox">
            <ul id="con1" ref="con1" :class="{anim:animate==true}">
              <li v-for='item in items'>{{item.name}}</li>
            </ul>
          </div> -->

        </div>
        <div class="centerBottomBox">
          <div class="titleBox">
            <div class="divider divider-left"></div>
            <div class="titleText">当日路线任务</div>
            <div class="divider divider-right"></div>
          </div>
          <div class="daytTaskBox">
            <div class="progressBar">
              <div>工作中 ({{running}})</div>
              <ul class="example">
                <li class="active running" :class="{none:running==0&&complete!==0}">
                  <div></div>
                </li>
                <li class="test complete" :class="{none:complete==0&&running!==0}">
                  <div></div>
                </li>
              </ul>
              <div>({{complete}})已完成</div>
            </div>
            <div class="progressBar progressBar1">
              <div>早送({{early}})</div>
              <ul class="example">
                <li class="active early" :class="{none:early==0&&night!==0}">
                  <div></div>
                </li>
                <li class="test1 night" :class="{none:night==0&&early!==0}">
                  <div></div>
                </li>
              </ul>
              <div>({{night}})晚收</div>
            </div>
          </div>

        </div>

      </div>
      <div class="rightChartBox">
        <div class="taskBox">
          <div class="titleBox">
            <div class="divider divider-left"></div>
            <div class="titleText">银行任务分布图</div>
            <div class="divider divider-right"></div>
          </div>
          <div class="topLegend">
            <div class="topLegendItem topLegendItem3">
              <div>

              </div>
              <div>
                完成任务网点
              </div>
            </div>
            <div class="topLegendItem topLegendItem4">
              <div>

              </div>
              <div>
                已有任务网点
              </div>
            </div>
          </div>
          <div class="bankChartBox">
            <div id="bankChart"></div>
            <!-- <div id="bankChart2" style="width: 500px;height:150px;"></div> -->
          </div>

        </div>
        <div class="alarmBox">
          <div class="titleBox">
            <div class="divider divider-left"></div>
            <div class="titleText">预警统计</div>
            <div class="divider divider-right"></div>
          </div>
          <div class="topLegend">
            <div class="topLegendItem topLegendItem1">
              <div>

              </div>
              <div>
                已处理
              </div>
            </div>
            <div class="topLegendItem topLegendItem2">
              <div>

              </div>
              <div>
                未处理
              </div>
            </div>
          </div>
          <div class="alarmContentBox">
            <div class="alarmBarBox">
              <div>{{notHandleGun}}</div>
              <ul class="example1">
                <li class="alarmBar1 notHandleGun">

                </li>

                <li class="alarmBar2 handledGun">
                  <div class="alarmBarLabel">
                    枪支报警
                  </div>
                </li>
              </ul>
              <div>{{handledGun}}</div>
            </div>
            <div class="alarmBarBox">
              <div>{{notHandleCar}}</div>
              <ul class="example1">
                <li class="alarmBar1 notHandleCar">

                </li>

                <li class="alarmBar2 handledCar">
                  <div class="alarmBarLabel">
                    车辆报警
                  </div>
                </li>
              </ul>
              <div>{{handledCar}}</div>
            </div>
            <div class="alarmBarBox">
              <div>{{notHandleAdas}}</div>
              <ul class="example1">
                <li class="alarmBar1 notHandleAdas">

                </li>

                <li class="alarmBar2 handledAdas">
                  <div class="alarmBarLabel">
                    行为监测
                  </div>
                </li>
              </ul>
              <div>{{handledAdas}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import echarts from "echarts";
import 'echarts/map/js/china'
import {
  carsCountHandover, gunsCountHandover, packageRecordInfoCountCurrent, bankTask,
  dayOfTask, countCurrent
} from "../../api/realTimeData";//出入库统计
import { formatDate } from "../../common";//出入库统计
import { progresssStatistics } from '../../api/progress'

let myLoading = undefined; // 弹窗服务调用

export default {
  name: "mainRealTimeData",
  data() {

    return {
      myChart: undefined, //echarts实例
      carNmus: {
        in: 0,
        out: 0
      },
      gunNmus: {
        in: 0,
        out: 0
      },
      pacNmus: {
        in: 0,
        out: 0
      },
      bankName: [],
      bankTaskTotal: [],
      bankTaskfinish: [],

      animate: false,
      items: [
      ],
      list: [
      ],
      newSocketData: {},
      value: 0,
      mapShow: true,
      early: 0,
      night: 0,
      running: 0,
      complete: 0,
      notHandleAdas: 0,
      notHandleCar: 0,
      notHandleGun: 0,
      handledAdas: 0,
      handledCar: 0,
      handledGun: 0,
      myChartCar: null,
      myChartGun: null,
      myChartPac: null,
      dayTask1: null,
      mapCanvas: null,
      bankChart: null,
      intervalId: null,
      boxAlarm: false,





    };
  },

  watch: {
    sidePop: function (val, oldVal) {
      if (val === true) {
        $('.app-container').css('z-index', 0);
        $('.app-container').css('margin-left', '-240px');
      } else {
        $('.app-container').css('z-index', 10);
        $('.app-container').css('margin-left', '-20px');
      }

    },
    "$store.state.realTimeMonitor.carParkingDetailSocket": {
      handler: function (newer, older) {
        // console.warn(this.$store.state.realTimeMonitor.carParkingDetailSocket);
        this.mapShow = false;
        this.animation(JSON.parse(JSON.stringify(this.$store.state.realTimeMonitor.carParkingDetailSocket)));
        // if (this.cancelRequest) {
        //   this.cancelRequest();
        //   this.cancelRequest = null;
        // }
        // console.warn(this.newSocketData)
        // pacQueryByRfids(this.newSocketData.rfids, new axios.CancelToken((c) => {
        //   this.cancelRequest = c; //保存当前请求
        // })).then(res => {
        //   this.rfidNames = res.data;
        //   let speakText = ''
        //   for (let i in this.rfidNames) {
        //     if (speakText == '') {
        //       speakText = this.rfidNames[i].name;

        //     } else {
        //       speakText += (',' + this.rfidNames[i].name)

        //     }

        //   }
        //   if (speakText !== '') {
        //     this.speak(speakText)

        //   }
        // }).catch(error => {
        //   this.cancelRequest = null;
        // });

      }


    },
    deep: true // 开启深度监听
  },
  // props:['sidePop'],
  beforeDestroy() {
    this.clear();
    // this.$emit('sidePopChange',true)
    if (!this.chart) {
      return
    }

    this.myChartCar && this.myChartCar.dispose()
    this.myChartCar = null
    this.myChartGun && this.myChartGun.dispose()
    this.myChartGun = null
    this.myChartPac && this.myChartPac.dispose()
    this.myChartPac = null
    this.dayTask1 && this.dayTask1.dispose()
    this.dayTask1 = null
    this.mapCanvas && this.mapCanvas.dispose()
    this.mapCanvas = null
    this.bankChart && this.bankChart.dispose()
    this.bankChart = null

  },


  created() {
    this.getData();
    window.addEventListener('resize', this.throttle(this.echartsResize, 1000), false);
    this.dataRefreh();
     if(this.$store.state.user.userInfo&&JSON.parse(this.$store.state.user.userInfo).UserName.indexOf('yunnan')==-1){
    this.getAlarm()
    }
  },

  mounted() {
    this.initBankChart();
    //地图
    this.$nextTick(() => {
      setTimeout(() => {
        this.drawChina()
      }, 1000);
    })

    // 定时模拟的socket的 推送数据， 需求： 页面只显示6个节点，推送几个新的数据，则删除头部几个数组。
    // setInterval(() => {
    //   this.animation();
    // }, 3000);

  },
  methods: {

    getAlarm() {
      let p1 = new Promise((resolve, reject) => {
        progresssStatistics({
          date: new Date().getTime(),
          type: 2,
        })
          .then(res => {
            if (res.status == 0) {
              resolve(res.data)
            } else {
              reject();
              this.$message.error(res.msg);
            }
          })
      })

      let p2 = new Promise((resolve, reject) => {
        progresssStatistics({
          date: new Date().getTime(),
          type: 1,
        })
          .then(res => {
            if (res.status == 0) {
              resolve(res.data)
            } else {
              reject();
              this.$message.error(res.msg);
            }
          })

      })


      Promise.all([p1, p2]).then((values) => {
        let arr=[];
        if (new Date().getHours() >= 14 && new Date().getHours() <= 21) {
          arr = values[0]
        } else if(new Date().getHours()>21){
          arr = values[0].concat(values[1])
        }
        for (let i = 0; i < arr.length; i++) {
          let item = arr[i]
          if ((item.taskCount - item.cancelledTask) && (item.finishTask / (item.taskCount - item.cancelledTask) * 100 != 1)) {
            this.$alert('款箱交接未完成！', '提示', {
              confirmButtonText: '知道了',
              customClass:'boxMessageBox',
              callback: action => {
              }
            });
            break;
          }
        }

      })
    },
    refreshData(data, myChart) {
      if (!myChart) {
        return;
      }
      //更新数据
      let option = myChart.getOption();
      option.series[0].data = data;
      myChart.setOption(option);
 
      let currentIndex = 0;

      // setTimeout(function () {

      currentIndex--;

      let dataLen = option.series[0].data.length;

      // 取消之前高亮的图形

      myChart.dispatchAction({

        type: 'downplay',

        seriesIndex: 0,

        dataIndex: currentIndex

      });

      currentIndex = (currentIndex + 1) % dataLen;

      // 高亮当前图形

      myChart.dispatchAction({

        type: 'highlight',

        seriesIndex: 0,

        dataIndex: currentIndex

      });

      // }, 100);

      $(myChart).mouseenter(function () {

        myChart.dispatchAction({

          type: 'downplay',

          seriesIndex: 0,

          dataIndex: currentIndex

        });

      });

      $(myChart).mouseleave(function () {

        myChart.dispatchAction({

          type: 'highlight',

          seriesIndex: 0,

          dataIndex: currentIndex

        });

      });
    },
    // 定时刷新数据函数
    dataRefreh() {
      // 计时器正在进行中，退出函数
      if (this.intervalId != null) {
        return;
      }
      // 计时器为空，操作
      this.intervalId = setInterval(() => {
        this.getData(true)
      }, 20000);
    },
    // 停止定时器
    clear() {
      clearInterval(this.intervalId); //清除计时器
      this.intervalId = null; //设置为null
    },
    throttle(func, delay) {
      var prev = Date.now();
      return function () {
        var context = this;
        var args = arguments;
        var now = Date.now();
        if (now - prev >= delay) {
          func.apply(context, args);
          prev = Date.now();
        }
      }
    },
    echartsResize() {
      this.myChartCar && this.myChartCar.resize()
      this.myChartGun && this.myChartGun.resize()
      this.myChartPac && this.myChartPac.resize()
      this.dayTask1 && this.dayTask1.resize()
      this.mapCanvas && this.mapCanvas.resize()
      this.bankChart && this.bankChart.resize()
    },
    mapClick() {
      this.$router.push('/realTimeMonitoring')
    },
    getTime(time) {
      if (time) {
        return this.formatTime1(parseInt(time)).split(' ')[1]
      }

    },
    checkCarParking(item) {
      sessionStorage.destinationId = item.destinationId;
      this.$router.push('/carParkingBySchedule')
    },
    speak(text) {


      let msg = new SpeechSynthesisUtterance(text)

      msg.volume = 100  // 声音的音量

      msg.rate = 1 // 语速，数值，默认值是1，范围是0.1到10

      msg.pitch = 1.5 // 表示说话的音高，数值，范围从0（最小）到2（最大）。默认值为1
      speechSynthesis.speak(msg);
    },
    formatTime1(time) {
      return formatDate(time, 1);
    },


    animation(addItems) {
      addItems.value = this.value++;
      let newItems = [...this.list];
      let times = this.formatTime1(parseInt(addItems.time)).split(' ')[1].split(':');
      let str = times[0] + '点' + times[1] + '分' + times[2] + '秒';


      // console.warn(this.formatTime1(parseInt(addItems.time)).split(' ')[1].split(':'));
      let text = addItems.licenceNo+',' + str + (addItems.type == 1 ? '进入' : '离开') + addItems.destinationName;

      this.speak(text);





      // Array.from(new Array(randomCount), (n, i) => i).forEach(() =>
      //   addItems.push({
      //     name: (Math.random(0, 1) * 1000).toFixed(0),
      //     value: Math.random(0, 1),
      //   })
      // );
      // console.warn('aaaa');

      if (newItems.length >= 8) {
        // 删除 数组 头部 几个item
        let len = newItems.length;
        newItems.splice(len - 1, 1);
      }
      // console.warn('bbb');

      // 触发 transition-group 动画
      // newItems.splice(0, 0, ...addItems);
      // for(let i=0;i<addItems.length;i++){
      newItems.unshift(addItems);
      // }
      // newItems.splice(newItems.length, 0, ...addItems);
      // console.warn(addItems)
      // 数组 尾部 添加 几个item
      // console.warn('ccccc');


      this.list = [...newItems];
      // console.warn('dddddd');

    },



    scroll() {
      this.animate = true;    // 因为在消息向上滚动的时候需要添加css3过渡动画，所以这里需要设置true

      setTimeout(() => {      //  这里直接使用了es6的箭头函数，省去了处理this指向偏移问题，代码也比之前简化了很多
        this.items.unshift(this.items[0]);  // 将数组的第一个元素添加到数组的
        //删除数组的第一个元素
        this.animate = false;  // margin-top 为0 的时候取消过渡动画，实现无缝滚动
        this.items.pop();
      }, 500)

    },
    getData(refresh) {
      dayOfTask({
        time: new Date().getTime()
      })
        .then(res => {
          if (res.status == 0) {
            if (res.data) {
              this.early = res.data.early;
              this.night = res.data.night;
              this.complete = res.data.complete;
              this.running = res.data.running;

              if (this.early == 0 && this.night == 0) {
                $('.early').width('50%')
                $('.night').width('50%')
              } else {
                $('.early').width(this.early * 100 / (this.early + this.night) + '%')
                $('.night').width(this.night * 100 / (this.early + this.night) + '%')

              }
              if (this.complete == 0 && this.running == 0) {
                $('.complete').width('50%')
                $('.running').width('50%')
              } else {
                $('.complete').width(this.complete * 100 / (this.complete + this.running) + '%')
                $('.running').width(this.running * 100 / (this.complete + this.running) + '%')


              }
            }



          }
        })
        .finally(() => {

        });
      countCurrent({})
        .then(res => {
          if (res.status == 0) {
            if (res.data) {

              this.notHandleAdas = res.data.notHandleAdas;
              this.notHandleCar = res.data.notHandleCar;
              this.notHandleGun = res.data.notHandleGun;
              this.handledAdas = res.data.handledAdas;
              this.handledCar = res.data.handledCar;
              this.handledGun = res.data.handledGun;

              if (this.notHandleAdas == 0 && this.handledAdas == 0) {
                $('.notHandleAdas').width('0%')
                $('.handledAdas').width('100%')

              } else {
                $('.notHandleAdas').width(this.notHandleAdas * 100 / (this.notHandleAdas + this.handledAdas) + '%')
                $('.handledAdas').width(this.handledAdas * 100 / (this.notHandleAdas + this.handledAdas) + '%')

              }
              if (this.notHandleCar == 0 && this.handledCar == 0) {
                $('.notHandleCar').width('0%')
                $('.handledCar').width('100%')
              } else {
                $('.notHandleCar').width(this.notHandleCar * 100 / (this.notHandleCar + this.handledCar) + '%')
                $('.handledCar').width(this.handledCar * 100 / (this.notHandleCar + this.handledCar) + '%')


              }
              if (this.notHandleGun == 0 && this.handledGun == 0) {
                $('.notHandleGun').width('0%')
                $('.handledGun').width('100%')
              } else {
                $('.notHandleGun').width(this.notHandleGun * 100 / (this.notHandleGun + this.handledGun) + '%')
                $('.handledGun').width(this.handledGun * 100 / (this.notHandleGun + this.handledGun) + '%')


              }

            }


          }
        })
        .finally(() => {

        });
      carsCountHandover({

      })
        .then(res => {
          if (res.status == 0) {
            let count = 0, inNum = 0, outNum = 0;

            if (res.data) {
              inNum = res.data.inbound ? res.data.inbound : 0;
              outNum = res.data.outbound ? (res.data.outbound) : 0
            }


            this.carNmus = {
              in: inNum,
              out: outNum
            }
            if (refresh) {
              this.refreshData([{ value: outNum, name: '车辆出库' }, { value: inNum, name: '车辆在库' }], this.myChartCar)
              return;
            }
            this.initChart('myChartCar', ['车辆出库', '车辆在库'], [{ value: outNum, name: '车辆出库' }, { value: inNum, name: '车辆在库' }]);
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {

        });
      gunsCountHandover({

      })
        .then(res => {
          if (res.status == 0) {
            let count = 0, inNum = 0, outNum = 0;
            if (res.data) {
              inNum = res.data.inbound ? res.data.inbound : 0;
              outNum = res.data.outbound ? (res.data.outbound) : 0
            }
            this.gunNmus = {
              in: inNum,
              out: outNum
            }
            if (refresh) {
              this.refreshData([{ value: outNum, name: '枪支出库' }, { value: inNum, name: '枪支在库' }], this.myChartGun)
              return;
            }
            this.initChart('myChartGun', ['枪支出库', '枪支在库'], [{ value: outNum, name: '枪支出库' }, { value: inNum, name: '枪支在库' }]);



          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {

        });
      packageRecordInfoCountCurrent({

      })
        .then(res => {
          if (res.status == 0) {
            let inNum = res.data ? res.data.inBound : 0

            let outNum = res.data ? res.data.outBound : 0
            this.pacNmus = {
              in: inNum,
              out: outNum
            }
            if (refresh) {
              this.refreshData([{ value: outNum, name: '库包出库' }, { value: inNum, name: '库包入库' }], this.myChartPac)
              return;
            }
            this.initChart('myChartPac', ['库包出库', '库包入库'], [{ value: outNum, name: '库包出库' }, { value: inNum, name: '库包入库' }]);

          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {

        });
      bankTask({

      })
        .then(res => {
          if (res.status == 0) {
            delete res.data['中国人民银行'];
            for (let item in res.data) {

              this.bankName.push(item)
              this.bankTaskTotal.push(res.data[item].total)
              this.bankTaskfinish.push(res.data[item].finish)
              //按银行的总任务量排序
              for (let i = this.bankTaskTotal.length - 1; i >= 0; i--) {
                for (let j = i - 1; j >= 0; j--) {
                  if (this.bankTaskTotal[j] < this.bankTaskTotal[i]) {
                    let a = this.bankTaskTotal[i], b = this.bankName[i], c = this.bankTaskfinish[i]
                    this.bankTaskTotal[i] = this.bankTaskTotal[j];
                    this.bankTaskTotal[j] = a;

                    this.bankName[i] = this.bankName[j];
                    this.bankName[j] = b;

                    this.bankTaskfinish[i] = this.bankTaskfinish[j];
                    this.bankTaskfinish[j] = c;
                  }
                }
              }


            }
            if (refresh) {
              return;
            }
            this.initBankChart()
            // let inNum = res.data?res.data.inBound:0

            // let outNum = res.data?res.data.outBound:0
            // this.pacNmus = {
            //   in: inNum,
            //   out: outNum
            // }
            // this.initChart('myChartPac', ['库包出库', '库包在库'], [{ value: outNum, name: '库包出库' }, { value: inNum, name: '库包在库' }]);

          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {

        });
