    initChart(myChart, data1, data2) {
      this[myChart] = echarts.init(document.getElementById(myChart));
      let option = {

        color: ['#7882D7', '#4E57A6'],
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
