          icon: 'circle',
          y: 'center',
          // top: 70,
          right: '50',
          orient: 'vertical',
          textStyle: {
            color: '#9EA6DA',
            fontSize: 14,
            rich: {
              verticalAlign: 'top'
            }
          },
          formatter: function (params) {
            for (let i = 0; i < data2.length; i++) {
              if (data2[i].name == params) {
                return data2[i].name + ' ' + data2[i].value

              }
            }
            // let num = params.value;
            // if (num > 1024) {
            //   return (num / 1024).toFixed(2) + 'GB'
            // } else if (num < 1024 && num > 0) {
            //   return num.toFixed(2) + 'MB'
            // } else {
