          // data: data1
        },
        series: [{
          name: '出入库',
          type: 'pie',
          radius: ['40%', '65%'],
          center: ['25%', '50%'],
          // roseType: 'radius',
          itemStyle: {
            normal: {
              label: {
                show: false
              },
              labelLine: {
                show: true
              }
            },
            emphasis: {
              label: {
                show: false,
                position: 'outer',
                textStyle: {
                  fontSize: '15',
                  fontWeight: 'bold',
                  color: 'white'
                }
              }
            }
          },
          data: data2
        }]
      }

      this[myChart].setOption(option)
     
      let currentIndex = 0;

      // setTimeout(function () {

      currentIndex--;

      let dataLen = option.series[0].data.length;

      // 取消之前高亮的图形

      this[myChart].dispatchAction({

        type: 'downplay',

        seriesIndex: 0,

        dataIndex: currentIndex

      });

      currentIndex = (currentIndex + 1) % dataLen;

      // 高亮当前图形

      this[myChart].dispatchAction({

        type: 'highlight',

        seriesIndex: 0,

        dataIndex: currentIndex

      });

      // }, 1000);

      $(myChart).mouseenter(function () {

        this[myChart].dispatchAction({

          type: 'downplay',

          seriesIndex: 0,

          dataIndex: currentIndex

        });

      });

      $(myChart).mouseleave(function () {

        this[myChart].dispatchAction({

          type: 'highlight',

          seriesIndex: 0,

          dataIndex: currentIndex

        });

      });
