<template>
  <div class="app-container" id="carParkingRecord">
    <div class="multidevice filter-container">
      <!-- 查找面板 -->
      <div class="selectBox">
        <el-form :inline="true" class="mdElForm" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
          <el-form-item>
            <el-cascader popper-class="myGunCas" ref="myMdGunsCas" v-model="mdForm.imeiCar" placeholder="选择车辆" :props="{expandTrigger:'hover'}" :options="dataGun" :show-all-levels="false" clearable filterable collapse-tags>
            </el-cascader>
          </el-form-item>
          <el-form-item prop="searchDate">
            <el-date-picker v-model="mdForm.searchDate" type="date" :clearable="false" placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
            <el-button class="filter-item filter-item-btn" type="primary" @click="handleExportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>

            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>

        <el-table-column label="车牌号" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.licenseNo }}</span>
          </template>
        </el-table-column>
        <el-table-column label="路线" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.routeName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="网点" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.destinationName }}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="类型" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.type==1?'晚收':'早送' }}</span>
          </template>
        </el-table-column> -->
        <el-table-column label="司机" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.driverName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="车组长" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.leaderName }}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="押运员1" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.safetyName }}</span>
          </template>
        </el-table-column>
         <el-table-column label="业务员1" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.leaderName }}</span>
          </template>
        </el-table-column>
         <el-table-column label="押运员2" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.additionSafetyName }}</span>
          </template>
        </el-table-column>
         <el-table-column label="业务员2" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.addBusinessName }}</span>
          </template>
        </el-table-column> -->
        <el-table-column label="排班日期" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.year+'-'+row.month+'-'+row.day}}</span>
          </template>
        </el-table-column>
        <el-table-column label="到达/离开" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.type==1?'到达':'离开'}}</span>
          </template>
        </el-table-column>
        <el-table-column label="时间" min-width="100px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatDate1(row.createTime)}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />
    <el-dialog title="导出" :visible.sync="formVisible" append-to-body center>
      <el-form ref="addForm" :model="temp" label-position="left" :rules="rules" class="formStyle">
        <el-form-item prop="imeiCar" label="车辆">
          <el-cascader popper-class="myGunCas1" class="myGunCas1" ref="myMdGunsCas1" v-model="temp.imeiCar" placeholder="选择车辆" :props="{expandTrigger:'hover',multiple: true}" :options="dataGun" :show-all-levels="false" clearable filterable collapse-tags>
          </el-cascader>
        </el-form-item>
         <el-form-item prop="searchDate" label="时间">
            <el-date-picker v-model="temp.searchDate" type="date" placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
        <!-- <el-form-item prop="searchDate" label="年月">
          <el-date-picker v-model="temp.searchDate" type="month" placeholder="选择月">
          </el-date-picker>
        </el-form-item> -->
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="exportExcel()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { teamAndCarList } from "../../api/realTimeMonitoring";//车辆列表
import { carparkingHistory } from "../../api/inOut/carParkingRecord";//出入库记录列表
import { formatDate } from "../../../src/common"; //格式化时间
import { exportExcelReq } from "../../api/managementArchives/common";

export default {
  name: "carParkingRecord",
  components: { Pagination },
  data() {
    //设备验证
    let multiRule = (rule, value, callback) => {
      let guns = this.mdForm.imeiCar;
      if (guns.length < 1) {
        callback(new Error("请选择设备"));
      } else {
        callback();
      }

    };
    //车辆导出多设备验证
    let multiRule1 = (rule, value, callback) => {
      let guns = this.temp.imeiCar;
      if (guns.length < 1) {
        callback(new Error("请选择"));
      } else {
        callback();
      }

    };
    //时间验证
    let timeRule = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请选择"));
      } else {
        callback();
      }

    };
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null,
      total: 0,
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10
      },
      mdForm: {
        imeiCar: [],
        searchDate: new Date()
      },
      mdRule: {
        imeiCar: [{ validator: multiRule, trigger: "change" }],
        searchDate: [{ trigger: "change", message: "请选择时间", validator: timeRule, }],
      },


      dataGun: [], // 多设备枪,
      imei2name: {}, // 设备的imei和对应的名字
      formVisible: false,
      temp: {
        imeiGun: [],
        searchDate: new Date()

      },
      rules: {
        imeiCar: [{ validator: multiRule1, trigger: "change", required: true }],
        searchDate: [{ trigger: "change", message: "请选择时间", validator: timeRule, required: true }],
      },
      exportLoading: false,


    };
  },
  created() {
    this.getTeamAndCarList();
    this.getList();
  },
  methods: {

    handleExportExcel() {
      this.formVisible = true;
      this.temp.imeiCar = []
    },
    formatDate1(time) {
      return formatDate(time, 1)
    },

    getTeamAndCarList() {
      let that = this;
      teamAndCarList({}).then(res => {
        if (res.status == 0) {
          that.dataGun = this.formatCas(res.data, "licenseNo", "licenseNo"); // 设置枪下拉树的数据格式
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    formatCas(data, imei, name) {
      // 格式化级联选择器数据 data是原始数据 imei是vaue值 name是label值
      let result = [],
        id = 0,
        _t = this;
      for (let i in data) {
        let childs = []; // 格式化子数组
        data[i].forEach(x => {


          childs.push({
            value: x[imei],
            label: x[name]
          });
          _t.imei2name[x[imei]] = x['serialNo']; // 将设备号与设备名组成一个数组

        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "未分组" : i,
          children: childs
        });
      }
      return result;
    },
    getList() {
      let that = this;
      this.listLoading = true;
      let carIds = [];
      carIds.push(this.mdForm.imeiCar[1])
      let subData = { time: this.mdForm.searchDate.getTime() }
      if (this.mdForm.imeiCar[1]) {
        subData.carNo = carIds[0];
      }
      carparkingHistory(subData)
        .then(res => {
          if (res.status == 0) {
            that.list = res.data;

          } else {
          }
        })
        .finally(() => {
          that.listLoading = false;
        });


    },
    //点击搜索
    handleFilter() {
      this.getList();
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }
      return "";
    },
    exportExcel() {

      let _t = this, carIds = [];
      for (let i = 0; i < this.temp.imeiCar.length; i++) {
        carIds.push(_t.imei2name[this.temp.imeiCar[i][1]])
      }
      // 查找数据
      _t.$refs["addForm"].validate(valid => {
        if (valid) {
      this.exportLoading = true;

          exportExcelReq({ excelType: 31, title: '报站记录表', imeis: carIds, times: [this.temp.searchDate.getTime().toString()] }).then(res => {
            try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.formVisible = false;

              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
              let disposition = res.headers;
              let fileName = "报站记录表" + ".xls";
              let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              link.remove();
              this.formVisible = false;
            }
          }).finally(() => {
            this.exportLoading = false;
          });
        }
      })
    }
  }
};
</script>
<style lang="scss">
#carParkingRecord {
  .filter-container {
    height: 70px;
  }
  .selectBox {
    display: flex;
    align-items: center;
    .pauseSelect {
      width: 165px !important;
      .el-input__inner,
      .el-form-item__content,
      .el-cascader,
      .el-input,
      .el-select,
      .el-form-item {
        width: 165px !important;
      }
    }
    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: 300px;
    }
  }

  .inOutNum {
    & > div {
      display: inline-block;
      margin-right: 10px;
      &:nth-of-type(1) {
        color: #666666;
      }
      &:nth-of-type(2) {
        color: #3fc349;
      }
      &:nth-of-type(3) {
        color: #ff4a4a;
        margin-right: 50px;
      }
    }

    width: 100%;
    height: 70px;
    line-height: 70px;
    margin-top: -70px;
    text-align: right;
    &.inOutNum1 {
      text-align: left;
    }
  }
}
.myGunCas1 {
  .el-cascader__tags {
    left: 6px;
  }
  // .el-input__inner {
  //   width: 306px;

  //   height: 30px !important;
  // }
  // .el-cascader__search-input {
  //   background-color: #fafafa;
  // }
 
}


</style>









