<template>
  <div class="app-container" id="carParkingBySchedule">
    <div class="filter-container">
      <div class="selectBox">
        <el-form :inline="true" class="mdElForm" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">

          <el-form-item prop="selectDate">
            <el-date-picker class="filter-item" v-model="mdForm.selectDate" align="right" type="date" @change="getScheduleList()" value-format="timestamp" placeholder="选择日期" :clearable="false" :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>

          <el-form-item prop="idIdentify">
            <el-select filterable v-model="mdForm.idIdentify" @change='changeSelect' class="filter-item" placeholder="请选择" multiple collapse-tags>
              <el-checkbox class="selectAll" v-model="checked" @change='selectAll'>全选</el-checkbox>
              <el-option v-for="item in scheduleOptions" :key="item.idIdentify" :label="item.showName" :value="item.idIdentify" :disabled="item.statusDisabled" />
            </el-select>
          </el-form-item>
          <el-form-item>

            <el-button class="filter-item" type="primary" @click="popShow" icon="el-icon-search">搜索</el-button>

            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>

    </div>
    <div class="tableBox" v-loading="listLoading">

      <div v-for="(item,index) in taskList" :key="index" class="roadelistBox">
        <div >{{ formateName(index)}}</div>
        <div class="step">
          <el-steps align-center>
            <el-step :title="i.destinationName" :description="i.parkTime"  :class="{active:i.active}" :icon="i.active?'el-icon-success':'el-icon-circle-close'" v-for="i in item" :key="i.id"></el-step>
          </el-steps>
        </div>

      </div>

    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination

import { progresssStatistics } from '../../api/progress'
import { actuallyByScheduling } from "../../api/schedule/schedule";
import { queryActually } from "../../api/schedule/schedule";
import { carparkingHistory } from "../../api/inOut/carParkingRecord";//出入库记录列表
import { formatDate } from "../../../src/common"; //时间格式化



export default {
  name: "progress1",
  components: { Pagination },
  data() {
    let multiRule = (rule, value, callback) => {
      // 多设备验证
      let cars = this.mdForm.idIdentify
      if (cars.length < 1) {
        callback(new Error("请选择排班"));
      } else {
        callback();
      }
    };
    //时间验证
    let timeRule = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请选择"));
      } else {
        callback();
      }

    };
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: [
      ],
      list1: [],
      total: 0,
      listLoading: false,
      listQuery: {
        selectDate: new Date().setHours(0, 0, 0, 0)
      },


      textMap: {
        update: "编辑",
        create: "添加"
      },
      taskList: [],

      activeName: 'first',
      pickerOptions: {
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      },
      mdRule: {
        idIdentify: [{ validator: multiRule, trigger: "change", required: true }],
        selectDate: [{ trigger: "change", message: "请选择时间", validator: timeRule }],
      },
      mdForm: {
        idIdentify: [],
        selectDate: new Date().setHours(0, 0, 0, 0)
      },
      scheduleOptions: [],
      // destinationsObj: {},
      destinationId: null,
      idIdentifyArr: [],
      checked: false,
      intervalId: null,
      destinations: [],
      des: [],

    };
  },
  created() {
    if (sessionStorage.destinationId) {
      this.destinationId = sessionStorage.destinationId;
    }
    // this.getList(); //初始化获取列表
    this.getScheduleList('created');
    // this.dataRefreh()

  },
  beforeDestroy() {
    sessionStorage.removeItem('destinationId')
    this.clear();
  },
  methods: {
     //回放功能
    // playBack(row) {
    //   let start = "00:00:00",
    //   end = "23:59:59",
    //   year=row[0].taskYear,
    //   month=row[0].taskMonth> 9 ? row[0].taskMonth : "0" + row[0].taskMonth,
    //   day=row[0].taskDay> 9 ? row[0].taskDay : "0" + row[0].taskDay,
    //   imei,
    //   routeId,
    //   networks;


    //   this.$router.push({
    //     name: `historicalPlayback`,
    //     query: {
    //       imei: row.iMei,
    //       type: "car",
    //       year: year+'-'+month+'-'+day,
    //       start: start,
    //       end: end
    //     }
    //   });
    // },
    formatDate,
    // 定时刷新数据函数
    dataRefreh() {
      // 计时器正在进行中，退出函数
      if (this.intervalId != null) {
        return;
      }
      // 计时器为空，操作
      this.intervalId = setInterval(() => {
        this.getScheduleList();
      }, 20000);
    },
    // 停止定时器
    clear() {
      clearInterval(this.intervalId); //清除计时器
      this.intervalId = null; //设置为null
    },
    changeSelect(val) {
      if (val.length === this.scheduleOptions.length) {
        this.checked = true
      } else {
        this.checked = false
      }
    },
    selectAll() {
      this.mdForm.idIdentify = []
      if (this.checked) {
        this.scheduleOptions.map((item) => {
          this.mdForm.idIdentify.push(item.idIdentify)
        })
      } else {
        this.mdForm.idIdentify = []
      }
    },

    formateName(index) {

      for (let j = 0; j < this.scheduleOptions.length; j++) {
        if (this.idIdentifyArr[index] == this.scheduleOptions[j].idIdentify) {
          return this.scheduleOptions[j].showName;
        }
      }
    },
    formateCarNo(index) {

      for (let j = 0; j < this.scheduleOptions.length; j++) {
        if (this.idIdentifyArr[index] == this.scheduleOptions[j].idIdentify) {
          return this.scheduleOptions[j].carLicenseNo;
        }
      }
    },


    //获取列表
    getScheduleList(created) {
      
      queryActually({
        times: [this.mdForm.selectDate]
      })
        .then(res => {
          if (res.status == 0) {
            this.scheduleOptions = [];
            this.mdForm.idIdentify = [];
            this.taskList=[]
            if (res.data) {
              for (let index in res.data) {
                for (let i = 0; i < res.data[index].length; i++) {
                  let imteI = res.data[index][i]
                  res.data[index][i].sortDate = parseInt(index);
                  res.data[index][i].showName = res.data[index][i].routeName + '-' + res.data[index][i].carLicenseNo + '-' + (res.data[index][i].type == 1 ? '晚收' : '早送')
                  if (res.data[index][i].status == 2 || res.data[index][i].status == 4) {
                    res.data[index][i].statusDisabled = true;
                  }
                  if (this.destinationId && res.data[index][i].destinationId == this.destinationId && this.mdForm.idIdentify.indexOf(this.destinationId) == -1) {
                    this.mdForm.idIdentify.push(res.data[index][i].idIdentify)
                  }
                }

                this.scheduleOptions = this.scheduleOptions.concat(res.data[index]);

              }
              this.scheduleOptions.sort((a, b) => {
                return a.sortDate - b.sortDate
              })
              //初始化时上午默认显示早送，下午2点开始默认显示晚收
              if (created == 'created') {
                for (let k = 0; k < this.scheduleOptions.length; k++) {
                  if (new Date().getHours() > 14 && this.scheduleOptions[k].type == 1) {
                    this.mdForm.idIdentify.push(this.scheduleOptions[k].idIdentify)
                  } else if (new Date().getHours() <= 14 && this.scheduleOptions[k].type == 2) {
                    this.mdForm.idIdentify.push(this.scheduleOptions[k].idIdentify)
                  }
                }
              }else{
                 for (let k = 0; k < this.scheduleOptions.length; k++) {
                  
                    this.mdForm.idIdentify.push(this.scheduleOptions[k].idIdentify)
                  
                }
              }
              if (this.mdForm.idIdentify.length&&created == 'created') {
                this.popShow();
              }



            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {

        });
    },

    getList() {
      let that = this;
      this.listLoading = true;
      this.list = [];
      this.list1 = [];
      progresssStatistics({
        date: this.listQuery.selectDate,
        type: 2,
      })
        .then(res => {
          if (res.status == 0) {
            this.list = this.dealData(res.data);
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          if (this.activeName == 'first') {
            that.listLoading = false;

          }
        });
      progresssStatistics({
        date: this.listQuery.selectDate,
        type: 1,
      })
        .then(res => {
          if (res.status == 0) {
            this.list1 = this.dealData(res.data);
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          if (this.activeName == 'second') {
            that.listLoading = false;

          }
        });
    },
    dealData(arr) {
      for (let i = 0; i < arr.length; i++) {

        arr[i] = Object.assign(arr[i], arr[i].schedulingEntityDto)
      }
      return arr;
    },
    popShow() {

      let _t = this, reqArr = [];
      this.taskList = [];
      this.listLoading = true;
      this.idIdentifyArr = [...this.mdForm.idIdentify];
      for (let j = 0; j < this.mdForm.idIdentify.length; j++) {
        let p1 = new Promise((resolve, reject) => {
          actuallyByScheduling({ uuid: this.mdForm.idIdentify[j] })
            .then(res => {
              if (res.status == 0) {
                resolve(res.data);
              } else {
                _t.taskList = [];
                _t.$message.error(res.msg);
              };
            })
            .finally(() => {
            });
        })
        reqArr.push(p1);
      }
      if (reqArr) {
        Promise.all(reqArr).then((tasks) => {
          this.taskList = [...tasks];
          this.getDestination();
        }).finally(() => {
        this.listLoading = false;
          

        })
      }


    },
    getDestination() {
      let reqArr1 = [];
      let destinations = [];
      for (let j = 0; j < this.scheduleOptions.length; j++) {
        if (this.mdForm.idIdentify.indexOf(this.scheduleOptions[j].idIdentify) == -1) {
          continue;
        }
        let carNo = this.scheduleOptions[j].carLicenseNo;
        let p2 = new Promise((resolve, reject) => {
          carparkingHistory({
            time: this.mdForm.selectDate,
            carNo: carNo
          })
            .then(res1 => {
              if (res1.status == 0) {
                if (res1.data.length) {
                  res1.data.carNo = carNo;
                }
                for (let i = 0; i < res1.data.length; i++) {
                  res1.data[i].carNo = carNo;
                }

                resolve(res1.data);

              } else {
              }
            })
            .finally(() => {

            });
        })
        reqArr1.push(p2);

      }

      if (reqArr1.length) {

        Promise.all(reqArr1).then((des) => {
          destinations = des.flat(Infinity);
          this.taskList.forEach((item, index) => {
            item.forEach((item1, index1) => {
              destinations.forEach((item2, index2) => {
                let hasPark = false;
                if (item1.destinationId == item2.destinationId && item2.licenseNo == item1.carLicenseNo && item1.type == 2 && new Date(item2.createTime).getHours() <= 14) {
                  hasPark = true;
                } else if (item1.destinationId == item2.destinationId && item2.licenseNo == item1.carLicenseNo && item1.type == 1 && new Date(item2.createTime).getHours() > 14) {
                  hasPark = true;
                }
                if (hasPark) {
                  if (item1.active && new Date(item2.createTime).getHours() < new Date(item1.parkTime).getHours()) {
                  this.$set(item1,'parkTime', this.formatDate(item2.createTime,1))

                  } else if (!item1.active) {
                  this.$set(item1,'parkTime',this.formatDate(item2.createTime,1))

                  }
                  this.$set(item1,'active',true)

                }

              })
            })
          })
        }).finally(() => {
          this.listLoading = false;

        })

      }
    },


  }
};
</script>
<style lang="scss">
#carParkingBySchedule {
  .filter-container {
    .el-select,
    .el-form-item {
      .el-input__inner {
        width: 300px !important;
      }
      width: 300px !important;
    }
  }

  .tableBox {
    padding: 20px;
    overflow: auto;
    flex-direction: column;
    justify-content: space-around;
    height: calc(100vh - 220px) !important;
    .leftBox {
      padding: 20px;
    }
    .outBox {
      margin-top: 70px;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
    }
    .roadelistBox {
      display: flex;
      min-height: 200px;
      width: 100%;
      & > div:first-child {
        text-align: center;
        font-size: 16px;
        min-width: 200px;
      }
      .step {
        display: flex;
        flex-grow: 1;
      }
    }

    .active {
      .el-step__title,
      .el-step__description {
        color: #31478f !important;
      }
    }
    .el-step__icon .el-icon-success {
      color: #67c23a;
    }
  }
  padding: 0;
  .el-tabs__header {
    background: #eaecf2;
  }
  .el-tabs__item {
    background: white;
  }
  .filter-container {
    height: 70px;
    border: 0px;
  }
  .oneday {
    display: inline-flex;
    align-items: center;
    .routeName,
    .onedayText {
      height: 50px;
      line-height: 50px;
      padding: 0 10px;
    }
    .routeName {
      width: 150px;
      text-align: right;
      overflow: hidden;
      word-break: keep-all;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }

  .progress {
    width: 416px;
    height: 50px;
    .el-progress-bar__outer {
      background: #c9c9c9;
      height: 50px !important;
    }
  }

  .onedayText {
    width: 60px;
    border: 1px solid #ccc;
  }
  .inAndOut {
    display: inline-block;
    border: 1px solid #32be3f;
    border-radius: 10px;
    color: #32be3f;
    width: 115px;
    height: 36px;
    line-height: 36px;
  }
  .allout {
    background-color: #ccc;
  }
  .el-progress-bar__outer,
  .el-progress-bar__inner {
    border-radius: 0;
  }
  .el-progress-bar__inner {
    .el-progress-bar__innerText {
      margin: 0;

      color: #000000;
      font-size: 18px;
    }
  }
  .el-tabs {
    border: 0;
    &.el-tabs--card > .el-tabs__header .el-tabs__nav {
      border: 0;
    }
    .el-tabs__item {
      min-width: 100px;
      border: 0;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      text-align: center;
      &.is-active {
        background: #31478f;
        color: white;
      }
    }
  }
}
.progressNetwork {
  div {
    margin: 10px 0;
  }
  div:not(.colorItem) {
    color: #cccccc;
  }
}

.colorItem {
  color: #ffc323;
}
.colorCancle {
  text-decoration: line-through;
}
.selectAll.el-checkbox {
  padding-right: 10px;
  margin-left: 20px;
}
</style>









