import request from '../request'
//枪支出入库列表
export function gunInOutList(data) {
  return request({
    url: '/eoms/gunHandovers/page',
    method: 'post',
    data

  })
}

//车辆出入库列表
export function carInOutList(data) {
  return request({
    url: '/eoms/carHandovers/page',
    method: 'post',
    data

  })
}

