<template>
  <div id="carInOutProcedure" class="app-container" :class="{adminClass:(power!==1&&power!==2)}">
    <div class="contenBox">
      <el-card class="box-card headerBox">

        <div slot="header" class="clearfix">
          <!-- <span>扫描动态</span> -->
          <div class="showText" :class="{activeColor:finish==1}">{{showText}}</div>
        </div>
        <!-- <div class="personMes toggleContent1 bottomText" :class="{activeColor:finish==1}">
          {{showText}}

        </div> -->
      </el-card>
      <div class="box1">
        
        <div class="box2">
          <el-card class="box-card">
        <div slot="header" class="clearfix">
          <div>扫描流程</div>
          <!-- <div class="showText" :class="{activeColor:finish==1}">{{showText}}</div> -->
          <!-- <el-button @click="restart">重新开始流程</el-button> -->

        </div>
        <div class="personMes toggleContent1 tableBox" v-show="inOutType==0||inOutType==1||inOutType==2">
          <el-table :key="tableKey" v-loading="listLoading" :data="procedureList" fit highlight-current-row :row-class-name="tableRowClassName">
            <el-table-column label="职位" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{scope.row.item}}</span>
              </template>
            </el-table-column>
            <el-table-column label="姓名" width="100px" align="center">
              <template slot-scope="scope">
                <span>{{scope.row.itemName}}</span>
              </template>
            </el-table-column>
            <el-table-column label="刷卡时间" min-width="150px" align="center">
              <template slot-scope="scope">
                <span>{{scope.row.time}}</span>
              </template>
            </el-table-column>
            <el-table-column label="完成状态" min-width="80px" align="center">
              <template slot-scope="scope">
                <i class="el-icon-success" :class="{activeColor:(scope.row.status==1?true:false)}"></i>
              </template>
            </el-table-column>
          </el-table>
          <!-- <div class="personDetail">

            <div class="photo">
              <img :src="personDetail.taskSrc" alt="暂无图片">
            </div>
            <div class="detailText">
              <div>姓名：{{personDetail.name}}</div>
              <div>性别：{{personDetail.sex==1?'男':personDetail.sex==null?'':'女'}}</div>
              <div>年龄：{{personDetail.age}}</div>
            </div>
          </div> -->
        </div>

      </el-card>
      <el-card class="box-card scheduleBox">
        <div slot="header" class="clearfix">
          <span>排班信息</span>
        </div>
        <div class="personMes toggleContent1 tableBox " v-show="inOutType==0||inOutType==1||inOutType==2">
          <el-table :key="tableKey" v-loading="listLoading" :data="scheduleList" fit highlight-current-row :row-class-name="tableRowClassName">
            <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
            <el-table-column label="司机" min-width="100px" align="center" prop="driverName">
              <template slot-scope="scope">
                <span>{{ scope.row.driverName }}</span>
              </template>
            </el-table-column>
            <el-table-column label="车辆" min-width="100px" align="center">
              <template slot-scope="scope">
                <span>{{ scope.row.carLicenseNo }}</span>
              </template>
            </el-table-column>
            <el-table-column label="路线" min-width="100px" align="center" prop="listProp">
              <template slot-scope="{row}">
                <span>{{ row.routeName }}</span>
              </template>
            </el-table-column>
            <el-table-column label="排班类型" min-width="80px" align="center" prop="listProp">
              <template slot-scope="scope">
                <span>{{  formateType(scope.row.type) }}</span>
              </template>
            </el-table-column>

            <el-table-column label="状态" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{ (scope.row.status==1||scope.row.status==3||scope.row.status==5)?'正常':'取消' }}</span>

              </template>
            </el-table-column>
            <el-table-column label="车辆状态" min-width="80px" align="center">
              <template slot-scope="scope">
                <span>{{ getSwipeStatusText1(swipeStatusObj[scope.row.carId]) }}</span>

              </template>
            </el-table-column>

            <el-table-column label="排班日期" min-width="150px" align="center" prop="sortDate">
              <template slot-scope="scope">
                <span>{{ scope.row.planYear+'-'+scope.row.planMonth+'-'+scope.row.planDay }}</span>
              </template>
            </el-table-column>

          
            <el-table-column label="操作" align="center" min-width="150" fixed="right" class-name="small-padding fixed-width" v-if="procedureList[1].itemName&&finish==0">
              <template slot-scope="{row}">
                <el-button type="primary" size="mini" :disabled="(row.status==2||row.status==4)||(swipeStatusObj[row.carId]!==0&&swipeStatusObj[row.carId]!==1)" @click="handleInOut({
                adminId: procedureList[1].linkId,
                carId: row.carId,
                driverId: procedureList[0].linkId
              })">{{swipeStatusObj[row.carId]==0?'领取':'归还'}}</el-button>

              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-card>
      <el-card class="box-card" v-show="haveforce">

        <div slot="header" class="clearfix">
          <span class="red">其他未归还车辆<em v-show="procedureList[1].itemName&&finish==0">(管理员刷卡后可点击以下按钮强制归还该车)</em></span>
        </div>
        <div class="personMes toggleContent1 noReturn">
          <div class="returnBtn">
            <el-button :disabled="!(procedureList[1].itemName&&finish==0)" v-for='(item,index) in unReturnCarNo' @click="handleInOut({carId:unReturnCarId[index],driverId:procedureList[0].linkId,adminId:procedureList[1].linkId})"> {{item}}</el-button>

          </div>

        </div>
      </el-card>
        </div>
           <div class="personDetail">
 <div class="detailText">
              {{personDetail.name}}
            </div>
            <div class="photo">
              <img :src="personDetail.taskSrc" alt="暂无图片">
            </div>
           
          </div>
      </div>

    

    </div>
    <el-dialog title="提示" :visible.sync="centerDialogVisible" width="30%" :modal="true" center>
      <div style="text-align:center;font-size:20px;padding-bottom:20px">{{dialogText}}</div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
      <span>{{forceText}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="forceSubmit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { queryR } from "../../api/inOut/rfid";
import { queryByWorker,queryTaskByPersonId ,queryActuallyByIdIdentify} from "../../api/schedule/schedule";
import { carDetail } from "../../api/managementArchives/vehicleArchives";
import { personsDetail } from "../../api/managementArchives/personnelFiles";

import { carPerform, carCondition } from "../../api/inOut";

import { formatDate } from "../../../src/common"; //格式化时间
import {
  imgDownload, photoInfosQuery
} from "../../api/schedule/schedule";
import axios from 'axios'
let myLoading = undefined; // 弹窗服务调用


export default {
  name: "carInOutProcedure",
  data() {

    return {
      inOutType: 0,
      options: [
        { key: "库包出入库之业务员刷卡", value: 0 },
        { key: "库包出入库之扫描库包", value: 1 },
        { key: "库包出入库之签字确认成功", value: 2 },
        { key: "车辆出入库之司机刷卡", value: 3 },
        { key: "车辆出入库之门卫刷卡", value: 4 },
        { key: "枪弹出入库之押运员刷卡", value: 5 },
        { key: "枪弹出入库之枪管刷卡", value: 6 },
      ],
      tableKey: 1,
      listLoading: false,
      procedureList: [
        { item: '司机' },
        { item: '管理员' }

      ],
      scheduleList: [],
      indexMethod: 1,
      show: false,
      someName: '',
      rfid: null,//最新的刷卡的rfid
      realRfid: null,//真实的已经获取到数据的rfid
      cancelRequest1: null,
      cancelRequest2: null,
      cancelRequest3: null,


      finish: 0,
      wsCar: null,
      wsArr:[],

      showText: '等待司机刷卡中......',
      centerDialogVisible: false,
      dialogText: '',
      dialogTimer: null,
      sameCar: true,
      gunScheduleDone: false,
      carDetailReqCancelRequest: {},
      swipeStatusObj: {},
      personDetail: {},
      forceText: '',
      dialogVisible: false,
      haveforce: false,
      unReturnCarNo: [],
      unReturnCarId: [],
      power: null,


    };
  },
  created() {
    this.power = JSON.parse(this.$store.state.user.userInfo).Power;
  },
  computed: {

  },
  mounted() {
    this.webSocketConnect();
  },
  beforeDestroy() {
    if (this.wsCar) {
      this.wsCar.close();
    }
    for(let i=0;i<this.wsArr.length;i++){
       if (this.wsArr[i]) {
      this.wsArr[i].close();
      this.wsArr[i]=null;
      }
    }
  },
  methods: {
      formateType(type){
      let text=''
    switch(type){
      case 1:
        text='晚收'
        break;
         case 2:
        text='早送'
        break;

         case 5:
        text='加钞'
        break;

         case 7:
        text='收款'
        break;

        
    }
    return text;

    },
    speak(text) {


      let msg = new SpeechSynthesisUtterance(text)

      msg.volume = 100  // 声音的音量

      msg.rate = 1 // 语速，数值，默认值是1，范围是0.1到10

      msg.pitch = 1.5 // 表示说话的音高，数值，范围从0（最小）到2（最大）。默认值为1

      speechSynthesis.speak(msg)
    },
    webSocketConnect() {
      let protojs = require("../../proto.js"), that = this;
      // this.wsCar = new WebSocket("ws://127.0.0.1:22345");
      // this.wsCar = new WebSocket("ws://127.0.0.1:22345");


      // this.wsCar.binaryType = "arraybuffer";
      // switch (this.wsCar.readyState) {
      //   case WebSocket.CONNECTING:
      //     console.log("正在连接");
      //     break;
      //   case WebSocket.OPEN:
      //     console.log("连接成功");
      //     break;
      //   case WebSocket.CLOSING:
      //     console.log("正在关闭");
      //     break;

      //   case WebSocket.CLOSED:
      //     console.log("已经关闭，或者打开连接失败");
      //     break;
      //   default:
      //     // this never happens
      //     break;
      // }

      // this.wsCar.onerror = function (params) {
      //   console.log('error')
      // };
      // this.wsCar.onclose = function () {
      //   console.log('close')

      // };
      // this.wsCar.onopen = function () {

      // };
      // this.wsCar.onmessage = function (event) {
      //   if (event.data instanceof ArrayBuffer) {
      //     let arraybuffer = event.data;
      //     let dataView = new DataView(arraybuffer.slice(0, 8));
      //     let int32_length = dataView.getInt32(0);
      //     // let int32_cmd = dataView.getInt32(4);
      //     let locationInfo = null;
      //     let source = new Uint8Array(arraybuffer.slice(8, int32_length + 4));
      //     locationInfo = protojs.lookupType(
      //       "com.beidou.proto.local.RealTimeRfidInfoResponse"
      //     )

      //     let message = locationInfo.decode(source);
      //     that.dealWebSocketData(message.rfid);
      //   }


      // };


    //    this. findIP(function(ip){
    //   //  console.warn(ip)
    //  that.wsInit("ws://"+ip+":22345",protojs)

    // })
     this.wsInit("ws://127.0.0.1:22345",protojs)
    // this.wsInit("ws://192.168.1.217:22345",protojs)
    },
    findIP(callback) {
        var myPeerConnection = window.RTCPeerConnection || window.mozRTCPeerConnection || window.webkitRTCPeerConnection; //compatibility for firefox and chrome
        var pc = new myPeerConnection({iceServers: []}),
            noop = function() {},
            localIPs = {},
            ipRegex = /([0-9]{1,3}(\.[0-9]{1,3}){3}|[a-f0-9]{1,4}(:[a-f0-9]{1,4}){7})/g,
            key;

        function ipIterate(ip) {
            if (!localIPs[ip]) callback(ip);
            localIPs[ip] = true;
        }
        pc.createDataChannel("");
        pc.createOffer().then(function(sdp) {
            sdp.sdp.split('\n').forEach(function(line) {
                if (line.indexOf('candidate') < 0) return;
                line.match(ipRegex).forEach(ipIterate);
            });
            pc.setLocalDescription(sdp, noop, noop);
        });
        pc.onicecandidate = function(ice) {
            if (!ice || !ice.candidate || !ice.candidate.candidate || !ice.candidate.candidate.match(ipRegex)) return;
            ice.candidate.candidate.match(ipRegex).forEach(ipIterate);
        };
    },
      wsInit(wsUrl,protojs){
      let wsCar=new WebSocket(wsUrl),that=this;
      this.wsArr.push(wsCar);
      wsCar.binaryType = "arraybuffer";
      switch (wsCar.readyState) {
        case WebSocket.CONNECTING:
          console.log("正在连接");
          break;
        case WebSocket.OPEN:
          console.log("连接成功");
          break;
        case WebSocket.CLOSING:
          console.log("正在关闭");
          break;

        case WebSocket.CLOSED:
          console.log("已经关闭，或者打开连接失败");
          break;
        default:
          // this never happens
          break;
      }

      wsCar.onerror = function (params) {
        console.log('error')
      };
      wsCar.onclose = function () {
        console.log('close')

      };
      wsCar.onopen = function () {

      };
      wsCar.onmessage = function (event) {

         if (event.data instanceof ArrayBuffer) {
          let arraybuffer = event.data;
          let dataView = new DataView(arraybuffer.slice(0, 8));
          let int32_length = dataView.getInt32(0);
          // let int32_cmd = dataView.getInt32(4);
          let locationInfo = null;
          let source = new Uint8Array(arraybuffer.slice(8, int32_length + 4));
          locationInfo = protojs.lookupType(
            "com.beidou.proto.local.RealTimeRfidInfoResponse"
          )

          let message = locationInfo.decode(source);
          that.dealWebSocketData(message.rfid);
        }
      };
    },
    dealWebSocketData(rfid) {
      if (this.procedureList[1].rfid && this.finish == 0) {
        return;
      }
      if (this.rfid === rfid) {
        return; //如果是重复的rfid不作任何处理
      };
      this.rfid = rfid;
      if (this.gunScheduleDone == true) {//已经获取到司机的排班信息
        this.gunScheduleDone = false;
        if (this.procedureList[0].rfid == rfid) {
          return;
        }
      }
      if (rfid && this.finish == 0 && (rfid == this.procedureList[0].rfid || rfid == this.procedureList[1].rfid)) {

        this.showDialog('已刷卡,请勿重复刷卡');
        return;
      }

      if (this.finish == 0 && this.procedureList[0].itemName && this.procedureList[1].itemName) {
        this.showDialog('无效刷卡，当前流程还未完成');
        return;
      }
      this.queryByRfid(this.rfid);

    },
    restart() {
      this.procedureList = [{ item: '司机' }, { item: '管理员' }];
      this.scheduleList = [];
      this.finish = 0;
      this.sameCar = true;
      this.showText = "等待司机刷卡中......"
      // this.rfid = null;
      this.personDetail = {};
      this.haveforce = false;
      this.unReturnCarId = [];
      this.unReturnCarNo = [];
      this.swipeStatusObj = {};


    },
    formatDate1(time) {
      return formatDate(time, 1)
    },
    queryByRfid(rfid) {
      if (this.cancelRequest1) {
        this.cancelRequest1();
        this.cancelRequest1 = null;
      }
      queryR({
        rfid: this.rfid,
      }, new axios.CancelToken((c) => {
        this.cancelRequest1 = c    //保存当前请求
      }))
        .then(res => {
          if (res.status == 0) {
            // console.warn(res.data);
            //如果司机还没有刷卡则提醒此次刷卡无效
             if (res.data&&res.data.length==0) {
              this.showDialog('未查询到信息');
              return;
            }
            let resData = res.data[0];
            if (!resData.linkId) {
              this.showDialog('该卡无身份信息');
              return;
            }
            if (resData.workTypes == null) {
              resData.workTypes = [];
            }
            if (resData.workTypes.indexOf(1) == -1) {
              // 非司机刷卡

              if (this.procedureList[0].rfid && (this.scheduleList.length == 0) && (!this.cancelRequest2 && !this.cancelRequest3)&&this.haveforce==false) {
                this.showDialog('刷卡流程错误，请下一位司机刷卡');
                return;
              }
              //排班或者是领取或归还状态或者是查询强制归还车辆信息还未获取到
              if (this.procedureList[0].rfid && (this.scheduleList.length == 0) && (this.cancelRequest3 || this.cancelRequest2 || this.getCancelRequestObj())) {
                return;
              }
            }
            if (resData.workTypes.indexOf(1) !== -1) {
              this.staffSwipe(resData, rfid);
            } else if (resData.workTypes.indexOf(7) == -1 && resData.workTypes.indexOf(1) == -1) {
              //除了普通员工，管理员以外的其他rfid类型刷卡
              this.showDialog('身份错误');
              return;
            } else if (resData.workTypes.indexOf(7) !== -1) {
              //车管刷卡
              if (!this.procedureList[0].itemName) {
                this.showDialog('刷卡流程错误');
                return;
              }
              if (this.procedureList[1].rfid && this.finish == 1) {
                this.showDialog('上个流程已完成，请司机刷卡');
                return;
              }
              if (this.procedureList[0].status == 0) {
              if(!this.scheduleList.length&&this.haveforce==false){
              this.showDialog('无任务或不存在需归还车辆，请下一位司机刷卡');
              return;
              }
                this.showDialog('刷卡流程错误');
                return;
              }

              this.adminSwipe(resData, rfid);


            }

          } else {
            this.$message.error(res.msg);
          }
        }).catch(error => {
        })
        .finally(() => {
          this.cancelRequest1 = null;

        });
    },
    getCancelRequestObj() {
      for (item in this.carDetailReqCancelRequest) {
        if (this.carDetailReqCancelRequest[item]) {
          return true;
        }
      }
    },
    getSwipeStatusText(status) {
      //实际上枪支列表当前出入库状态0是归还，1是领取，但是来领还枪的时候提示语就应该正好相反
      let text = '';
      switch (status) {
        case 0:
          text = '领取';
          break;
        case 1:
          text = '归还';
          break
        case 2:
          text = '维修';
          break
        case 3:
          text = '保养中';
          break
        case 4:
          text = '报废';
          break
      }
      return text;

    },
    getSwipeStatusText1(status) {
      //实际上枪支列表当前出入库状态0是归还，1是领取，但是来领还枪的时候提示语就应该正好相反
      let text = '';
      switch (status) {
        case 0:
          text = '入库';
          break;
        case 1:
          text = '出库';
          break
        case 2:
          text = '维修';
          break
        case 3:
          text = '保养中';
          break
        case 4:
          text = '报废';
          break
      }
      return text;

    },
    //普通员工刷卡
    staffSwipe(resData, rfid) {
      let changeObj = {}, that = this;
      this.personDetail = {}
      //司机刷卡停止查询是否存在需要强制归还车辆
      if (this.cancelRequest3) {
        this.cancelRequest3();
        this.cancelRequest3 = null;

      }
      //司机刷卡停止之前的排班请求，排班和流程表清空
      if (this.cancelRequest2) {
        this.cancelRequest2();
        this.cancelRequest2 = null;
        for (item in this.carDetailReqCancelRequest) {
          if (this.carDetailReqCancelRequest[item]) {
            this.carDetailReqCancelRequest[item]()
          }
        }
        this.carDetailReqCancelRequest = {};
      }



      this.finish = 0;
      this.sameCar = true;
      this.scheduleList = [];

      this.procedureList = [{ item: '司机' }, { item: '管理员' }]
      changeObj = {
        item: '司机',
        itemName: resData.linkName,
        time: this.formatDate1(new Date().getTime()),
        status: 0,
        linkId: resData.linkId,
        rfid: resData.rfid

      }
      this.procedureList.splice(0, 1, changeObj)
      this.showText = this.procedureList[0].itemName + '刷卡';
      this.speak(this.showText)

      //先获取排版信息，再根据排班的车辆获取每个车辆是领取还是归还
      // let p1 = this.querySchedule({ personId: resData.linkId, time: new Date().getTime() })
      let p0=this.queryTask({ personId: resData.linkId}),p1;
      p0.then((value0)=>{
      if(value0.length){
      p1 = this.queryScheduleByidIdentifys({ idIdenties:value0 });
       p1.then((value) => {
        //查询是否有需要归还的车辆
        let p3 = this.forceUpdate(resData.linkId);
        p3.then((res) => {
          that.unReturnCarId = res.unReturnCarId
          that.unReturnCarNo = res.unReturnCarNo
          if (value.length) {
            let obj1 = {}, arr = [];

            for (let i = 0; i < value.length; i++) {
              arr.push(that.carSwipeStatus(value[i]));
            }
            Promise.all(arr).then((values) => {
              values.forEach((item) => {
                obj1 = { ...obj1, ...item }
              })
              that.swipeStatusObj = { ...obj1 };

              for (let j = that.unReturnCarId.length - 1; j >= 0; j--) {
                for (let k = 0; k < value.length; k++) {
                  if (that.unReturnCarId[j] == value[k] && obj1[value[k]] == 1) {
                    that.unReturnCarId.splice(j, 1);
                    that.unReturnCarNo.splice(j, 1);
                  }
                }
              }
              //以下需要获取车辆详细信息知道每个车辆是领取还是归还
              that.$set(that.procedureList[0], 'status', 1);
              if (value.length && !that.unReturnCarId.length && value.length == 1) {

                that.showText = that.procedureList[0].itemName + '刷卡成功,等待' + that.getSwipeStatusText(that.swipeStatusObj[value[0]]) + '钥匙中......';
              }
              if (that.unReturnCarId.length) {
                that.haveforce = true;
              } else {
                that.haveforce = false;

              }
              that.gunScheduleDone = true;
            }).catch((err) => {
              that.showText = '获取车辆领取或归还状态信息失败！'
              this.speak(this.showText)

            })


          } else {
            if (that.unReturnCarId.length) {

              that.haveforce = true;
            } else {
              that.haveforce = false;

            }
          }


        })

      });
      }else{
       this.showDialog('无效刷卡，未获取到任务信息,请下一个司机刷卡')
      }
      })
      

      let p2 = this.queryGun(resData.linkId);

     
      //获取人员照片
      p2.then((res) => {
        that.queryPhoto(res).then((res1) => {
          that.showPhoto(res1);
        });
      }).catch(err => {

      })





    },
    forceUpdate(driverId) {
      return new Promise((resolve, reject) => {
        carCondition({ driverId: driverId }, new axios.CancelToken((c) => {
          this.cancelRequest3 = c    //保存当前请求
        }))
          .then(res => {
            if (res.status == 0) {



            } else {
            }

            resolve(res.data);

          }).catch(error => {
          })
          .finally(() => {
            this.cancelRequest3 = null;
          });
      });
    },

    //管理员刷卡
    adminSwipe(resData, rfid) {
      let changeObj = {};
      //如果司机已经刷卡，则显示管理员信息
      changeObj = {
        item: '管理员',
        itemName: resData.linkName,
        time: this.formatDate1(new Date().getTime()),
        status: 1,
        linkId: resData.linkId,
        rfid: resData.rfid
      }
      this.procedureList.splice(1, 1, changeObj);
      if (this.sameCar == true && this.haveforce == false) {
        this.showText = '钥匙刷卡成功，管理员已确认，提交中......'

      } else {
        this.showText = '管理员刷卡成功，请点击按钮确认归还或领取......'
        this.speak(this.showText)

      }

      if (this.procedureList[1].itemName && this.finish == 0 && this.sameCar == true && this.haveforce == false) {
        this.handleInOut({
          adminId: this.procedureList[1].linkId,
          carId: this.scheduleList[0].carId,
          driverId: this.procedureList[0].linkId
        })
      }
    },
    judgeSame(obj) {
      let isSame = true;
      for (let i = 0; i < obj.length - 1; i++) {
        if (obj[i].carLicenseNo !== obj[i + 1].carLicenseNo) {
          isSame = false;
        }
      }
      this.sameCar = isSame;
      return isSame;
    },
    getCar(sList) {
      let arr = [];
      for (let i = 0; i < sList.length; i++) {
        if (arr.indexOf(sList[i].carId) == -1) {
          arr.push(sList[i].carId);
        }
      }
      return arr;
    },
    // 通过人员详情查询枪支
    queryGun(personId) {
      this.personDetail = {}
      return new Promise((resolve, reject) => {
        personsDetail({ personId: personId })
          .then(res => {
            if (res.status == 0) {


              this.personDetail.name = res.data.name
              this.personDetail.age = res.data.age
              this.personDetail.sex = res.data.sex


            }
            resolve(res.data)
          }).catch(error => {

          })
          .finally(() => {
          });
      });

    },
    // 查询人员照片信息
    queryPhoto(row) {
      return new Promise((resolve, reject) => {
        if (row.photoId == null) {
          return;
        }
        photoInfosQuery({ id: row.photoId, type: [1] })
          .then(res => {
            if (res.status == 0) {
              if (res.data && res.data.length == 0) {
                return;
              }
              resolve(res);

            }
          })
          .catch(() => {
          }).finally(() => {

          });
      });

    },
    // 显示人员照片
    showPhoto(res) {
      return new Promise((resolve, reject) => {

        imgDownload({ path: res.data[0].path, fileName: res.data[0].fileName })
          .then(res1 => {
            this.$set(this.personDetail, "taskSrc", window.URL.createObjectURL(res1))

          })
          .finally(() => {
          });
      });

    },
      queryTask(obj) {
      return new Promise((resolve, reject) => {
        queryTaskByPersonId(obj, new axios.CancelToken((c) => {
          this.cancelRequest2 = c    //保存当前请求
        }))
          .then(res => {
              let idIdentifys=[]

            if (res.status == 0) {
              for (let i = res.data.length - 1; i >= 0; i--) {
                // debugger;

                if (res.data[i].status == 2 || res.data[i].status == 4|| res.data[i].status == 6||res.data[i].driverId!=this.procedureList[0].linkId) {
                  res.data.splice(i, 1);
                }

              }
             
             idIdentifys=Array.from(new Set(res.data.map((item,index)=>{
                return item.idIdentify
              })))


            } else {
              idIdentifys = [];
            }
            resolve(idIdentifys);

          }).catch(error => {
          })
          .finally(() => {
            this.cancelRequest2 = null;
            // this.rfid = null;
          });
      });
    },
    querySchedule(obj) {
      return new Promise((resolve, reject) => {
        queryByWorker(obj, new axios.CancelToken((c) => {
          this.cancelRequest2 = c    //保存当前请求
        }))
          .then(res => {
            if (res.status == 0) {
              for (let i = res.data.length - 1; i >= 0; i--) {
                if (res.data[i].status == 2 || res.data[i].status == 4|| res.data[i].status == 6||res.data[i].driverId!=this.procedureList[0].linkId) {
                  res.data.splice(i, 1);
                }

              }
              this.scheduleList = res.data;
              this.judgeSame(this.scheduleList);


            } else {
              this.scheduleList = [];
            }
            resolve(this.getCar(this.scheduleList));

          }).catch(error => {
          })
          .finally(() => {
            this.cancelRequest2 = null;
            // this.rfid = null;
          });
      });
    },
     queryScheduleByidIdentifys(obj) {
       let that=this;
      return new Promise((resolve, reject) => {
        queryActuallyByIdIdentify(obj, new axios.CancelToken((c) => {
          that.cancelRequest2 = c    //保存当前请求
        }))
          .then(res => {
            if (res.status == 0) {
              that.scheduleList=[];
              for(let item in res.data){
              that.scheduleList.push(res.data[item])
              }
              // for (let i =that.scheduleList.length - 1; i >= 0; i--) {
              //   if (that.scheduleList[i].status == 2 ||that.scheduleList[i].status == 4||that.scheduleList[i].status == 6||that.scheduleList[i].driverId!=that.procedureList[0].linkId) {
              //    that.scheduleList.splice(i, 1);
              //   }

              // }
              that.judgeSame(that.scheduleList);


            } else {
              that.scheduleList = [];
            }
            resolve(that.getCar(that.scheduleList));

          }).catch(error => {
          })
          .finally(() => {
            that.cancelRequest2 = null;
            // this.rfid = null;
          });
      });
    },
    carSwipeStatus(id) {
      return new Promise((resolve, reject) => {
        carDetail({ id: id }, new axios.CancelToken((c) => {
          this.carDetailReqCancelRequest[id] = c;
        }))
          .then(res => {
            if (res.status == 0) {

              resolve({ [id]: res.data.runType });


            }


          }).catch(error => {
          })
          .finally(() => {
            this.carDetailReqCancelRequest[id] = null;

          });
      });
    },
    forceSubmit() {
      this.carPerformSubmit(this.submitData);
      this.dialogVisible = false;
    },
    handleInOut(row) {
      myLoading = this.$loading({ target: "#myDialog .el-dialog" });
      this.carPerformSubmit(row);


    },
    carPerformSubmit(row) {
      carPerform(row)
        .then(res => {
          if (res.status == 0) {
            this.$message.success(res.data);

            this.finish = 1;
            this.showText = this.procedureList[0].itemName + res.data + ',等待下一个司机刷卡';
            this.speak(this.showText)

            // let p1 = this.querySchedule({ personId: this.procedureList[0].linkId, time: new Date().getTime() })
            let p0=this.queryTask({ personId: this.procedureList[0].linkId }),p1;
            p0.then((value0)=>{
            if(value0.length){
            p1 = this.queryScheduleByidIdentifys({ idIdenties:value0 })
              p1.then((value) => {
              //查询刷卡人是否有需要强制归还的车辆
              let p3 = this.forceUpdate(this.procedureList[0].linkId);

              p3.then((res1) => {
                this.unReturnCarId = res1.unReturnCarId
                this.unReturnCarNo = res1.unReturnCarNo
                if (value.length) {
                  let obj1 = {}, arr = [];

                  for (let i = 0; i < value.length; i++) {
                    arr.push(this.carSwipeStatus(value[i]));
                  }
                  Promise.all(arr).then((values) => {
                    values.forEach((item) => {
                      obj1 = { ...obj1, ...item }
                    })
                    this.swipeStatusObj = { ...obj1 };

                    for (let j = this.unReturnCarId.length - 1; j >= 0; j--) {
                      for (let k = 0; k < value.length; k++) {
                        if (this.unReturnCarId[j] == value[k] && obj1[value[k]] == 1) {
                          this.unReturnCarId.splice(j, 1);
                          this.unReturnCarNo.splice(j, 1);
                        }
                      }
                    }

                  }).catch((err) => {


                  })


                } else {
                  if (this.unReturnCarId.length) {

                    this.haveforce = true;
                  } else {
                    this.haveforce = false;

                  }
                }


              })

            });
            }
            })
          

          } else {
            this.scheduleList = [];

          }
        }).catch(error => {
          this.showDialog('提交出错啦');
          this.restart();
        })
        .finally(() => {
          myLoading.close();

        });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }
      return "";
    },
    //消息弹窗内容
    showDialog(text) {
      clearTimeout(this.dialogTimer);
      this.centerDialogVisible = true;
      this.dialogTimer = setTimeout(() => {
        this.centerDialogVisible = false;
      }, 5000)
      this.dialogText = text;
      this.speak(this.dialogText);

    },

    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }
      return "";
    },

  }

};
</script>
