<template>
  <div class="app-container" id="parkRecord">
    <div class="multidevice filter-container">
      <!-- 查找面板 -->
      <!-- <div class="selectBox">
        <el-form :inline="true" class="mdElForm" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
          <el-form-item prop="imeiCar">
            <el-cascader popper-class="myGunCas" ref="myMdGunsCas" v-model="mdForm.imeiCar" placeholder="选择车辆" :props="{expandTrigger:'hover',multiple: true}" :options="dataGun" :show-all-levels="false" clearable filterable collapse-tags>
            </el-cascader>
          </el-form-item>
          <el-form-item prop="searchDate">
            <el-date-picker v-model="mdForm.searchDate" type="date" placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>

            </el-date-picker>
          </el-form-item>
        </el-form>
      </div> -->
    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>

        <el-table-column label="车牌号" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.licenseNo }}</span>
          </template>
        </el-table-column>
        
         <el-table-column label="停车超时地址" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.address }}</span>
          </template>
        </el-table-column>
        <el-table-column label="停车时间" min-width="100px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatDate1(row.offlineTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="离开时间" min-width="100px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.onlineTime?formatDate1(row.onlineTime):row.onlineTime}}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="更新时间" min-width="100px" align="center">
          <template slot-scope="{row}">
            <span>{{ formatDate1(row.updateTime)}}</span>
          </template>
        </el-table-column> -->
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />
    <el-dialog title="导出" :visible.sync="formVisible" append-to-body center>
      <el-form ref="addForm" :model="temp" label-position="left" :rules="rules" class="formStyle">
        <el-form-item prop="imeiCar" label="车辆">
          <el-cascader popper-class="myGunCas1" class="myGunCas1" ref="myMdGunsCas1" v-model="temp.imeiCar" placeholder="选择车辆" :props="{expandTrigger:'hover',multiple: true}" :options="dataGun" :show-all-levels="false" clearable filterable collapse-tags>
          </el-cascader>
        </el-form-item>

      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="exportExcel()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { teamAndCarList } from "../../api/realTimeMonitoring";//车辆列表
import { carOfflinesPage } from "../../api/inOut/parkRecord";//出入库记录列表
import { formatDate } from "../../../src/common"; //格式化时间
import { exportExcelReq } from "../../api/managementArchives/common";

export default {
  name: "parkRecord",
  components: { Pagination },
  data() {
    //设备验证
    let multiRule = (rule, value, callback) => {
      let guns = this.mdForm.imeiCar;
      if (guns.length < 1) {
        callback(new Error("请选择设备"));
      } else {
        callback();
      }

    };
    //车辆导出多设备验证
    let multiRule1 = (rule, value, callback) => {
      let guns = this.temp.imeiCar;
      if (guns.length < 1) {
        callback(new Error("请选择"));
      } else {
        callback();
      }

    };
    //时间验证
    let timeRule = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请选择"));
      } else {
        callback();
      }

    };
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //大队列表
      total: 0, //大队数量
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10
      },
      mdForm: {
        imeiCar: [],
        searchDate: new Date()
      },
      dataGun: [], // 多设备枪,
      imei2name: {}, // 设备的imei和对应的名字
      formVisible: false,
      temp: {
        imeiGun: [],
        searchDate: new Date()
      },
      rules: {
        imeiCar: [{ validator: multiRule1, trigger: "change", required: true }],
        searchDate: [{ trigger: "change", message: "请选择时间", validator: timeRule, required: true }],
      },
      mdRule: {
        imeiCar: [{ validator: multiRule, trigger: "change" }],
        searchDate: [{ trigger: "change", message: "请选择时间", validator: timeRule, }],
      },
      dataGun: [], // 多设备枪,


    };
  },
  created() {
    // this.getTeamAndCarList()
    this.getList();
  },
  methods: {

    getTeamAndCarList() {
      let that = this;
      teamAndCarList({}).then(res => {
        if (res.status == 0) {
          that.dataGun = this.formatCas(res.data, "id", "licenseNo"); // 设置枪下拉树的数据格式
          for (let i = 0; i < this.dataGun.length; i++) {
            for (let j = 0; j < this.dataGun[i].children.length; j++) {

              that.mdForm.imeiCar.push([that.dataGun[i].value, this.dataGun[i].children[j].value]);


            }


          }
          if (that.mdForm.imeiCar.length) {
            this.getList();

          }
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    formatDate1(time) {
      return formatDate(time, 1)
    },
    formatCas(data, imei, name) {
      // 格式化级联选择器数据 data是原始数据 imei是vaue值 name是label值
      let result = [],
        id = 0,
        _t = this;
      for (let i in data) {
        let childs = []; // 格式化子数组
        data[i].forEach(x => {
          childs.push({
            value: x[imei],
            label: x[name]
          });
          _t.imei2name[x[imei]] = x[name]; // 将设备号与设备名组成一个数组

        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "未分组" : i,
          children: childs
        });
      }
      return result;
    },
    //点击搜索
    handleFilter() {
      this.listQuery.page=1;
      this.getList();
    },

    getList() {
      let that = this;
      // that.$refs["mdForm"].validate(valid => {
      //   if (valid) {
           this.listLoading = true;
          // let carIds = [];
          // for (let i = 0; i < this.mdForm.imeiCar.length; i++) {
          //   carIds.push(this.mdForm.imeiCar[i][1])
          // }
          carOfflinesPage({
            page: this.listQuery.page,
            limit: this.listQuery.limit
            // tableDate: this.mdForm.searchDate.getTime(),
            // carIds: carIds
          })
            .then(res => {
              if (res.status == 0) {
                that.list = res.data;
                that.total = res.count;
                that.indexMethod =
                  (that.listQuery.page - 1) * that.listQuery.limit + 1;
              } else {
              }
            })
            .finally(() => {
              that.listLoading = false;
            });

      //   }else{
      //     return false;
      //   }
      // })


    },

    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }
      return "";
    },
    exportExcel() {

      let _t = this, carIds = [];
      for (let i = 0; i < this.temp.imeiCar.length; i++) {
        carIds.push(_t.imei2name[this.temp.imeiCar[i][1]])
      }
      // 查找数据
      _t.$refs["addForm"].validate(valid => {
        if (valid) {
          exportExcelReq({ excelType: 31, title: '报站记录表', imeis: carIds, times: [this.mdForm.searchDate.getTime().toString()] }).then(res => {
            try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
              let disposition = res.headers;
              let fileName = "报站记录表" + ".xls";
              let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              link.remove();
            }
          });
        }
      })
    }
  }
};
</script>
<style lang="scss">
#parkRecord {
  .filter-container {
    height: 70px;
  }
  .selectBox {
    display: flex;
    align-items: center;
    .pauseSelect {
      width: 165px !important;
      .el-input__inner,
      .el-form-item__content,
      .el-cascader,
      .el-input,
      .el-select,
      .el-form-item {
        width: 165px !important;
      }
    }
    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: 300px;
    }
  }

  .inOutNum {
    & > div {
      display: inline-block;
      margin-right: 10px;
      &:nth-of-type(1) {
        color: #666666;
      }
      &:nth-of-type(2) {
        color: #3fc349;
      }
      &:nth-of-type(3) {
        color: #ff4a4a;
        margin-right: 50px;
      }
    }

    width: 100%;
    height: 70px;
    line-height: 70px;
    margin-top: -70px;
    text-align: right;
    &.inOutNum1 {
      text-align: left;
    }
  }
}
.myGunCas1 {
  .el-cascader__tags {
    left: 6px;
  }
  // .el-input__inner {
  //   width: 306px;

  //   height: 30px !important;
  // }
  // .el-cascader__search-input {
  //   background-color: #fafafa;
  // }
}
</style>









