<template>
  <div id="deviceTest">
    <el-upload
      action
      :http-request="uploadFile"
      :limit="1"
      :on-exceed="fileExceed"
      accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
      :file-list="uploadList"
      :before-remove="beforeRemove"
      ref="fileupload"
    >
      <el-button size="small" type="primary" class="uploadButton">点击上传</el-button>
    </el-upload>

    <el-table
      v-loading="listLoading"
      :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)"
      border
      fit
      highlight-current-row
    >
      <el-table-column
        label="序号"
        prop="id"
        sortable="custom"
        align="center"
        width="80"
        type="index"
        :index="indexMethod"
      ></el-table-column>
      <el-table-column label="imei号" prop="imei" align="center" width="300"></el-table-column>
      <el-table-column label="状态" min-width="150px" align="center" prop="isOnlineText"></el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next, sizes, total, jumper"
      :page-sizes="[2, 10, 15, 20]"
      :page-size="pagesize"
      :total="list.length"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
      style="margin-bottom:10px;margin-top:40px;"
    ></el-pagination>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination";

import { fileCheck } from "../../api/deviceTest";

export default {
  name: "deviceTest",
  components: { Pagination },
  data() {
    return {
      uploadList: [],
      fileList: [],
      list: [],
      listLoading: false,
      pagesize: 10,
      currpage: 1,
      indexMethod: 1,
      deviceOnline: {}
    };
  },

  created() {
    XMLHttpRequest.prototype.sendAsBinary = function(datastr) {
      function byteValue(x) {
        return x.charCodeAt(0) & 0xff;
      }
      var ords = Array.prototype.map.call(datastr, byteValue);
      var ui8a = new Uint8Array(ords);
      this.send(ui8a.buffer);
    };

    this.deviceOnline = this.$store.state.realTimeMonitor.deviceOnline;
  },
  watch: {
    "$store.state.realTimeMonitor.deviceOnline": {
      handler: function(newer, older) {
        var that = this;
        if (newer) {
          for (let i = 0; i < this.list.length; i++) {
            let imei = this.list[i].imei;
            if (newer.hasOwnProperty(list[i].imei)) {
              this.list[i] = {
                imei: list[i].imei,
                isOnline: 1,
                isOnlineText: "在线"
              };
            }
          }
        }
      },
      deep: true
    }
  },

  methods: {
    // methods
    fileExceed() {
      this.$message.error("别贪心！一次只能上传一个哦~");
    },

    // 自定义上传
    uploadFile(item) {
      let that = this;
      const form = new FormData();
      var reader = new FileReader();
      reader.readAsBinaryString(item.file); //这个读法是异步的
      reader.onloadend = function() {
        // 这个事件在读取结束后，无论成功或者失败都会触发
        if (reader.error) {
        } else {
          that.uploadAndSubmit(reader.result);
        }
      };
    },
    uploadAndSubmit(BinaryContent) {
      let that = this;
      let Url = "http://beidoulab.work:9090/app/file/check";
      let xmlHttp = new XMLHttpRequest();
      xmlHttp.open("POST", Url); //IE处理汉字url
      xmlHttp.setRequestHeader(
        "Authorization",
        "Bearer " + this.$store.getters.token
      );
      xmlHttp.sendAsBinary(BinaryContent);
      xmlHttp.onreadystatechange = function() {
        if (xmlHttp.readyState == 4) {
          if (xmlHttp.status == 200) {
            var str = JSON.parse(xmlHttp.response);
            that.formatList(str.data);

            xmlHttp.abort();
          }
        }
      };
    },

    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`).then(() => {
        this.list = [];
      });
    },
    formatList(data) {
      this.list = [];
      for (let i = 0; i < data.length; i++) {
        let deviceOnline = 0;
        let deviceOnlineText = "离线";
        let imei = parseInt(data[i]).toString();

        if (this.deviceOnline.hasOwnProperty(imei)) {
          deviceOnline = 1;
          deviceOnlineText = "在线";
        }
        this.list.push({
          imei: parseInt(data[i]).toString(),
          isOnline: deviceOnline,
          isOnlineText: deviceOnlineText
        });
      }
    },
    handleCurrentChange(cpage) {
      this.currpage = cpage;
      this.indexMethod = (cpage-1)*(this.pagesize)+1;
    },
    handleSizeChange(psize) {
      this.pagesize = psize;
      this.indexMethod = (this.currpage-1)*(psize)+1;
    }
  }
};
</script>
<style scoped lang="scss">
#deviceTest {
  .uploadButton {
    margin: 30px 30px;
  }
}
</style>
<style  lang="scss" >
#deviceTest {
  .el-upload-list {
    display: inline-block;
    margin-bottom: -6px;
  }
}
</style>




