<template>
  <div id="inOutProcedure">
    <div class="contenBox">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <!-- <span>库包</span> -->
          <div class="selectBox">
            <el-form :inline="true" class="mdElForm" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
              <el-form-item prop="date">
                <el-date-picker v-model="mdForm.date" type="date" placeholder="选择日期" @change="dateChange" :clearable="false">
                </el-date-picker>
              </el-form-item>
              <el-form-item prop="routeId">
                <el-select v-model="mdForm.routeId" class="filter-item" placeholder="选择路线">
                  <el-option v-for="item in routeOptions" :key="item.routeId" :label="item.routeName" :value="item.routeId" />
                </el-select>
              </el-form-item>

              <el-form-item prop="type">
                <el-select v-model="mdForm.type" class="filter-item" placeholder="请选择">
                  <el-option v-for="item in typeOptions" :key="item.id" :label="item.name" :value="item.id" />
                </el-select>
              </el-form-item>

              <el-button type="primary" @click="handleFilter">搜索</el-button>

            </el-form>
          </div>
        </div>
        <!-- <div class="otherM">
            <div >凭证袋数量:{{nullNamePackage}}</div>
            <div>临时箱数量:{{nullNameBox}}</div>
            <div>周转箱数量:{{templateBoxNum}}</div>
            <div>库包数量:{{packageNum}}</div>
            <div>合计:{{nullNamePackage+nullNameBox+templateBoxNum+packageNum}}</div>
            <div>周转箱:{{templateBoxName}}</div>
          </div> -->
        <!-- <div class="package">
          <div v-for="item in rfidNames" v-if="newSocketData.rfids">{{item.name}}</div>
        </div> -->
        <div class="mainContent">
          <div>
            <div>路线名称</div>
            <div>{{routeName}}</div>
          </div>
          <div>
            <div>周转箱数量</div>
            <div>{{templateBoxNum}}</div>
          </div>
          <div>
            <div>凭证袋数量</div>
            <div>{{nullNamePackage}}</div>
          </div>
          <div>
            <div>临时箱数量</div>
            <div>{{nullNameBox}}</div>
          </div>
          <div>
            <div>库包数量</div>
            <div>{{packageNum}}</div>
          </div>
          <div>
            <div>合计</div>
            <div>{{nullNamePackage+nullNameBox+templateBoxNum+packageNum}}</div>
          </div>
          <!-- <div>周转箱:{{templateBoxName}}</div> -->
        </div>
      </el-card>
    </div>

  </div>
</template>
<script>
import axios from 'axios'
import { pacQueryByRfids } from "../../api/inOut/package";//出入库记录列表
import { queryR } from "../../api/inOut/rfid";
import { queryActually, queryPackageByActually } from "../../api/schedule/schedule";
import { progresssStatistics } from '../../api/progress'



export default {
  name: "inOutProcedure",
  data() {

    return {
      newSocketData: {},
      cancelRequest: null,
      rfidNames: {},
      nullNamePackage: 0,
      nullNameBox: 0,
      templateBoxNum: 0,
      templateBox: '',
      rfidOptions: [],
      rfidNameObj: {},
      templateBoxName: '',
      packageNum: 0,
      routeName: '-',
      mdRule: {
        date: [{ trigger: "change", message: "请选择时间", required: true }],
        routeId: [{ trigger: "change", message: "请选择路线", required: true }],

      },
      mdForm: {
        date: new Date(),
        type: 2,
        routeId:null
      },
      typeOptions: [
        { id: 2, name: '早送' },
        { id: 1, name: '晚收' }
      ],
      scheduleOptions: [],
      routeOptions: [],
    };
  },
  watch: {
    //实时推送数据的监控
    "$store.state.realTimeMonitor.packageInoutSocket": {
      handler: function (newer, older) {

        this.newSocketData = JSON.parse(JSON.stringify(this.$store.state.realTimeMonitor.packageInoutSocket)); //实时推送的数据
        // if (this.cancelRequest) {
        //   this.cancelRequest();
        //   this.cancelRequest = null;
        // }
        // console.warn(this.newSocketData)
        this.nullNamePackage = this.newSocketData.nullNamePackage ? this.newSocketData.nullNamePackage : 0
        this.nullNameBox = this.newSocketData.nullNameBox ? this.newSocketData.nullNameBox : 0

        this.templateBoxNum = this.newSocketData.templateBoxNum ? this.newSocketData.templateBoxNum : 0

        this.templateBox = this.newSocketData.templateBox ? this.newSocketData.templateBox : ''
        this.templateBoxName = this.rfidNameObj[this.templateBox] ? this.rfidNameObj[this.templateBox] : ''
        this.packageNum = 0;
        this.routeName = this.newSocketData.routeName?this.newSocketData.routeName:'-';
        let routeNameSpeak=this.newSocketData.routeName?this.newSocketData.routeName:'';


        this.speak(routeNameSpeak+'周转箱' + this.templateBoxNum + '个' + ',' + '凭证袋' + this.nullNamePackage + '个' + ',' + '临时箱' + this.nullNameBox + '个')
        

        pacQueryByRfids(this.newSocketData.rfids, new axios.CancelToken((c) => {
          this.cancelRequest = c; //保存当前请求
        })).then(res => {
          // this.rfidNames = res.data;
          // let speakText = '';

          // for (let i in this.rfidNames) {
          //   if (speakText == '') {
          //     speakText = this.rfidNames[i].name.replace('-','杠');

          //   } else {
          //     speakText += (',' + this.rfidNames[i].name).replace('-','杠')

          //   }

          // }

          // if (speakText) {
          //   this.speak(speakText)
          // }
          this.packageNum = Object.keys(res.data).length;
          let total = this.nullNamePackage + this.nullNameBox + this.templateBoxNum + this.packageNum;
          this.speak('库包' + this.packageNum + '个' + ',' + '合计' + total + '个');


        }).catch(error => {
          this.cancelRequest = null;
        });

      }


    },
    deep: true // 开启深度监听

  },
  created() {
    this.getSchedule();
    if(JSON.parse(this.$store.state.user.userInfo).UserName.indexOf('yunnan')==-1){
    this.getAlarm()
    }

  },

  computed: {
  },
  mounted() {
    this.getRfids();
  },
  methods: {
     getAlarm() {
      let p1 = new Promise((resolve, reject) => {
        progresssStatistics({
          date: new Date().getTime(),
          type: 2,
        })
          .then(res => {
            if (res.status == 0) {
              resolve(res.data)
            } else {
              reject();
              this.$message.error(res.msg);
            }
          })
      })

      let p2 = new Promise((resolve, reject) => {
        progresssStatistics({
          date: new Date().getTime(),
          type: 1,
        })
          .then(res => {
            if (res.status == 0) {
              resolve(res.data)
            } else {
              reject();
              this.$message.error(res.msg);
            }
          })

      })


      Promise.all([p1, p2]).then((values) => {
        let arr=[];
        if (new Date().getHours() >= 14 && new Date().getHours() <= 21) {
          arr = values[0]
        } else if(new Date().getHours()>21){
          arr = values[0].concat(values[1])
        }
        for (let i = 0; i < arr.length; i++) {
          let item = arr[i]
          if ((item.taskCount - item.cancelledTask) && (item.finishTask / (item.taskCount - item.cancelledTask) * 100 != 1)) {
            this.$alert('款箱交接未完成！', '提示', {
              confirmButtonText: '知道了',
              customClass:'boxMessageBox',
              callback: action => {
              }
            });
            break;
          }
        }

      })
    },
    dateChange(){
    this.mdForm.routeId=null;
    this.getSchedule();
    
    },
    handleFilter() {
      this.$refs["mdForm"].validate(valid => {
        if (valid) {
          let idIdentifys = [];
          this.nullNamePackage = 0;
          this.nullNameBox = 0;
          this.templateBoxNum = 0;
          this.packageNum = 0;
          //查找选择的路线 和排班类型所匹配的排班
          this.scheduleOptions.forEach(element => {
            if (element.routeId == this.mdForm.routeId && this.mdForm.type == element.type) {
              if (!idIdentifys.length) {
                this.routeName = element.routeName;
              }
              idIdentifys.push(element.idIdentify)
            }
          });
          queryPackageByActually({
            time: this.mdForm.date.getTime(),
            uuids:idIdentifys
          })
            .then(res => {
              if (res.status == 0) {
                if (res.data) {
                  for (let index in res.data) {
                    let item = res.data[index]
                    this.nullNamePackage += item.nullNamePackage;
                    this.nullNameBox += item.nullNameBox;
                    this.templateBoxNum += item.temp;
                    this.packageNum += item.package;
                  }

                }
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {

            });
        } else {
          return false;
        }
      })
    },
    getSchedule() {
      this.scheduleOptions=[];
      this.routeOptions=[];
      queryActually({
        times: [this.mdForm.date.getTime()]
      })
        .then(res => {
          if (res.status == 0) {
            if (res.data) {
              for (let index in res.data) {
                for (let i = res.data[index].length - 1; i >= 0; i--) {
                  if (res.data[index][i].status == 2 || res.data[index][i].status == 4) {
                    res.data[index].splice(i, 1)
                  } else {
                    let routeArr = this.routeOptions.filter((item1, index1) => {
                      return item1.routeId == res.data[index][i].routeId
                    })
                    if (!routeArr.length) {
                      this.routeOptions.push(res.data[index][i])
                    }
                  }
                }
                this.scheduleOptions = this.scheduleOptions.concat(res.data[index]);
              }
            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {

        });
    },
    getRfids() {

      queryR({ rfidType: [90], status: [0, 1] }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          this.rfidOptions = res.data;

          for (let i = 0; i < this.rfidOptions.length; i++) {
            //获取周转箱名称
            this.rfidNameObj[this.rfidOptions[i].rfid] = this.rfidOptions[i].linkName;


          }




        } else {
          if (res.data == null) {
            this.rfidOptions = [];
            this.rfidOptions1 = [];

          }
          this.$message.error(res.msg);
        }
      });
    },
    speak(text) {


      let msg = new SpeechSynthesisUtterance(text)

      msg.volume = 100  // 声音的音量

      msg.rate = 1 // 语速，数值，默认值是1，范围是0.1到10

      msg.pitch = 1.5 // 表示说话的音高，数值，范围从0（最小）到2（最大）。默认值为1
      speechSynthesis.speak(msg);
    },

  }

}
</script>
<style lang="scss" scoped>
$formItem-height: 69px;
$formItem-borderColor: #bfbfbf;
$theme-color: #324890;
$headerBarH: 120px;
$leftBarW: 240px;
#inOutProcedure {
  .selectBox {
    display: inline-flex;
  }
  .otherM {
    display: flex;
    color: #ccc;
    font-size: 40px;
    color: #000000;
    div {
      margin: 10px 20px;
    }
  }
  .package {
    display: flex;
    flex-wrap: wrap;
    & > div {
      min-width: 300px;
      height: 80px;
      line-height: 80px;
      background: rgb(244, 247, 255);
      padding: 0 10px;
      border-radius: 2px;
      text-align: center;
      margin: 10px;
      font-size: 40px;
      font-weight: 500;
      color: #000000;
    }
  }
  .mainContent {
    display: flex;
    flex-wrap: wrap;
    padding: 0px 80px 20px;
    height: 100%;
    align-items: center;
    & > div {
      // min-width: 300px;
      width: 100%;
      // height: 80px;
      // line-height: 80px;
      background: rgb(244, 247, 255);
      border-radius: 2px;
      text-align: center;
      // margin: 10px;
      font-size: 40px;
      font-weight: 500;
      color: #000000;
      & > div {
        display: inline-block;
        width: 50%;
        // height:100%;
        // line-height: 100%;
        height:80px;
        line-height: 80px;
        &:nth-child(1) {
          padding-right: 50px;
          text-align: center;
        }
        &:nth-child(2) {
          padding-left: 50px;
          text-align: center;
          background: rgb(238, 238, 252);
        }
      }
    }
  }

  margin: 20px 10px 10px;
  height: calc(100vh - #{$headerBarH} - 30px);

  .el-card {
    color: #205cd5;
    font-size: 16px;
  }
  .el-card__header {
    padding: 10px 20px;
  }
}

</style>
<style lang="scss" >
$formItem-height: 69px;
$formItem-borderColor: #bfbfbf;
$theme-color: #324890;
$headerBarH: 120px;
$leftBarW: 240px;
#inOutProcedure {
  .el-card__body {
    height: calc(100vh - #{$headerBarH} - 90px);
    overflow-y: auto;
    padding: 0 5px 20px;
    max-height: auto;
  }
}
.boxMessageBox{
.el-message-box__message{
  color:red;
  font-size: 20px;
  text-align: center;
}
}
</style>

