<template>
  <div class="app-container" id="groupTaskStatistic">
    <div class="filter-container">
      <el-select v-model="typeValue" placeholder="请选择" class="typeSelect" @change="typeSelectChange">
        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
      <!-- <div> -->
      <!-- 查找面板 -->
      <div class="selectBox">
        <el-form :inline="true" class="filterTool" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
          <el-form-item prop="searchDate">
            <el-date-picker class="filter-item" v-model="mdForm.searchDate" type="daterange" range-separator="至" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" :clearable="false"></el-date-picker>
            </el-date-picker>
          </el-form-item>
          <el-button class="filter-item" type="primary" @click="getTable" icon="el-icon-search">搜索</el-button>
          <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>
          <el-button class="filter-item" type="primary" icon="iconfont icontubiao" @click="showChartDialog">生成图表</el-button>

        </el-form>
      </div>
      <!-- </div> -->
    </div>
    <div class="tableBox" v-loading="listLoading">
      <el-table border :key="tableKey" v-if="rowList.length" :data="rowList" fit highlight-current-row style="width: 100%;" height="100%">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="车组名称" prop="groupName" min-width="80px" align="center"></el-table-column>
        <template>
          <el-table-column :label="item.name" v-for="item in colList" min-width="80px" align="center">
            <template slot-scope="{row}">
              <span>{{ formateNum( row.id,item.id)}}</span>
            </template>
          </el-table-column>
        </template>
      </el-table>
    </div>
    <el-dialog title="图表" :visible.sync="dialogTableVisible" width="90%">
      <div id="mychart">

      </div>
    </el-dialog>

  </div>
</template>

<script>
import echarts from "echarts";
import { queryRouteList, queryGroupRouteReport, queryGroupTaskReport } from "../../../api/dataCenter/dataStatistic/groupTaskStatistic";//出入库统计
import { carGroupAlllist } from "../../../api/managementArchives/unitManagement";
import { formatDate } from "../../../common";
import { exportExcelReq } from "../../../api/managementArchives/common";


export default {
  name: "groupTaskStatistic",
  data() {
    //时间验证
    let timeRule = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请选择时间"));
      } else {
        callback();
      }

    };
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      bankerId: JSON.parse(sessionStorage.getItem("userInfo")).PersonId,
      tableKey: 0,
      indexMethod: 1,
      listLoading: false,
      mdForm: {
        searchDate: [this.formatDate(new Date().setHours(0, 0, 0)), this.formatDate(new Date().setHours(0, 0, 0))],
      },
      mdRule: {
        searchDate: [{ trigger: "change", message: "请选择时间", validator: timeRule, }],
      },
      rowList: [],//车组
      colList: [],//路线
      dataObj: {},
      exportLoading: false,
      dialogTableVisible: false,
      echartsData: [],
      myChart: null,
      typeValue: 1,
      options: [
        {
          value: 1,
          label: '车组任务按路线统计'
        },
        {
          value: 2,
          label: '车组任务按任务类型统计'
        },
      ]

    };
  },
  created() {
    this.getTable();

  },
  beforeDestroy() {
    this.myChart && this.myChart.dispose();
    this.myChart = null
  },
  methods: {
    typeSelectChange() {
      this.colList = [];
      this.rowList = [];
      this.dataObj = {};
      this.echartsData = [];
    },
    formatDate,
    formateNum(rowId, colId) {
      let arr = this.dataObj[rowId], num, key;
      if (this.typeValue == 1) {
        key = 'routeId';
      } else if (this.typeValue == 2) {
        key = 'type'
      }
      if (arr) {
        for (let i = 0; i < arr.length; i++) {
          if (arr[i][key] == colId) {
            num = arr[i].num;
            break;
          }
        }
      }
      return num;

    },
    showChartDialog() {
      this.dialogTableVisible = true;
      this.$nextTick(() => {
        this.showChart();
      })
    },
    showChart() {
      let data1 = [], data2 = [];
      data2 = this.rowList.map((item, index) => {
        return item.groupName

      })
      data1 = this.colList.map((item, index) => {
        return item.name
      })

      // if (this.myChart) {
      //   let option1 = this.myChart.getOption();
      //   option1.series = this.echartsData;

      //   option1.xAxis.data = data2;
      //   option1.legend.data = data1;
      //   this.myChart.setOption(option1,true);
      //   return;
      // }
      this.myChart && this.myChart.dispose();
      // this.myChart=null
      var chartDom = document.getElementById('mychart');
      this.myChart = echarts.init(chartDom);
      var option;

      option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow',
            label: {
              show: true
            }
          }
        },
        legend: {
          data: data1,

          itemGap: 5
        },
        xAxis: {
          type: 'category',
          data: data2,
          splitLine: {
            show: true,
          }


        },
        yAxis: {
          show: false,
          type: 'value'
        },
        dataZoom: [
          {
            show: true,
            start: 0,
            end: 100
          },
          {
            type: 'inside',
            start: 0,
            end: 100
          },

        ],

        series: this.echartsData,
      };

      option && this.myChart.setOption(option);
    },
    getTable() {
      this.listLoading = true;
      this.colList = [];
      this.rowList = [];
      this.dataObj = [];
      this.echartsData = [];
      //查询表头
      let p1, req;
      if (this.typeValue == 1) {
        req = queryGroupRouteReport;
        p1 = new Promise((resolve, reject) => {
          queryRouteList().then(res => {
            if (res.status == 0) {
              this.colList = res.data;
              resolve(res.data);
            } else {
              reject();
            }
          }).catch(() => {
            reject();
          })
        })

      } else if (this.typeValue == 2) {
        req = queryGroupTaskReport;
        this.colList = [
          { id: 1, name: '晚收' },
          { id: 2, name: '早送' },
          { id: 5, name: '加钞' },
          { id: 6, name: '缴款' },
          { id: 7, name: '送款' },
          { id: 8, name: '上门收款' }
        ];
        p1 = Promise.resolve([
          { id: 1, name: '晚收' },
          { id: 2, name: '早送' },
          { id: 5, name: '加钞' },
          { id: 6, name: '缴款' },
          { id: 7, name: '送款' },
          { id: 8, name: '上门收款' }
        ]

        )
      }


      //   查询行头
      let p2 = new Promise((resolve, reject) => {
        carGroupAlllist().then(res => {
          if (res.status == 0) {
            this.rowList = res.data.filter((item) => {
              return item.status == 1
            });
            //  this.rowList = res.data
            resolve(res.data);
          } else {
            reject();
          }
        })
          .catch(() => {
            reject();
          });
      })

      //查询数据
      let p3 = new Promise((resolve, reject) => {
        req({ startTime: this.mdForm.searchDate[0]+' 00:00:00', endTime: this.mdForm.searchDate[1]+' 00:00:00' }).then(res => {
          if (res.status == 0) {
            this.dataObj = res.data;
            resolve(res.data);
          } else {
            reject();
          }
        })
          .catch(() => {
            this.dataObj = {};
            reject();
          });
      })
      Promise.all([p1, p2, p3]).then((res) => {
        this.listLoading = false;
        this.echartsData = [];
        if (this.typeValue == 1) {
          this.getDataByDealGroupRoute('id', 'routeId', 'id')
        } else if (this.typeValue == 2) {
          this.getDataByDealGroupRoute('id', 'type', 'id')
        }


      }).catch(() => {


        this.listLoading = false;
      })


    },
    getDataByDealGroupRoute(key1, key2, key3) {
      for (let i = 0; i < this.colList.length; i++) {//路线
        let arr1 = [];
        for (let j = 0; j < this.rowList.length; j++) {//车组
          let num = 0;;
          if (this.dataObj.hasOwnProperty(this.rowList[j][key1]) && this.dataObj[this.rowList[j][key1]]) {
            for (let k = 0; k < this.dataObj[this.rowList[j][key1]].length; k++) {
              if (this.dataObj[this.rowList[j][key1]][k][key2] == this.colList[i][key3]) {
                num = this.dataObj[this.rowList[j][key1]][k].num
                break;
              }
            }

          }
          arr1.push(num)

        }
        this.echartsData.push({
          name: this.colList[i].name,
          data: arr1,
          type: 'bar',

        })
      }

    },

    dealDate() {
      // 处理时间数组
      let _t = this;
      if (!_t.mdForm.searchDate || _t.mdForm.searchDate.length == 0) {
        return [];
      }
      let oneDay = 1000 * 60 * 60 * 24, // 一天的毫秒数
        s = new Date(_t.mdForm.searchDate[0]).getTime(), // 取小的值作为开始时间
        e = new Date(_t.mdForm.searchDate[1]).getTime() + oneDay - 1, // 大的值作为结束时间并且加上一天的毫秒
        len = Math.ceil((e - s) / oneDay), // 用于将日期遍历
        dayArr = []; // 传到后台的日期数组
      for (let i = 0; i < len; i++) {
        // let time = formatTime(s + oneDay * i);
        dayArr.push(s + oneDay * i);
      }
      return dayArr;
    },

    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      // 查找数据
      _t.$refs["mdForm"].validate(valid => {
        if (valid) {
          this.exportLoading = true;
          let excelType, title;
          if (this.typeValue == 1) {
            excelType = 35;
            title = '车组路线报表'
          } else if (this.typeValue == 2) {
            excelType = 37;
            title = '车组任务量报表'
          }

          exportExcelReq({ excelType: excelType, title: title, times: [new Date(_t.mdForm.searchDate[0]).getTime(),new Date(_t.mdForm.searchDate[1]).getTime()] }).then(res => {
            try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
              let disposition = res.headers;
              let fileName = title + ".xls";
              let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              link.remove();
            }



          }).finally(() => {
            this.exportLoading = false;
          });
        }
      })
    }

  }
};
</script>
<style lang="scss">
#groupTaskStatistic {
  .filter-container {
    height: 70px;
    justify-items: flex-start;
    .typeSelect {
      margin-left: 20px;
    }
    .el-input__inner {
      background-color: white;
    }
    .el-range-input {
      background: white;
    }
    // .el-range-separator,
    // .el-range__icon,
    // .el-input__icon {
    //   height: 30px !important;
    //   line-height: auto;
    // }
    // .el-range-separator {
    //   width: 8%;
    // }
    .dateRange {
      .el-form-item,
      .el-input__inner {
        width: 262px;
      }
      &.el-form-item {
        width: 262px;
      }
      .el-date-editor .el-range-input {
        width: 40%;
      }
    }
  }
  .selectBox {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    .el-range-separator,
    .el-range__icon,
    .el-input__icon {
      height: 30px !important;
      line-height: auto;
    }
    .el-range-separator {
      width: 8%;
    }
    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      //   width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: auto;
    }
  }
  #mychart {
    height: 600px;
  }
}
</style>









