<template>
  <div class="app-container" id="gunInOutRecord" :class="{adminClass:(power!==1&&power!==2)}">
    <div class="multidevice filter-container">
      <!-- 查找面板 -->
      <div class="selectBox">
        <el-form :inline="true" class="mdElForm" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
          <el-form-item prop="imeiGun">
            <el-cascader popper-class="myGunCas" ref="myMdGunsCas" v-model="mdForm.imeiGun" placeholder="选择枪支" :props="{expandTrigger:'hover',multiple: true}" :options="dataGun" :show-all-levels="false" clearable filterable collapse-tags>
            </el-cascader>
          </el-form-item>
          <!-- <el-form-item prop="searchDate">
            <el-date-picker v-model="mdForm.searchDate" type="month" placeholder="选择月" :clearable="false">
            </el-date-picker>
          </el-form-item> -->

          <el-form-item prop="searchDate">
            <el-date-picker type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :clearable="false" v-model="mdForm.searchDate" :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
          <el-form-item class="btnBox">
            <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
            <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel(1)" :loading="exportLoading1" icon="iconfont icondaochu">按时间段导出</el-button>

            <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel(2)" :loading="exportLoading2" icon="iconfont icondaochu">按枪号导出</el-button>

            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="枪号" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.gunCode }}</span>
          </template>
        </el-table-column>
        <el-table-column label="时间" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ formatDate1(scope.row.handoverTime) }}</span>
          </template>
        </el-table-column>
        <!-- 暂时不显示任务路线 -->
        <!-- <el-table-column label="任务路线" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.routeName }}</span>
          </template>
        </el-table-column> -->
        <!-- 暂时不发杀伤弹，暂不显示 -->
        <!-- <el-table-column label="杀伤弹(发)" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.killBulletNum }}</span>
          </template>
        </el-table-column> -->
        <el-table-column label="动能弹(发)" min-width="80px" align="center">
          <template slot-scope="{row}">
            <!-- <span>{{ row.kineticBulletNum }}</span> -->
            <span>5</span>
          </template>
        </el-table-column>
        <el-table-column label="押运员" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.safetyName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="管理员" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.adminName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="管理员" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.additionAdminName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="状态" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.status==1?'领用':'归还' }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { teamAndGunList } from "../../api/realTimeMonitoring";//枪支列表
import { gunInOutList } from "../../api/inOut";//出入库记录列表
import { formatDate } from "../../../src/common"; //格式化时间
import { exportExcelReq } from "../../api/managementArchives/common";
export default {
  name: "gunInOutRecord",
  components: { Pagination },
  data() {
    //设备验证
    let multiRule = (rule, value, callback) => {
      let guns = this.mdForm.imeiGun;
      if (guns.length < 1) {
        callback(new Error("请选择"));
      } else {
        callback();
      }

    };
    //枪支导出多设备验证
    let multiRule1 = (rule, value, callback) => {
      let guns = this.temp.imeiGun;
      if (guns.length < 1) {
        callback(new Error("请选择"));
      } else {
        callback();
      }

    };
    //时间验证
    let timeRule = (rule, value, callback) => {
      if (!value || value.length == 0) {
        callback(new Error("请选择时间"));
      } else {
        callback();
      }

    };

    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null,
      total: 0,
      indexMethod: 1,
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10
      },
      mdForm: {
        imeiGun: [],
        searchDate: [new Date(), new Date()]
      },
      mdRule: {
        imeiGun: [{ validator: multiRule, trigger: "change" }],
        searchDate: [{ trigger: "change", message: "请选择时间", validator: timeRule, }],
      },

      dataGun: [], // 多设备枪,
      imei2name: {}, // 设备的imei和对应的名字
      formVisible: false,
      temp: {
        imeiGun: [],
        searchDate: [new Date(), new Date()]

      },
      exportLoading1: false,
      exportLoading2: false,
      selectDate: null,
      pickerOptions: {
        //限制只能选择同一个月的
        disabledDate: time => {
          if (this.selectDate == null) {
            return false
          } else {
            return (this.selectDate.getMonth() != time.getMonth())
          }
        },
        onPick: date => {
          // 如果只选择一个则保存至selectDate 否则selectDate 为空
          if (date.minDate && !date.maxDate) {
            this.selectDate = date.minDate
          } else {
            this.selectDate = null
          }
        }
      },




    };
  },
  created() {
    this.getTeamAndGunList();
  },
  methods: {
    //计算两个时间戳之间间隔的天数
    intervalDay(startTime, endTime) {

      var date = endTime.setHours(0, 0, 0, 0) - startTime.setHours(0, 0, 0, 0); //时间差的毫秒数
      //计算出相差天数
      var days = Math.floor(date / (24 * 3600 * 1000));

      return days
    },
    getDateArr(days, startTime) {
      let arr = [];
      for (let i = 0; i < (days + 1); i++) {
        arr.push(startTime + i * 24 * 3600 * 1000)
      }
      return arr;
    },
    formatDate,
    formatDate1(time) {
      return formatDate(time, 1)
    },

    getTeamAndGunList() {
      let that = this;
      teamAndGunList({}).then(res => {
        if (res.status == 0) {
          that.dataGun = this.formatCas(res.data, "id", "code"); // 设置枪下拉树的数据格式
          for (let i = 0; i < this.dataGun.length; i++) {
            for (let j = 0; j < this.dataGun[i].children.length; j++) {

              that.mdForm.imeiGun.push([that.dataGun[i].value, this.dataGun[i].children[j].value]);


            }


          }
          if (that.mdForm.imeiGun.length) {
            this.handleFilter()

          }
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    formatCas(data, imei, name) {
      // 格式化级联选择器数据 data是原始数据 imei是vaue值 name是label值
      let result = [],
        id = 0,
        _t = this;
      for (let i in data) {
        let childs = []; // 格式化子数组
        data[i].forEach(x => {
          //过滤掉没有绑定设备的车


          childs.push({
            value: x[imei],
            label: x[name],
            name: x.gunCode
          });
          _t.imei2name[x[imei]] = x[name]; // 将设备号与设备名组成一个数组

        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "未分组" : i,
          children: childs
        });
      }
      return result;
    },
    getList() {

      let that = this;
      // that.$refs["mdForm"].validate(valid => {
      //   if (valid) {
      gunInOutList(this.listQuery)
        .then(res => {
          if (res.status == 0) {
            that.list = res.data;
            that.total = res.count;
            that.indexMethod =
              (that.listQuery.page - 1) * that.listQuery.limit + 1;
          } else {
          }
        })
        .finally(() => {
          that.listLoading = false;
        });
      //   }
      // });

    },
    //点击搜索
    handleFilter() {
      this.$refs["mdForm"].validate(valid => {
        if (valid) {
          this.listLoading = true;
          this.listQuery.page = 1;
          let gunIds = [];
          for (let i = 0; i < this.mdForm.imeiGun.length; i++) {
            gunIds.push(this.mdForm.imeiGun[i][1])
          }
          this.listQuery.startTime = this.formatDate(this.mdForm.searchDate[0]) + ' 00:00:00';
          this.listQuery.endTime = this.formatDate(this.mdForm.searchDate[1]) + ' 23:59:59';
          this.listQuery.gunIds = gunIds;
          this.getList();
        }
      })

    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }
      return "";
    },

    exportExcel(mode) {

      let _t = this, gunIds = [], times = [];
      for (let i = 0; i < this.mdForm.imeiGun.length; i++) {
        gunIds.push(this.mdForm.imeiGun[i][1])
      }
      // if (mode == 1) {
      //   // //获取当前月第一天
      //   // let date = this.mdForm.searchDate;
      //   // let date1 = date.setDate(1);
      //   // let date2 = new Date(date1).setHours(0, 0, 0, 0)
      //   // //判断一个月有几天
      //   // let days = new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate();
      //   // for (let i = 0; i < days; i++) {
      //   //   times.push(date2 + (i * 24 * 3600 * 1000))
      //   // }


      // } else if (mode == 2) {
      //   // times = [this.mdForm.searchDate.getTime().toString()]
      // }
      let days = this.intervalDay(this.mdForm.searchDate[0], this.mdForm.searchDate[1]);
      times = this.getDateArr(days, this.mdForm.searchDate[0].getTime())

      // 查找数据
      _t.$refs["mdForm"].validate(valid => {
        if (valid) {
          if (mode == 1) {
            this.exportLoading1 = true;
          } else if (mode == 2) {
            this.exportLoading2 = true;

          }
          exportExcelReq({ mode: mode, excelType: 21, title: '枪支领用表', gunIds: gunIds, times: times }
          ).then(res => {
            try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
              let disposition = res.headers;
              let fileName = "枪支领用表" + ".xls";
              let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              link.remove();
            }
          }).finally(() => {
            if (mode == 1) {
              this.exportLoading1 = false;
            } else if (mode == 2) {
              this.exportLoading2 = false;

            }
          });
        }
      })
    }
  }
};
</script>
<style lang="scss">
#gunInOutRecord {
  .filter-container {
    height: 70px;
    .el-button--primary {
      padding: 0 10px;
      width: auto;
      min-width: 100px;
    }
    .el-input__inner {
      background-color: white;
    }
    .el-range-separator,
    .el-range__icon,
    .el-input__icon {
      height: 30px !important;
      line-height: auto;
    }
    .el-range-separator {
      width: 8%;
    }
    .selectBox {
      .el-cascader__search-input {
        background-color: white;
      }
    }
  }
  .selectBox {
    display: flex;
    align-items: center;
    .pauseSelect {
      width: 165px !important;
      .el-input__inner,
      .el-form-item__content,
      .el-cascader,
      .el-input,
      .el-select,
      .el-form-item {
        width: 165px !important;
      }
    }
    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: 300px;
    }
    .btnBox {
      .el-form-item__content {
        width: 500px;
      }
    }
  }

  .inOutNum {
    & > div {
      display: inline-block;
      margin-right: 10px;
      &:nth-of-type(1) {
        color: #666666;
      }
      &:nth-of-type(2) {
        color: #3fc349;
      }
      &:nth-of-type(3) {
        color: #ff4a4a;
        margin-right: 50px;
      }
    }

    width: 100%;
    height: 70px;
    line-height: 70px;
    margin-top: -70px;
    text-align: right;
    &.inOutNum1 {
      text-align: left;
    }
  }
}
.myGunCas1 {
  .el-cascader__tags {
    left: 6px;
  }
  // .el-input__inner {
  //   width: 306px;

  //   height: 30px !important;
  // }
  // .el-cascader__search-input {
  //   background-color: #fafafa;
  // }
}
</style>









