<template>
  <div class="app-container" id="networkHandoverStatistics">
    <div class="filter-container">
      <div>
        <!-- 查找面板 -->
        <div class="selectBox">
          <el-form :inline="true" class="filterTool" :rules="mdRule" ref="mdForm" :model="mdForm" label-width="80px">
             

            <el-form-item prop="searchDate">
              <el-date-picker class="filter-item" v-model="mdForm.searchDate" type="daterange" range-separator="至" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" :clearable="false"></el-date-picker>
              </el-date-picker>
            </el-form-item>
            <el-button class="filter-item" type="primary" @click="getList" icon="el-icon-search">搜索</el-button>
          <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>


          </el-form>
        </div>
      </div>

    </div>

    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="网点" min-width="80px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.destinationName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="入网点" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.inBound }}</span>
          </template>
        </el-table-column>
        <el-table-column label="出网点" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.outBound }}</span>
          </template>
        </el-table-column>
        <el-table-column label="任务" min-width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.task }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import { queryPointHandoverSummary } from "../../api/dataCenter/networkHandoverStatistics";//出入库统计
import { formatDate } from "../../common";
import { exportExcelReq } from "../../api/managementArchives/common";
export default {
  name: "networkHandoverStatistics",
  components: { Pagination },
  data() {
    //时间验证
    let timeRule = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请选择时间"));
      } else {
        callback();
      }

    };
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      bankerId: JSON.parse(sessionStorage.getItem("userInfo")).PersonId,
      tableKey: 0,
      list: null,
      total: 0,
      indexMethod: 1,
      listLoading: true,
      listQuery: {
        page: 1,
        limit: 10,
        keyword: undefined
      },
      mdForm: {
        searchDate:[this.formatDate(new Date().setHours(0, 0, 0)), this.formatDate(new Date().setHours(0, 0, 0))]
      },
      mdRule: {
        searchDate: [{ trigger: "change", message: "请选择时间", validator: timeRule, }],
      },
      exportLoading: false,


    };
  },
  created() {
    this.getList(); //初始化获取列表

  },
  methods: {
    formatDate,
     
    dealDate() {
      // 处理时间数组
      let _t = this;
      if (!_t.mdForm.searchDate || _t.mdForm.searchDate.length == 0) {
        return [];
      }
      let oneDay = 1000 * 60 * 60 * 24, // 一天的毫秒数
        s = _t.mdForm.searchDate[0], // 取小的值作为开始时间
        e = _t.mdForm.searchDate[1] + oneDay - 1, // 大的值作为结束时间并且加上一天的毫秒
        len = Math.ceil((e - s) / oneDay), // 用于将日期遍历
        dayArr = []; // 传到后台的日期数组
      for (let i = 0; i < len; i++) {
        // let time = formatTime(s + oneDay * i);
        dayArr.push(s + oneDay * i);
      }
      return dayArr;
    },
    getList() {
      let that = this;
      this.listLoading = true;
      queryPointHandoverSummary(
       { startTime: this.mdForm.searchDate[0]+' 00:00:00', endTime: this.mdForm.searchDate[1]+' 00:00:00' }
      )
        .then(res => {
          if (res.status == 0) {
            that.listLoading = false;
            that.list = []
            for (let i in res.data) {
              res.data[i].destinationName = i;
              that.list.push(res.data[i]);
            }

          } else {
          }
        })
        .finally(() => {
          that.listLoading = false;
        });
    },


    //点击搜索
    handleFilter() {
      this.$router.push({ name: 'gunInoutChild', params: { keyword: this.listQuery.keyword } });

    },

    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
     exportExcel() {
      let _t = this;
      // 查找数据
      _t.$refs["mdForm"].validate(valid => {
        if (valid) {
          this.exportLoading = true;
          exportExcelReq({ excelType: 41, title: '网点交接统计' ,startDate: this.mdForm.searchDate[0]+' 00:00:00', endDate: this.mdForm.searchDate[1]+' 00:00:00' ,personId: this.bankerId,power:this.power}).then(res => {
            try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
              let fileName = "网点交接统计.xls";
              let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = fileName;
              link.click();
              link.remove();
            }



          }).finally(() => {
            this.exportLoading = false;
          });
        }
      })
    }

  }
};
</script>
<style lang="scss">
#networkHandoverStatistics {
  .filter-container {
    height: 70px;
    .el-input__inner {
      background-color: white;
    }
    .el-range-separator,
    .el-range__icon,
    .el-input__icon {
      height: 30px !important;
      line-height: auto;
    }
    .el-range-separator {
      width: 8%;
    }
 
  }
  .selectBox {
    display: flex;
    align-items: center;
    .pauseSelect {
      width: 165px !important;
      .el-input__inner,
      .el-form-item__content,
      .el-cascader,
      .el-input,
      .el-select,
      .el-form-item {
        width: 165px !important;
      }
    }
    .el-select {
      width: 300px;
    }
    .el-input__inner,
    .el-form-item__content,
    .el-cascader,
    .el-input {
      width: 300px;
      height: 30px !important;
      line-height: 30px;
    }
    .el-cascader__search-input {
      background-color: #f9f9f9;
    }
    .el-form-item {
      margin-bottom: 0;
      width: 300px;
    }
  }
  .search {
    margin-left: -200px;
  }
  .inOutNum {
    & > div {
      display: inline-block;
      margin-right: 10px;
      &:nth-of-type(1) {
        color: #666666;
      }
      &:nth-of-type(2) {
        color: #3fc349;
      }
      &:nth-of-type(3) {
        color: #ff4a4a;
        margin-right: 131px;
      }
    }

    width: 100%;
    height: 70px;
    line-height: 70px;
    margin-top: -70px;
    text-align: right;
    &.inOutNum1 {
      text-align: left;
    }
  }
}
</style>









